<template>
  <div class="jUnBudgetCost">
    <b-row>
      <b-col
        cols="6"
        class="p__r--1">
        <j-budget-dynamic
          :data="budgetData"
          :status="budgetColor"
          :height="20"/>
      </b-col>
      <b-col
        cols="6"
        class="p__l--2">
        <div class="right-text">
          <j-currency
            id="totalOverbudget"
            :value="totalBudget"/>
        </div>
      </b-col>
    </b-row>
    <b-tooltip
      v-if="totalBudget.toString().length > 6"
      target="totalOverbudget">
      <j-currency
        id="totalOverbudget"
        :value="totalBudget"/>
    </b-tooltip>
  </div>
</template>

<script>
export default {
  props: {
    locked: {
      type: Number,
      default: 0,
    },
    amount: {
      type: Number,
      default: 0,
    },
    remaining: {
      type: Number,
      default: 0,
    },
    used: {
      type: Number,
      default: 0,
    },
    accrue: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {
      data: [],
      budgetColor: ['red', 'light-grey', 'green'],
    };
  },
  computed: {
    budgetData() {
      let use;
      let lock;
      if (this.used + this.accrue > this.amount) {
        use = (this.used + this.accrue) - this.amount;
        lock = use + this.locked;
      } else {
        use = 1;
        lock = (this.locked + this.used + this.accrue) - this.amount;
      }
      this.$set(this.data, '0', use);
      this.$set(this.data, '1', lock);
      this.$set(this.data, '2', lock);
      return this.data;
    },
    totalBudget() {
      const remaining = Math.abs(this.remaining);
      return remaining;
    },
  },
};
</script>



