import jApi from './jApi';

export default {
  getTagList(req) {
    const api = jApi.generateApi();
    return api.post('costcenter/config/tag/list', req)
      .then(res => res.data);
  },
  createTagName(tagName) {
    const api = jApi.generateApi();
    return api.post('costcenter/config/tag/create', tagName)
      .then(res => res.data);
  },
};



