<template>
  <div class="custom-search-table">
    <v-server-table
      ref="transferTable"
      :columns="columns"
      :options="options"
      class="jTable"
      @loading="loading"
      @loaded="loaded">
      <div
        slot="beforeFilter"
        class="custom-search">
        <b-row>
          <b-col cols="7">
            <j-input
              v-model="filter_transfer"
              @keyup.13.native="customFilter(filter_transfer)"/>
          </b-col>
          <b-col
            class="p__l--10"
            cols="5">
            <b-button
              class="btn blue outline"
              @click="customFilter(filter_transfer)">
            <i class="fa fa-search bold" /> Search </b-button>
          </b-col>
        </b-row>
      </div>
      <div
        slot="Date"
        slot-scope="props">
        {{ props.row.date_time | moment("DD MMM, YYYY") }}
      </div>
      <div
        slot="From"
        slot-scope="props">
        {{ props.row.source_costcenter.name }}
      </div>
      <div
        slot="Amount"
        slot-scope="props">
        <j-currency
          :value="props.row.amount"/>
      </div>

      <div
        slot="status"
        slot-scope="props">
        {{ props.row.status_name }}
      </div>

      <div
        slot="Notes"
        slot-scope="props">
        {{ props.row.notes }}
      </div>


    </v-server-table>
  </div>
</template>
<script>
import { jMixPaginationTransfer } from '@/mixins/paginations/transfer-budget/jMixPaginationTransfer';
import { jMixReusableFunction } from '@/mixins/paginations/transfer-budget/jMixReusableFunction';

export default {
  mixins: [
    jMixPaginationTransfer,
    jMixReusableFunction,
  ],
};
</script>




