<template>
  <div>
    <b-form-group>
      <j-input
        id="exampleInput1"
        :class="{'error-input' : $v.form.email.$error}"
        :error-message="null"
        v-model="$v.form.email.$model"
        type="email"
        placeholder="Email Address"
        @keyup.13.native="onSubmit"/>
    </b-form-group>
    <b-form-group id="exampleInputGroup2">
      <j-input
        id="exampleInput2"
        :type="password_id"
        :class="{'error-input' : $v.form.password.$error}"
        :error-message="null"
        v-model="$v.form.password.$model"
        placeholder="Password"
        @keyup.13.native="onSubmit"/>
    </b-form-group>
    <j-checkbox
      v-model="show_pass"
      :checked="checkbox"
      :checked-value="true"
      :unchecked-value="false">
      show password
    </j-checkbox>
    <b-row>
      <b-col
        class="btn-left">
        <b-button
          :class="{'disabled': $v.form.$invalid}"
          class="btn blue"
          @click="onSubmit">
          Login
        </b-button>
      </b-col>
      <b-col
        class="btn-right">
        <b-button
          class="btn red"
          @click="onReset">
          Register
        </b-button>
      </b-col>
    </b-row>
    <b-link
      href="https://jojonomic.com/"
      target="_blank"
      class="forgot_password">
      Forgot Password?
    </b-link>
  </div>
</template>

<script>
import { required, minLength } from 'vuelidate/lib/validators';
import { mapActions } from 'vuex';

export default {
  name: 'JFormLogin',
  data() {
    return {
      form: {
        email: '',
        password: '',
      },
      show_pass: false,
    };
  },
  validations: {
    form: {
      email: {
        required,
      },
      password: {
        required,
        minLength: minLength(5),
      },
    },
  },
  computed: {
    password_id() {
      if (this.show_pass) {
        return 'text';
      }
      return 'password';
    },
  },
  watch: {
    // eslint-disable-next-line
    // 'form.email': function(newVal, oldVal) {
    //   console.log(newVal, 'way');
    // eslint-disable-next-line
    //   if (!newVal.match(/^[A-Za-z0-9]+$/g) || !newVal.match(/^\w+@[a-zA-Z_]+?\.[a-zA-Z]{2,3}$/)) {
    //     setTimeout(() => {
    //       this.form.email = oldVal;
    //     }, 0);
    //   }
    // },
  },
  methods: {
    ...mapActions('jStoreLogin', ['loginAccount']),
    onSubmit() {
      this.$v.form.$touch();
      if (!this.form.email.match(/^[A-Za-z0-9@.]+$/g)) {
        this.$store.dispatch('jStoreNotificationScreen/toggleProblem', 'Please input email correctly');
      } else if (!this.$v.form.$error) {
        this.loginAccount(this.form);
      }
    },
    onReset() {
      this.$v.$reset();
    },
  },
};
</script>



