import jMappingLayer from '@/views/dashboard/mapping-layer/jMappingLayer';
import auth from '@/middleware/auth';
import adminBudget from '@/middleware/admin-budget';

export default [
  {
    path: '/dashboard/mapping-layer',
    name: 'jMappingLayer',
    component: jMappingLayer,
    meta: {
      title: 'Mapping Layer',
      middleware: [auth, adminBudget],
      layout: 'jWrapper',
    },
  },
];



