/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapState, mapActions } from 'vuex';

export const jMixModeEditMappingLayer = {
  computed: {
    ...mapState('jStoreMappingLayer', ['mapping_layer_detail']),
  },
  data() {
    return {
      mapping_detail: null,
    };
  },
  mounted() {
    this.mapping_detail = this.mapping_layer_detail;
  },
  watch: {
    mapping_detail(val) {
      if (val !== null && this.mode === 'edit') {
        const data = this.generateDataEdit(val);
        this.mapping_layer_data = [...data];
      }
    },
    mapping_layer_detail(val) {
      if (val !== null && this.mode === 'edit') {
        const data = this.generateDataEdit(val);
        this.mapping_layer_data = [...data];
      }
    },
  },
  methods: {
    generateDataEdit(val) {
      const data = val;
      const dataMapping = [];
      data.forEach((e, index) => {
        const a = {
          is_exist: e.is_exist,
          is_delete: false,
          expense_type: {
            id: e.id,
            name: e.name,
          },
          case: [],
        };
        this.default_rule.push([]);
        e.cases.forEach((f, indexCase) => {
          const dataCase = {
            id: f.id,
            relation_id: f.relation_id,
            item_id: f.item_id,
            is_delete: f.is_delete,
            is_over_budget: f.is_over_budget,
            rule: f.rule,
            value: f.value,
            operator: {
              id: f.operation,
            },
            status_transfer: {
              id: f.value_type,
            },
            flow: f.flows,
          };
          a.case.push(dataCase);
          this.$set(this.default_rule[index], indexCase, [...f.flows]);
        });
        dataMapping.push(a);
      });

      return dataMapping;
    },
  },
};



