<template>
  <div>
    <br>
    <div
      v-if="user_role === 'admin_budget' || user_role === 'SysAdmin'"
      class="cc-btn">
      <br>
      <b-button
        class="btn green outline"
        @click="$router.replace('/dashboard/cost-center/create/0')">
        Create Cost Center
      </b-button>
    </div>
    <br>
    <jListCostCenter
      v-if="user_role === 'admin_budget' || user_role === 'SysAdmin'"/>
    <jListCostCenterCustody v-else-if="user_role === 'budget_custody'"/>
  </div>
</template>

<script>
import jPrivCostCenter from '@/privilage/views/cost-center/jPrivCostCenter';
import jListCostCenter from './jListCostCenter';
import jListCostCenterCustody from './jListCostCenterCustody';

export default {
  name: 'JListCostCenterContainer',
  components: {
    jListCostCenter,
    jListCostCenterCustody,
  },
  mixins: [
    jPrivCostCenter,
  ],
};
</script>



