import jApi from './jApi';

export default {
  exportReport(req) {
    const api = jApi.generateApi();
    return api.post('costcenter/report/export', req)
      .then(res => res);
  },

  getListRootCategory(req) {
    const api = jApi.generateApi();
    return api.post('costcenter/report/root-category/list', req)
      .then(res => res);
  },

  exportBudget(req) {
    const api = jApi.generateApi();
    return api.post('costcenter/report/budget/export', req)
      .then(res => res);
  },
};



