<template>
  <div>
    <v-server-table
      ref="transferTable"
      :columns="columns"
      :options="options"
      class="jTable"
      @loading="loading"
      @loaded="loaded">

      <div
        slot="Date"
        slot-scope="props">
        {{ props.row.date_time | moment("DD MMM, YYYY") }}
      </div>
      <div
        slot="To"
        slot-scope="props">
        {{ props.row.destination_costcenter.name }}
      </div>
      <div
        slot="Amount"
        slot-scope="props">
        <j-currency
          :value="props.row.amount"/>
      </div>

      <div
        slot="status"
        slot-scope="props">
        {{ props.row.status_name }}
      </div>

      <div
        slot="Notes"
        slot-scope="props">
        {{ props.row.notes }}
      </div>


    </v-server-table>
  </div>
</template>
<script>
import { jMixPaginationTransferOut } from '@/mixins/paginations/transfer-budget/jMixPaginationTransferOut';
import { jMixReusableFunction } from '@/mixins/paginations/transfer-budget/jMixReusableFunction';

export default {
  mixins: [
    jMixPaginationTransferOut,
    jMixReusableFunction,
  ],
};
</script>




