<template>
  <div class="create-costcenter">
    <!-- <b-button @click="show">CREATE</b-button> -->
    <b-modal
      ref="createCost"
      class="mod-form"
      centered
      no-close-on-backdrop
      no-close-on-esc>
      <div
        class="jback-to-route"
        @click="replacePage">
        <i class="fa fa-times"/>
      </div>
      <h3 class="float-to-center">
        Create Cost Center
      </h3>

      <div>
        <jCostCenterForm
          v-if="!reset_form"
          ref="costCenterForm"
          @getValue="getValue"/>
      </div>

      <br>
      <b-row>

        <b-col>
          <label class="italic c--light-grey">choose budget preferal</label>
          <div>
            <b-row>
              <b-col
                :class="{'half-opacity': !is_lock}"
                cols="3"
                class="pointer--cursor"
                @click="is_lock = true">
                <i class="fa fa-unlock"/>Lock
              </b-col>
              <b-col
                :class="{'half-opacity': is_lock}"
                class="pointer--cursor"
                @click="is_lock = false">
                <i class="fa fa-unlock-alt"/>Unlock
              </b-col>
            </b-row>
          </div>
        </b-col>

        <b-col>
          <div class="float-to-right btn-create-costcenter">
            <b-button
              class="btn blue"
              @click="save">
              Create Cost Center
            </b-button>
          </div>
        </b-col>

      </b-row>
    </b-modal>
  </div>
</template>

<script>
import jCostCenterForm from '@/components/cost-center-form/jCostCenterForm';
import { mapActions, mapState } from 'vuex';

export default {
  components: {
    jCostCenterForm,
  },
  data() {
    return {
      is_lock: false,
      reset_form: false,
      send_data: null,
    };
  },
  computed: {
    ...mapState('jStoreCostCenter', ['costcenter_created']),
  },
  watch: {
    costcenter_created() {
      this.$router.replace('/dashboard/cost-center');
      this.reset_form = true;
      this.$refs.createCost.hide();
      this.$router.go();
      setTimeout(() => {
        this.reset_form = false;
      }, 100);
    },
  },
  mounted() {
    this.show();
  },
  methods: {
    ...mapActions('jStoreCostCenter', ['createCostCenter']),
    getValue(data) {
      if (data === null) {
        this.send_data = data;
      } else {
        const sendData = {
          costcenter: data,
        };
        sendData.costcenter.policy.is_lock = this.is_lock;
        // sendData.costcenter.root_id = 1;
        this.send_data = sendData;
      }
    },
    show() {
      this.$refs.createCost.show();
    },
    hide() {
      this.$refs.createCost.hide();
    },
    save() {
      this.$refs.costCenterForm.validateForm();
      if (this.send_data !== null) {
        this.createCostCenter(this.send_data);
      }
    },

    replacePage() {
      if (this.$route.path === `/dashboard/cost-center/search/create/${this.$route.params.root}`) {
        this.$router.replace('/dashboard/cost-center/search');
      } else if (this.$route.path === `/dashboard/cost-center/create/${this.$route.params.root}`) {
        this.$router.replace('/dashboard/cost-center');
      }
    },
  },
};
</script>

<style lang="scss">

</style>



