<template>
  <div>
    <jMappingLayerForm
      ref="form"
      mode="edit"
      @data="getData" />

    <b-button
      class="btn blue float-right"
      @click="saveMapping">
      Submit
    </b-button>
  </div>
</template>

<script>
import jMappingLayerForm from '@/components/form/mapping-layer/jMappingLayerForm';
import { jMixModelEditMappingLayer } from '@/mixins/models/mapping-layer/jMixModelEditMappingLayer';

export default {
  components: {
    jMappingLayerForm,
  },
  mixins: [
    jMixModelEditMappingLayer,
  ],
  data() {
    return {
      mapping_layer_data: null,
    };
  },
  methods: {

    saveMapping() {
      this.$refs.form.save();
      if (this.mapping_layer_data !== null) {
        this.sendData();
      }
    },

    getData(data) {
      this.mapping_layer_data = data;
    },

  },
};
</script>



