<template>
  <div class="jcost-center-list">
    <b-modal
      ref="editCost"
      class="mod-form"
      centered
      no-close-on-backdrop
      no-close-on-esc>
      <div class="jclose-modal">
        <i
          class="fa fa-times"
          @click="replacePage()"/>
      </div>
      <h3 class="float-to-center">
        Edit Cost Center
      </h3>
      <j-form-cost-center
        ref="costCenterForm"
        @getValue="getValue"/>
      <br><br>
      <div>
        <j-transfer-budget/>
      </div>
      <br><br>

      <b-row>
        <b-col>
          <div
            class="float-to-left btn-create-costcenter">
            <b-button
              class="btn red"
              @click="deleteCostCenterID">
              Delete
            </b-button>
          </div>
        </b-col>
        <b-col>
          <div class="float-to-right btn-create-costcenter">
            <b-button
              v-if="user_role === 'admin_budget' || user_role === 'SysAdmin'"
              class="btn green m__r--20"
              @click="$router.replace(`/dashboard/cost-center/search/create/${$route.params.id}`)">
              Add Child Cost
            </b-button>
            <b-button
              class="btn blue"
              @click="save">
              Edit Cost Center
            </b-button>
          </div>
        </b-col>
      </b-row>


    </b-modal>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex';
import jFormCostCenter from '@/components/cost-center-form/jCostCenterForm';
import jTransferBudget from '@/components/cost-center-form/jTransferBudget';
import jPrivCostCenter from '@/privilage/views/cost-center/jPrivCostCenter';

export default {
  name: 'JEditCostCenter',
  components: {
    jFormCostCenter,
    jTransferBudget,
  },
  mixins: [
    jPrivCostCenter,
  ],
  data() {
    return {
      send_data_edit: null,
    };
  },
  computed: {
    ...mapState('jStoreCostCenter', ['costcenter_list', 'response_lock_action', 'delete_response', 'list_budget_transfer', 'detail_cost']),
  },
  watch: {
    delete_response() {
      if (this.$route.path === `/dashboard/cost-center/search/edit/${this.detail_cost.id}`) {
        this.$router.replace('/dashboard/cost-center/search');
      } else if (this.$route.path === `/dashboard/cost-center/edit/${this.detail_cost.id}`) {
        this.$router.replace('/dashboard/cost-center');
      }
    },
  },
  mounted() {
    this.$refs.editCost.show();
  },
  methods: {
    ...mapActions('jStoreCostCenter', ['getCostCenterList', 'lockActionCostCenter', 'deleteCostCenter', 'getBudgetTransferList', 'getDetailCostCenter', 'updateCostCenter']),

    getValue(data) {
      if (data === null) {
        this.send_data_edit = data;
      } else {
        const sendData = {
          costcenter: data,
        };
        this.send_data_edit = sendData;
      }
    },

    save() {
      this.$refs.costCenterForm.validateForm();
      if (this.send_data_edit !== null) {
        const text = `to update cost center "${this.detail_cost.name}"`;

        this.$swal({
          title: 'Are you sure?',
          text,
          type: 'question',
          showCancelButton: true,
          cancelButtonColor: '#d33',
          confirmButtonText: 'Yes',
        }).then((result) => {
          if (result.value) {
            this.updateCostCenter(this.send_data_edit);
          }
        });
      }
    },

    deleteCostCenterID() {
      const text = `to delete cost center "${this.detail_cost.name}"`;

      this.$swal({
        title: 'Are you sure?',
        text,
        type: 'question',
        showCancelButton: true,
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes',
      }).then(async (result) => {
        if (result.value) {
          const costCenter = {
            costcenters: [
              {
                id: this.detail_cost.id,
              },
            ],
          };
          await this.deleteCostCenter(costCenter);
          this.$router.replace('/dashboard/cost-center');
          this.$router.go();
          this.replacePage();
        }
      });
    },
    replacePage() {
      if (this.$route.path === `/dashboard/cost-center/search/edit/${this.detail_cost.id}`) {
        this.$router.replace('/dashboard/cost-center/search');
      } else if (this.$route.path === `/dashboard/cost-center/edit/${this.detail_cost.id}`) {
        this.$router.replace('/dashboard/cost-center');
      }
    },
  },
};
</script>



