<template>
  <div class="jcost-center-list">
    <div
      v-for="(project, index) in project_data_only"
      :key="index">
      <div class="jnested">
        <div
          class="item">
          <b-row class="bar">
            <b-col class="button-open" />
            <b-col>
              <div
                class="content">

                <b-row class="no-margin">
                  <b-col
                    md="1"
                    class="icon no-padding">
                    <i class="fa fa-money"/>
                  </b-col>
                  <b-col
                    md="7"
                    class="description no-padding">
                    <p
                      :class="projectData.name ? '' : 'm__b--10'"
                      class="name bold">
                      {{ projectData.name }}
                    </p>
                    <p
                      :class="project.project.name ? '' : 'm__b--10'"
                      class="name bold">{{ project.project.name }}
                    </p>
                    <j-budget-cost
                      :amount="project.amount"
                      :used="project.used"
                      :locked="project.locked"
                      :accrue="project.accrue">
                      <div
                        slot="left-bottom"
                        class="budget-comp">
                        <span>
                          <j-currency
                            :value="project.amount">
                            <template slot="text-left">Budget</template>
                          </j-currency>
                        </span>
                        <span v-if="project.remaining < 0">
                          <j-currency
                            :value="Math.abs(project.remaining) + project.amount">
                            <template slot="text-left">Total Spending</template>
                          </j-currency>
                        </span>
                      </div>
                      <div
                        v-if="project.remaining > 0"
                        slot="right-center"
                        class="float-right">
                        <span>
                          <j-currency
                            :value="project.remaining">
                            <template slot="text-left">
                              Remaining
                            </template>
                          </j-currency>
                        </span>
                      </div>
                      <div
                        v-else
                        slot="right-center"
                        class="float-right">
                        <span>
                          <j-currency
                            :value="0">
                            <template slot="text-left">Remaining</template>
                          </j-currency>
                        </span>
                      </div>
                    </j-budget-cost>
                  </b-col>
                  <b-col
                    class="p__l--5"
                    md="4">
                    <b-row>
                      <b-col
                        class="add-div no-padding"
                        md="7">
                        <!-- <label
                          class="add-child bold"
                          @click="$router.replace(`/dashboard/cost-center/create/${props.row.id}`)">
                          + Add child cost
                        </label> -->
                      </b-col>
                      <b-col
                        class="button-choice"
                        md="5"/>
                    </b-row>
                    <b-row
                      v-if="project.remaining < 0"
                      :class="project.project.name ? 'm__t--16' : 'm__t--8'">
                      <b-col class="p__r--1">
                        <j-unbudgeted-cost
                          :remaining="project.remaining"
                          :used="project.used"
                          :accrue="project.accrue"
                          :amount="project.amount"
                          :locked="project.locked"/>
                      </b-col>
                    </b-row>
                  </b-col>
                </b-row>
              </div>
            </b-col>
          </b-row>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import jBudgetCost from '@/components/budget-component/jBudgetComponent';
import jUnbudgetedCost from '@/components/budget-component/jUnbudgetedComponent';
import jLoadingNestedTable from '@/components/skeleton-loading/jLoadingNestedTable';
// import jTransferBudget from '@/components/cost-center-form/jTransferBudget';

export default {
  name: 'JListCostCenterProjectDetail',
  components: {
    jBudgetCost,
    jLoadingNestedTable,
    jUnbudgetedCost,
    // jTransferBudget,
  },
  props: {
    projectData: {
      type: Object,
      default: () => {},
    },
  },
  computed: {
    project_data_only() {
      const sortData = this.projectData.budget.filter(data => data.is_project_based === true);
      return sortData;
    },
  },
};
</script>



