/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapActions } from 'vuex';

export const jMixModelEditCategoryLayer = {
  methods: {

    generateDataEdit() {
      const getIdLastChild = this.layer_categories[this.layer_categories.length - 1];
      let getData;
      let convertToObj;
      if (typeof getIdLastChild !== 'object') {
        // eslint-disable-next-line
        convertToObj = this.layer_options[this.layer_categories.length - 1].filter(data => data.id === getIdLastChild);
        convertToObj[0].relation_id = 0;
        convertToObj[0].is_delete = false;
        getData = [...convertToObj];
      } else {
        getData = [...getIdLastChild];
      }

      const chooseLastChild = typeof getIdLastChild !== 'object' ? convertToObj : getIdLastChild;
      this.last_old_layer.forEach((k) => {
        let dataExist = false;
        for (let i = 0; i < chooseLastChild.length; i += 1) {
          chooseLastChild[i].relation_id = 0;
          chooseLastChild[i].is_delete = false;
          chooseLastChild[i].package = k.package;
          chooseLastChild[i].root_id = k.root_id;
          if (k.id === chooseLastChild[i].id) {
            chooseLastChild[i].relation_id = k.relation_id;
            dataExist = true;
          }
        }
        if (!dataExist) {
          const a = {
            id: k.id,
            is_delete: true,
            relation_id: k.relation_id,
            name: k.name,
            package: k.package,
            root_id: k.root_id,
            is_have_child: k.is_have_child,
          };
          getData.push(a);
        }
      });
      this.$emit('lastChild', getData);
    },
  },
};



