<template>
  <div>
    <j-floating-prompt ref="floating">

      <b-row>
        <b-col md="3">
          <b-row>
            <h1 class="f-blue">{{ selectedRows.length }}</h1>
            <h4> rows <br> selected </h4>
          </b-row>
        </b-col>
        <b-col md="7">
          <h4>Period Name : </h4>
          <p> {{ periods_name }} </p>
        </b-col>
        <b-col md="2">
          <b-button
            class="btn red"
            @click="deletePeriods">DELETE</b-button>
        </b-col>
      </b-row>

    </j-floating-prompt>

    <jProgressScreen
      ref="deletePeriod"
      :data-kirim="delete_data"
      :url-api="url"
      :headers="headers"
      @finish="finish"/>

  </div>
</template>

<script>
import { jMixModelDeletePeriods } from '@/mixins/models/config/jMixModelDeletePeriod';
// import Vue from 'vue';
// import jApi from '@/services/jApi';
import jProgressScreen from '@/components/progress-screen/jProgressScreen';

export default {
  components: {
    jProgressScreen,
  },
  mixins: [
    jMixModelDeletePeriods,
  ],
  props: {
    selectedRows: {
      type: Array,
      default: () => [],
    },
  },
  computed: {

    headers() {
      // const token = Vue.ls.get('Token');
      // const headers = {
      //   Authorization: `Bearer ${token}`,
      // };
      return null;
    },

    // apiAxios() {
    //   console.log('hayyhhh', jApi.generateApi());
    //   return jApi.generateApi();
    // },

    url() {
      const getEnv = 'costcenter/config/period/delete';
      return getEnv;
    },

    periods_name() {
      const name = [];
      this.selectedRows.forEach((a) => {
        name.push(a.name);
      });
      return name.join(', ');
    },
  },

  methods: {
    // apiAxios(api) {
    //   console.log(api, 'apalahahha');
    //   api = jApi.generateApi();
    //   return api;
    //   // return jApi.generateApi();
    // },

    show() {
      this.$refs.floating.show();
    },

    hide() {
      this.$refs.floating.hide();
    },

    finish() {
      this.hide();
      this.$emit('finish');
    },

  },
};
</script>



