/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapState, mapActions } from 'vuex';

export const jMixModeEditPeriod = {
  methods: {
    ...mapActions('jStoreConfig', ['getPeriodDetail']),
    modeEdit() {
      const periodId = {
        period: {
          id: Number(this.$route.params.id),
        },
      };

      // get detail
      this.getPeriodDetail(periodId);
    },
  },

  computed: {
    ...mapState('jStoreConfig', ['period_data']),

    is_used_transaction_edit() {
      if (this.period_data.detail_period !== null) {
        return this.period_data.detail_period.is_used_transaction;
      }
      return false;
    },

  },

  watch: {
    'period_data.detail_period'(newVal) {
      const periodForm = {
        id: newVal.id,
        name: newVal.name,
        description: newVal.description,
        project_based: newVal.type === 2,
        start_date: newVal.start_date,
        end_date: newVal.end_date,
        is_used_transaction: newVal.is_used_transaction,
        backdate_settlement: newVal.backdate_settlement,
        blackout_date: {
          start_date: newVal.blackout_date.start_date,
          end_date: newVal.blackout_date.end_date,
        },
      };
      this.period_form = periodForm;
    },
  },
};



