<template>
  <div>
    <jSearchCostCenter v-if="user_role === 'SysAdmin' || user_role === 'admin_budget'"/>
    <jSearchCostCenterCustody v-else-if="user_role === 'budget_custody'"/>
  </div>
</template>

<script>
import jPrivCostCenter from '@/privilage/views/cost-center/jPrivCostCenter';
import jSearchCostCenter from './jSearchCostCenter';
import jSearchCostCenterCustody from './jSearchCostCenterCustody';

export default {
  components: {
    jSearchCostCenter,
    jSearchCostCenterCustody,
  },
  mixins: [
    jPrivCostCenter,
  ],
};
</script>



