/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */

export const jMixModeCreateCategoryLayer = {
  methods: {
    modeCreateListMC(newVal) {
      if (this.layer_options.length < 1) {
        this.layer_categories.push(null);
        this.layer_options.push(newVal);
      } else {
        this.layer_options[0] = newVal;
      }

      this.modeCreateAddHoverInfo();
    },

    modeCreateListActivity(newVal) {
      this.layer_options.push(newVal);
      const isHaveChildren = newVal.filter(child => child.is_have_child === true);
      if (isHaveChildren.length > 0) {
        this.layer_categories.push(null);
      } else {
        this.layer_categories.push([]);
      }

      this.modeCreateAddHoverInfo();
    },

    modeCreateAddHoverInfo() {
      const tooltips = {
        is_show: false,
        label: null,
      };
      this.category_hover_tooltip[this.layer_options.length - 1] = tooltips;
    },
  },
};



