import jApi from './jApi';

const req = {
  pagination: {
    limit: 0,
    page: 0,
    column: '',
    ascending: false,
    query: '',
    query_type: '',
  },
};

export default {
  getPositionList() {
    const api = jApi.generateApi();
    return api.post('jojoflow/organigram/list', req)
      .then(res => res.data);
  },
};



