<template>
  <div class="form-transfer-budget">
    <b-card>
      <label
        v-if="preview"
        :class="'c--'+transferStatus+ ' bold'">
        Status : {{ transferStatus }} </label>
      <b-row class="p__l--15 p__r--15">
        <div class="card-costcenter">
          <b-card>
            <b-row>
              <b-col :cols="!preview ? '8' : '12'">
                <b-form-group :class="{'error-input' : $v.transfer_budget.selected_source.$error}">
                  <label> Source Cost Center Budget </label>

                  <div>
                    <div v-if="!preview">
                      <div>
                        <b-tooltip
                          v-if="label_source_name"
                          target="showPopoverSource"
                          placement="top"
                          triggers="hover">
                          {{ label_source_name }}
                        </b-tooltip>
                      </div>

                      <j-single-select
                        v-if="refresh_field"
                        id="showPopoverSource"
                        v-model="$v.transfer_budget.selected_source.$model"
                        :is-async="true"
                        :load-options="loadOptions"
                        :disabled="preview || active_destination[0].is_active === false"
                        :label="label_condition"
                        :placeholder="label_source ? 'Type Cost Center Name'
                        : 'Type Cost Center Code'"/>

                    </div>

                    <j-single-select
                      v-else
                      v-model="$v.transfer_budget.selected_source.$model"
                      :options="list_source_preview"
                      :disabled="preview || active_destination[0].is_active === false"
                      label="name"
                      placeholder="Select Source"/>
                  </div>

                </b-form-group>
              </b-col>
              <b-col
                v-if="!preview"
                cols="4">
                <label> Search by </label>
                <b-button-group
                  class="btn-toggle"
                  size="sm">
                  <b-button
                    :disabled="active_destination[0].is_active === false"
                    :class="label_source ? 'btn-active' : ''"
                    class="btn blue"
                    @click="toggleLabel">
                    Name
                  </b-button>
                  <b-button
                    :disabled="active_destination[0].is_active === false"
                    :class="label_source ? '' : 'btn-active'"
                    class="btn blue"
                    @click="toggleLabel">
                    Code
                  </b-button>
                </b-button-group>
              </b-col>
            </b-row>
            <b-form-group>
              <div
                v-if="transfer_budget.selected_source &&
              (user_role === 'admin_budget' || user_role === 'SysAdmin')">
                <j-budget-cost
                  :amount="active_source[0].amount"
                  :used="active_source[0].used"
                  :locked="active_source[0].locked"
                  :substract="$route.params.idAproverStatus === '3' ? 0 : transfer_budget.amount"
                  :accrue="active_source[0].accrue">
                  <div
                    slot="right-center"
                    class="float-right">
                    <span class="italic bold">
                      <j-currency
                        :value="$route.params.idAproverStatus === '3' ?
                        remaining_source_approved : remaining_source">
                        <template slot="text-left">
                          Remaining</template>
                      </j-currency>
                    </span>
                  </div>
                  <div
                    slot="left-bottom"
                    class="budget-comp">
                    <span class="italic bold">
                      <j-currency
                        :value="active_source[0].amount">
                        <template slot="text-left">
                          Budget</template>
                      </j-currency>
                    </span>
                    <span
                      v-if="transfer_budget.amount > 0 && preview
                      && $route.params.idAproverStatus !== '3'"
                      class="italic bold c--purple">
                      <j-currency
                        :value="transfer_budget.amount">
                        <template slot="text-left">
                          Substract</template>
                      </j-currency>
                    </span>
                  </div>
                  <div
                    v-if="!preview"
                    slot="right-bottom"
                    class="float-right budget-comp">
                    <span
                      v-if="transfer_budget.amount > 0"
                      class="italic bold c--purple">
                      <j-currency
                        :value="transfer_budget.amount">
                        <template slot="text-left">
                          Substract</template>
                      </j-currency>
                    </span>
                  </div>
                </j-budget-cost>
              </div>
            </b-form-group>
          </b-card>
        </div>
        <div class="div-arrow">
          <i class="fa fa-arrow-right"/>
        </div>
        <div class="card-costcenter">
          <b-card>
            <b-form-group>
              <label> Destination Cost Center Budget </label> <br>
              <j-input
                v-model="transfer_budget.destination_costcenter.name"
                placeholder="Search Cost Center"
                disabled/>
            </b-form-group>
            <b-form-group
              v-if="user_role === 'admin_budget' || user_role === 'SysAdmin'">
              <j-budget-cost
                :amount="active_destination[0].amount"
                :used="active_destination[0].used"
                :locked="active_destination[0].locked"
                :accrue="active_destination[0].accrue">
                <div
                  slot="right-center"
                  class="float-right">
                  <span class="italic bold">
                    <j-currency
                      :value="$route.params.idAproverStatus === '3' ?
                      remaining_destination_approved : remaining_destination">
                      <template slot="text-left">
                        Remaining</template>
                    </j-currency>
                  </span>
                </div>
                <div
                  slot="left-bottom"
                  class="budget-comp">
                  <span class="italic bold">
                    <j-currency
                      :value="active_destination[0].amount">
                      <template slot="text-left">Budget</template>
                    </j-currency>
                  </span>
                  <span
                    v-if="transfer_budget.amount > 0 && preview
                    && $route.params.idAproverStatus !== '3'"
                    class="c--orange italic bold">
                    <j-currency
                      :value="transfer_budget.amount">
                      <template slot="text-left">
                        Add</template>
                    </j-currency>
                  </span>
                </div>
                <div
                  v-if="!preview"
                  slot="right-bottom"
                  class="float-right budget-comp">
                  <span
                    v-if="transfer_budget.amount > 0"
                    class="c--orange italic bold">
                    <j-currency
                      :value="transfer_budget.amount">
                      <template slot="text-left">
                        Add</template>
                    </j-currency>
                </span></div>
              </j-budget-cost>
            </b-form-group>
          </b-card>
        </div>
      </b-row>
      <br>
      <b-row>
        <b-col md="4">
          <b-form-group :class="{'error-input' : $v.transfer_budget.amount.$error}">
            <label> Amount of budget </label>
            <j-currency
              v-model="$v.transfer_budget.amount.$model"
              :disabled="preview || active_destination[0].is_active === false"
              :input_mode="true"
              class="budget-comp"
              type="number"
              placeholder="Budget Amount"/>
          </b-form-group>
        </b-col>
      </b-row>
      <b-row>
        <b-col
          v-if="!preview"
          md="6">
          <b-form-group>
            <label> Approver </label>
            <j-multi-select
              v-model="transfer_budget.approver"
              :options="list_approvers"
              :disabled="preview || active_destination[0].is_active === false"
              label="name"
              placeholder="Select Approver"/>
          </b-form-group>
        </b-col>
        <b-col md="6">
          <b-form-group :class="{'error-input' : $v.transfer_budget.note.$error}">
            <label> Notes </label>
            <j-input
              :disabled="preview || active_destination[0].is_active === false"
              :max-length="300"
              v-model="$v.transfer_budget.note.$model"
              placeholder="Notes"/>
          </b-form-group>
        </b-col>
        <b-col
          v-if="!preview"
          md="12 align-end">
          <b-button
            :disabled="active_destination[0].is_active === false"
            class="btn orange outline float-right margin-bottom-button"
            @click="save"> Transfer </b-button>
        </b-col>
        <b-col
          v-else-if="preview && transfer_budget.status === 2"
          md="6 align-end">
          <b-row>
            <b-col md="8">
              <b-button
                class="btn orange outline float-right margin-bottom-button"
                @click="approveAllocation"> Approve </b-button>
            </b-col>
            <b-col md="4">
              <b-button
                class="btn purple outline float-right margin-bottom-button"
                @click="rejectAllocation"> Reject </b-button>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
      <label class="italic">Budget Incoming Transfer Log History</label>
      <b-form-group class="form-table-list">
        <j-transfer-table/>
      </b-form-group>

      <label class="italic">Budget Transfer Out Log History</label>
      <b-form-group class="form-table-list">
        <j-transfer-table-out/>
      </b-form-group>

      <label class="italic">Budget Transaction Log History</label>
      <i
        v-b-toggle="'mutation-collapse'"
        class="fa fa-angle-down"/>
      <j-collapse
        id="mutation-collapse"
        :visible="false">
        <b-form-group class="form-table-list2">
          <j-transaction-table/>
        </b-form-group>
      </j-collapse>
    </b-card>
  </div>
</template>

<script>
import jPrivCostCenter from '@/privilage/views/cost-center/jPrivCostCenter';
import jBudgetCost from '@/components/budget-component/jBudgetComponent';
import jTransferTable from '@/components/cost-center-form/jTransferTable';
import jTransferTableOut from '@/components/cost-center-form/jTransferTableOut';
import jTransactionTable from '@/components/cost-center-form/jTransactionTable';
import { jMixOptionsFormTransferBudget } from '@/mixins/list-options/transfer-budget/jMixOptionsFormTransferBudget';
import { jMixModeCreateTransferBudget } from '@/mixins/mode-form/transfer-budget/jMixModeCreateTransferBudget';
import { jMixModePreviewTransferBudget } from '@/mixins/mode-form/transfer-budget/jMixModePreviewTransferBudget';
import { jMixReusableTransferBudget } from '@/mixins/mode-form/transfer-budget/jMixReusableTransferBudget';
import { jMixValidationFormTransferBudget } from '@/mixins/validations/transfer-budget/jMixValidationFormTransferBudget';
import { jMixModelCreateTransferBudget } from '@/mixins/models/transfer-budget/jMixModelCreateTransferBudget';
import { jMixModelDetailTransferBudget } from '@/mixins/models/transfer-budget/jMixModelDetailTransferBudget';

export default {
  components: {
    jBudgetCost,
    jTransferTable,
    jTransferTableOut,
    jTransactionTable,
  },
  mixins: [
    jPrivCostCenter,
    jMixOptionsFormTransferBudget,
    jMixModeCreateTransferBudget,
    jMixModePreviewTransferBudget,
    jMixReusableTransferBudget,
    jMixValidationFormTransferBudget,
    jMixModelCreateTransferBudget,
    jMixModelDetailTransferBudget,
  ],
  props: {
    preview: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      transfer_budget: {
        amount: null,
        selected_source: null,
        source_costcenter: [],
        destination_costcenter: [],
        note: '',
        approver: [],
        status: '',
      },
      mode: 'create',
    };
  },
};
</script>



