<template>
  <div>
    <v-server-table
      :url="env"
      :columns="columns"
      :options="options"
      class="jTable"
      @row-click="rowClick">
      <div
        slot="amount"
        slot-scope="props">
        <j-currency
          :value="props.row.amount"/>
      </div>
      <div
        slot="source"
        slot-scope="props">
        {{ props.row.source_costcenter.name }}
      </div>
      <div
        slot="destination"
        slot-scope="props">
        {{ props.row.destination_costcenter.name }}
      </div>
      <div
        slot="created_date"
        slot-scope="props">
        {{ props.row.created_date | moment("DD MMM, YYYY") }}
      </div>
    </v-server-table>
  </div>
</template>
<script>
import axios from 'axios';
import Vue from 'vue';

export default {
  data() {
    return {
      columns: ['id', 'amount', 'source', 'destination', 'created_date'],
      env: 'costcenter/approver/list',
      options: {
        headings: {
          id: 'REF ID',
          amount: 'AMOUNT',
          source: 'SOURCE',
          destination: 'DESTINATION',
          created_date: 'CREATED DATE',
        },
        filterable: false,
        perPage: 5,
        perPageValues: [5, 10, 20, 50, 100],
        requestAdapter(data) {
          return {
            pagination: {
              limit: data.limit,
              page: data.page,
              column: data.orderBy ? data.orderBy : '',
              ascending: !!data.ascending,
              query: data.query ? data.query : '',
            },
            status: 3,
          };
        },
        requestFunction(data) {
          const token = Vue.ls.get('Token');

          const api = axios.create({
            baseURL: process.env.GATE_URL,
            headers: {
              Authorization: `Bearer ${token}`,
            },
          });
          return api.post(this.url, data).catch((e) => {
            this.dispatch('error', e);
            this.$store.commit('jStoreNotificationScreen/toggleProblem', e.message);
          });
        },
        responseAdapter({ data }) {
          return {
            data: data.data || [],
            count: data.length,
          };
        },
      },
    };
  },
  methods: {
    rowClick(data) {
      this.$router.replace(`/dashboard/budget-transfer/detail/${data.row.approver_status}/${data.row.id}`);
    },
  },
};
</script>



