/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapActions } from 'vuex';

export const jMixModelCreateCategoryLayer = {
  methods: {
    modelDataCreate(valAdd) {
      const genData = {
        id: valAdd.id,
        package: valAdd.package,
        root_id: valAdd.root_id,
        name: valAdd.name,
        relation_id: valAdd.relation_id ? valAdd.relation_id : 0,
        is_delete: false,
        is_have_child: valAdd.is_have_child ? valAdd.is_have_child : false,
      };
      return genData;
    },

    generateDataCreate() {
      const getIdLastChild = this.layer_categories[this.layer_categories.length - 1];
      if (Array.isArray(getIdLastChild)) {
        const sendData = getIdLastChild.map(valAdd => this.modelDataCreate(valAdd));
        this.$emit('lastChild', sendData);
      } else {
        // eslint-disable-next-line
        const getDetailData = this.layer_options[this.layer_options.length - 1].filter(noId => noId.id === getIdLastChild);
        const sendData = getDetailData.map(valAdd => this.modelDataCreate(valAdd));
        this.$emit('lastChild', sendData);
      }
    },
  },
};



