<template>
  <div class="w__percent--80">

    <div class="jback-to-route">
      <router-link
        to="/dashboard/config">
        <i class="fa fa-times"/>
      </router-link>
    </div>

    <br>

    <div class="jContent">
      <b-card>

        <div class="float-to-center">
          <h2>Edit Period</h2>
        </div>

        <br>

        <j-period-form
          ref="form"
          mode="edit"
          @data="getData"/>

        <b-row>
          <b-col>
            <div class="float-to-left m__t--50">
              <b-button
                class="btn blue m__r--15"
                @click="saveEditPeriod">SAVE</b-button>
              <b-button
                class="btn grey"
                @click="$router.replace('/dashboard/config')">CANCEL</b-button>
            </div>
          </b-col>

          <b-col>
            <div class="float-to-right m__t--50">
              <b-button
                :class="is_used_transaction_edit ? 'disabled' : ''"
                class="btn red"
                @click="deleteData">DELETE</b-button>
            </div>
          </b-col>
        </b-row>

      </b-card>
    </div>

  </div>
</template>

<script>
import jPeriodForm from '@/components/form/config/jPeriodForm';
import { jMixModelEditPeriod } from '@/mixins/models/config/jMixModelEditPeriod';
import { jMixModeEditPeriod } from '@/mixins/mode-form/config/jMixModeEditPeriod';
import { jMixModelDeletePeriod } from '@/mixins/models/config/jMixModelDeletePeriod';

export default {
  components: {
    jPeriodForm,
  },
  mixins: [
    jMixModelEditPeriod,
    jMixModelDeletePeriod,
    jMixModeEditPeriod,
  ],
  data() {
    return {
      period_form: null,
    };
  },
  methods: {

    saveEditPeriod() {
      this.$refs.form.save();
      if (this.period_form !== null) {
        this.sendData();
      }
    },

    getData(data) {
      this.period_form = data;
    },

  },
};
</script>



