import jLogin from '@/views/login/jLogin';
import Vue from 'vue';

export default [
  {
    path: '/',
    name: 'jLogin',
    component: jLogin,
    beforeEnter(to, from, next) {
      if (Vue.ls.get('Token')) {
        next('/dashboard/cost-center');
      } else if (process.env.NODE_ENV === 'production') {
        window.location = '/';
        next();
      } else {
        next();
      }
    },
    meta: {
      title: 'Login',
    },
  },
];



