import jApi from './jApi';

export default {
  getCostCenterList(dataUser) {
    const apis = jApi.generateApi();
    return apis.post('costcenter/list', dataUser)
      .then(res => res.data);
  },

  deleteCostCenter(dataUser) {
    const apis = jApi.generateApi();
    return apis.post('costcenter/delete', dataUser)
      .then(res => res);
  },

  getDetailCostCenter(dataUser) {
    const apis = jApi.generateApi();
    return apis.post('costcenter/detail', dataUser)
      .then(res => res.data);
  },

  createCostCenter(req) {
    const apis = jApi.generateApi();
    return apis.post('costcenter/create', req)
      .then(res => res.data);
  },

  updateCostCenter(req) {
    const apis = jApi.generateApi();
    return apis.post('costcenter/update', req)
      .then(res => res.data);
  },

  getListSources(req) {
    const apis = jApi.generateApi();
    return apis.post('costcenter/list/last-child', req)
      .then(res => res.data);
  },

  getCostCenterListByCustody(req) {
    const api = jApi.generateApi();
    return api.post('costcenter/custody/list-by-child', req)
      .then(res => res.data);
  },

  getListCostcenterProject(req) {
    const apis = jApi.generateApi();
    return apis.post('costcenter/project/list', req)
      .then(res => res.data);
  },

  getListCostcenterCustodyProject(req) {
    const apis = jApi.generateApi();
    return apis.post('costcenter/project/custody/list', req)
      .then(res => res.data);
  },

  searchCostCenterAdmin(req) {
    const apis = jApi.generateApi();
    return apis.post('costcenter/search', req)
      .then(res => res);
  },

  searchCostCenterCustody(req) {
    const apis = jApi.generateApi();
    return apis.post('costcenter/search/custody', req)
      .then(res => res);
  },
};



