/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */

export const jMixOptionsFormPeriod = {
  created() {
    if (this.mode === 'create') {
      this.modeCreate();
    } else if (this.mode === 'edit') {
      this.modeEdit();
    }
  },

  computed: {
    is_used_transaction_disabled() {
      if (this.mode === 'create') {
        return false;
      } else if (this.mode === 'edit') {
        return this.is_used_transaction_edit;
      }
      return false;
    },
  },

};



