import Vue from 'vue';
import Vuex from 'vuex';

// Modules import downhere
import jStoreLogin from './modules/jStoreLogin';
import jStoreNotificationScreen from './modules/jStoreNotificationScreen';
import jStoreConfig from './modules/jStoreConfig';
import jStoreCostCenter from './modules/jStoreCostCenter';
import jStoreBudgetAllocation from './modules/jStoreBudgetAllocation';
import jStoreReport from './modules/jStoreReport';
import jStoreMappingLayer from './modules/jStoreMappingLayer';

// Initialize vuex
Vue.use(Vuex);

const debug = process.env.NODE_ENV !== 'production';

export default new Vuex.Store({
  modules: {
    // Include imported modules in here
    jStoreLogin,
    jStoreNotificationScreen,
    jStoreConfig,
    jStoreCostCenter,
    jStoreBudgetAllocation,
    jStoreReport,
    jStoreMappingLayer,
  },
  strict: debug,
});



