/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
// eslint-disable-next-line
import { ASYNC_SEARCH } from '@riophae/vue-treeselect';
import jApi from '@/services/jApi';

export const jMixModeCreateTransferBudget = {
  data() {
    return {
      label_source_name: null,
      refresh_field: true,
      label_source: true,
    };
  },
  computed: {
    label_condition() {
      if (this.label_source) {
        return 'name';
      }
      return 'code';
    },
  },
  methods: {
    modeCreate() {

    },
    getListSource(req, callback, paramThis) {
      const api = jApi.generateApi();
      return api.post('costcenter/list/last-child', req)
        .then((res) => {
          res.data.data.forEach((lab, index) => {
            // eslint-disable-next-line
            res.data.data[index].label = lab[paramThis.label_condition];
          });
          if (res.data.data.length > 1) {
            callback(null, null);
          } else {
            if (res.data.data[0].label.length > 24) {
              // eslint-disable-next-line
              paramThis.label_source_name = res.data.data[0].label;
            }
            callback(null, res.data.data);
          }
          return res.data;
        });
    },

    toggleLabel() {
      this.label_source = !this.label_source;
      this.refresh_field = false;
      this.transfer_budget.selected_source = null;
      setTimeout(() => {
        this.refresh_field = true;
      }, 100);
    },

    loadOptions({ action, searchQuery, callback }) {
      this.label_source_name = null;
      if (action === ASYNC_SEARCH) {
        const reqData = {
          pagination: {
            limit: 10,
            page: 1,
            column: '',
            ascending: false,
            query: searchQuery,
          },
          costcenter: {
            destination_costcenter_id: Number(this.$route.params.id),
          },
        };
        if (searchQuery.length > 5) {
          this.getListSource(reqData, callback, this);
        }
      }
    },

    getListSourcesCreate(val) {
      const filterDat = this.list_sources.filter(data => data.id === val);
      if (filterDat.length > 0) {
        this.transfer_budget.source_costcenter = filterDat[0];
      }
    },
  },
};



