<template>
  <div>
    <j-title-header :options="title"/>
    <jCreateMappingLayer v-if="mode_create"/>
    <jEditMappingLayer v-else/>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import jEditMappingLayer from './edit-mapping-layer/jEditMappingLayer';
import jCreateMappingLayer from './create-mapping-layer/jCreateMappingLayer';

export default {
  components: {
    jEditMappingLayer,
    jCreateMappingLayer,
  },
  data() {
    return {
      title: {
        icon: 'fa-cog',
        main_title: 'MAPPING LAYER',
        opt_title: 'Administrator',
      },
    };
  },
  computed: {
    ...mapState('jStoreMappingLayer', ['mapping_layer_detail']),
    mode_create() {
      if (this.mapping_layer_detail !== null) {
        if (this.mapping_layer_detail.length === 0) {
          return true;
        }
        return false;
      }
      return true;
    },
  },
  created() {
    this.getListMapping();
  },
  methods: {
    ...mapActions('jStoreMappingLayer', ['getList']),
    getListMapping() {
      const req = {
        pagination: {
          limit: 0,
          page: 1,
          column: 'order',
          ascending: true,
          query_type: '',
          query: '',
        },
      };
      this.getList(req);
    },
  },
};
</script>



