<template>
  <div>
    <b-row>
      <b-col md="8">
        <h4>
          <i class="fa fa-clipboard"/>
          Budget Allocation Detail
        </h4>
        <hr>
        <j-transfer-budget :preview="true"/>
      </b-col>
      <b-col md="4">
        <h4>
          <i class="fa fa-clipboard"/>
          Logs
        </h4>
        <hr>
        <b-card>
          <j-timeline :data="mydata"/>
        </b-card>
      </b-col>
    </b-row>
  </div>
</template>
<script>
import { mapActions, mapState } from 'vuex';
import jTransferBudget from '@/components/cost-center-form/jTransferBudget';

export default {
  components: {
    jTransferBudget,
  },
  computed: {
    ...mapState('jStoreBudgetAllocation', ['transfer_log']),
    mydata() {
      const dataLog = this.transfer_log.map((e) => {
        const time = this.$moment(e.date_time).format('YYYY-MM-DD');
        const filterData = {
          status: e.status,
          photo: e.user.profile.photo_url,
          position: e.name,
          email: e.user.email,
          first_name: e.user.profile.first_name,
          last_name: e.user.profile.last_name,
          message: e.message,
          note: e.note,
          date_time: time,
        };
        return filterData;
      });
      return dataLog;
    },
  },
  created() {
    if (this.$route.params.idTransfer) {
      this.mode = 'edit';
      const transferId = {
        reference_id: Number(this.$route.params.idTransfer),
        package: 5,
      };
      this.getBudgetAllocationLog(transferId);
    }
  },
  methods: {
    ...mapActions('jStoreBudgetAllocation', ['getBudgetAllocationLog']),
  },
};
</script>



