import jApi from './jApi';

export default {
  createPeriod(newPeriodData) {
    const api = jApi.generateApi();
    return api.post('costcenter/config/period/create', newPeriodData)
      .then(res => res.data);
  },

  getPeriodList(req) {
    const api = jApi.generateApi();
    return api.post('costcenter/config/period/list', req)
      .then(res => res);
  },

  getPeriodDetail(periodeId) {
    const api = jApi.generateApi();
    return api.post('costcenter/config/period/detail', periodeId)
      .then(res => res.data);
  },

  updatePeriodDetail(periodData) {
    const api = jApi.generateApi();
    return api.post('costcenter/config/period/update', periodData)
      .then(res => res.data);
  },

  deletePeriod(periodeId) {
    const api = jApi.generateApi();
    return api.post('costcenter/config/period/delete', periodeId)
      .then(res => res.data);
  },
};



