/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapActions, mapState } from 'vuex';
import jApi from '@/services/jApi';
import apiPeriod from '@/services/jApiPeriod';
import jReusableFunction from '@/mixins/paginations/jReusableFunction';

export const jMixPaginationPeriod = {
  data() {
    return {
      select_type: 0,
      options_search: [
        {
          text: 'BAU',
          value: 1,
        },
        {
          text: 'Project',
          value: 2,
        },
        {
          text: 'All',
          value: 0,
        },
      ],
      filter_period: '',
      data_table: [],
      selected_rows: [],
      load_first: false,
      columns: ['checkbox', 'name', 'description', 'period', 'backdate_settlement', 'blackout_date'],
      options: {
        headings: {
          name: 'NAME',
          description: 'DESCRIPTION',
          period: 'PERIOD',
          backdate_settlement: 'BACKDATE',
          blackout_date: 'BLACKOUT DATE PERIOD',
          checkbox(h) {
            const objThis = this;
            return jReusableFunction.checkbox(h, objThis);
          },
        },
        texts: {
          filterPlaceholder: 'Search',
          limit: 'Rows per page',
        },
        sortIcon: {
          base: 'fa',
          up: 'fa-chevron-up',
          down: 'fa-chevron-down',
          is: 'fa-sort',
        },
        debounce: 1000,
        filterable: true,
        sortable: ['name'],
        perPage: 5,
        perPageValues: [5, 10, 20, 50, 100],
        requestAdapter(data) {
          return {
            pagination: {
              limit: data.limit,
              page: data.page,
              column: data.orderBy ? data.orderBy : '',
              ascending: !!data.ascending,
              query: data.query ? data.query : '',
            },
            costcenter: {
              root_id: 0,
            },
            type: 0,
            filter: {
              type: data.type ? data.type : '',
            },
          };
        },
        requestFunction(data) {
          const getListPeriod = apiPeriod.getPeriodList(data);
          return getListPeriod.catch((e) => {
            this.$store.dispatch('jStoreNotificationScreen/toggleProblem', e.response.data.errors[0].message).bind(this);
          });
        },
        responseAdapter({ data }) {
          return {
            data: data.data ? data.data : [],
            count: data.length ? data.length : 0,
          };
        },
      },
    };
  },
  computed: {
    // ...mapState('jStoreProject', ['project_data']),
  },
  methods: {
    ...mapActions('jStoreNotificationScreen', ['toggleProblem', 'showLoading', 'hideLoading']),

    loading(val) {
      const a = val;
      a.filter.type = this.select_type;
      a.type = this.select_type;
      if (this.load_first) {
        this.showLoading();
      }
    },

    loaded(data) {
      this.data_table = data.data.data;
      if (this.load_first) {
        this.hideLoading();
      } else {
        this.load_first = !this.load_first;
      }
    },

    refreshTable() {
      this.$refs.periodServer.getData();
    },

    periodsDeleted() {
      this.refreshTable();
      this.selected_rows = [];
    },

    customFilter(val) {
      if (val !== '') {
        this.$refs.periodServer.setFilter(val);
      }
    },
  },
  watch: {
    selected_rows() {
      if (this.selected_rows.length === 0) {
        this.$refs.floating.hide();
      } else {
        this.$refs.floating.show();
      }
    },

    filter_period(val) {
      if (val === '') {
        this.$refs.periodServer.setFilter('');
      }
    },

    select_type() {
      this.refreshTable();
    },

    // eslint-disable-next-line
    // 'project_data.project_updated': function() {
    //   this.refreshTable();
    // },

    // eslint-disable-next-line
    // 'project_data.project_deleted': function() {
    //   this.refreshTable();
    // },

    // eslint-disable-next-line
    // 'project_data.project_created': function() {
    //   this.refreshTable();
    // },
  },
};



