import jApi from './jApi';

export default {
  // getMCList() {
  //   return api.post('costcenter/expense/team/category')
  //     .then(res => res.data);
  // },
  // getSLList(req) {
  //   return api.post('costcenter/expense/category/child/list', req)
  //     .then(res => res.data);
  // },
  getActivityList(req) {
    const apis = jApi.generateApi();
    return apis.post('expense-setup/expense-layer/relation/child-list', req)
      .then(res => res.data);
  },
  getLayerCategory(req) {
    const apis = jApi.generateApi();
    return apis.post('expense-setup/expense-layer/relation/by-division', req)
      .then(res => res.data);
  },
  getLayerList() {
    const apis = jApi.generateApi();
    return apis.get('expense-setup/expense-layer/setup/list')
      .then(res => res.data);
  },
};



