<template>
  <div>
    <b-row v-if="costcenter_data.list_mc.length > 0">
      <b-col
        v-for="(layer, index) in layer_options"
        :key="index"
        cols="4">
        <b-form-group :class="{'error-input' : isError(index) }">
          <div>
            <div v-if="index === is_has_child.index && is_has_child.is_has">
              <b-row>
                <b-col>
                  <label> {{ costcenter_data.list_layer[index].name }}* </label>
                </b-col>
                <b-col class="float-to-right">
                  <label
                    class="c--blue pointer--cursor"
                    @click="selectAll(index)">
                    Select All
                  </label>
                </b-col>
              </b-row>
              <j-multi-select
                v-model="layer_categories[index]"
                :disabled="is_used_transaction_disabled"
                :options="layer"
                value-format="object"
                placeholder="Select Layer"
                label="name"
                @getData="selectLayer(index)"/>
            </div>

            <div v-else>
              <div>
                <b-tooltip
                  v-if="category_hover_tooltip[index].is_show"
                  :target="'layerCategory'+index"
                  placement="top"
                  triggers="hover">
                  {{ category_hover_tooltip[index].label }}
                </b-tooltip>
              </div>
              <label> {{ costcenter_data.list_layer[index].name }}* </label>
              <j-single-select
                :id="'layerCategory'+index"
                :options="layer"
                :disabled="is_used_transaction_disabled"
                v-model="layer_categories[index]"
                placeholder="Select Layer"
                label="name"
                @getData="selectLayer(index)"/>
            </div>
          </div>

        </b-form-group>
      </b-col>

      <b-col
        v-if="loading_skeleton"
        cols="4">
        <jLoadingField />
      </b-col>

    </b-row>
  </div>
</template>

<script>
import jLoadingField from '@/components/skeleton-loading/jLoadingField';
import { jMixValidationFormLayerCategory } from '@/mixins/validations/costcenter/jMixValidationFormLayerCategory';
import { jMixOptionsFormLayerCategory } from '@/mixins/list-options/costcenter/jMixOptionsFormLayerCategory';
import { jMixModeCreateCategoryLayer } from '@/mixins/mode-form/costcenter/jMixModeCreateCategoryLayer';
import { jMixModeEditCategoryLayer } from '@/mixins/mode-form/costcenter/jMixModeEditCategoryLayer';
import { jMixModelCreateCategoryLayer } from '@/mixins/models/costcenter/jMixModelCreateCategoryLayer';
import { jMixModelEditCategoryLayer } from '@/mixins/models/costcenter/jMixModelEditCategoryLayer';

export default {
  components: {
    jLoadingField,
  },
  mixins: [
    jMixValidationFormLayerCategory,
    jMixOptionsFormLayerCategory,
    jMixModeCreateCategoryLayer,
    jMixModeEditCategoryLayer,
    jMixModelCreateCategoryLayer,
    jMixModelEditCategoryLayer,
  ],
  props: {
    mode: {
      type: String,
      default: 'create',
    },
  },
  data() {
    return {
      layer_categories: [],
      layer_options: [],
      list_id_category: [],
      last_old_layer: null,
      is_has_child: {
        index: 0,
        is_has: false,
      },
      loading_skeleton: false,
    };
  },
  methods: {

    validateForm() {
      this.$v.layer_categories.$touch();
      if (this.$v.layer_categories.$error) {
        this.$emit('getData', null);
        this.$emit('lastChild', null);
      }
    },

    selectAll(index) {
      if (this.layer_categories[index].length !== this.layer_options[index].length) {
        this.layer_categories[index].splice(0);
        this.layer_categories[index].push(...this.layer_options[index]);
      }
    },
  },
};
</script>



