<template>
  <div class="w__percent--80">

    <div class="jback-to-route">
      <router-link
        to="/dashboard/config">
        <i class="fa fa-times"/>
      </router-link>
    </div>

    <br>

    <div class="jContent">
      <b-card>

        <div class="float-to-center">
          <h2>Create New Period</h2>
        </div>

        <br>

        <j-period-form
          ref="form"
          mode="create"
          @data="getData"/>

        <div class="float-to-right m__t--50">
          <b-button
            class="btn blue m__r--15"
            @click="saveCreatePeriod">SAVE</b-button>
          <b-button
            class="btn grey"
            @click="$router.replace('/dashboard/config')">CANCEL</b-button>
        </div>

      </b-card>
    </div>

  </div>
</template>

<script>
import jPeriodForm from '@/components/form/config/jPeriodForm';
import { jMixModelCreatePeriod } from '@/mixins/models/config/jMixModelCreatePeriod';

export default {
  components: {
    jPeriodForm,
  },
  mixins: [
    jMixModelCreatePeriod,
  ],
  data() {
    return {
      period_form: null,
    };
  },
  methods: {

    saveCreatePeriod() {
      this.$refs.form.save();
      if (this.period_form !== null) {
        this.sendData();
      }
    },

    getData(data) {
      this.period_form = data;
    },
  },

};
</script>



