/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
// eslint-disable-next-line import/no-extraneous-dependencies
import { ASYNC_SEARCH } from '@riophae/vue-treeselect';
import jApi from '@/services/jApi';
import { mapState, mapActions } from 'vuex';

export const jMixModeFilterReport = {
  data() {
    return {
      project_types: [
        {
          id: 0,
          name: 'All',
        },
        {
          id: 1,
          name: 'Non Project',
        },
        {
          id: 2,
          name: 'Project',
        },
      ],
    };
  },
  computed: {
    ...mapState('jStoreReport', ['list_period', 'list_division', 'list_root_category']),
  },
  methods: {
    ...mapActions('jStoreReport', ['getPeriodList', 'getDivisionList', 'getListRootCategory']),
    modeFilterReport() {
      this.getDivisions();
      this.getListRoot();
    },
    getDivisions() {
      const dataReq = {
        pagination: {
          limit: 0,
          page: 0,
          column: '',
          ascending: false,
          query: '',
          query_type: '',
        },
      };
      this.getDivisionList(dataReq);
    },

    getListRoot() {
      const dataReq = {
        pagination: {
          limit: 5,
          page: 1,
          column: '',
          ascending: true,
          query: '',
        },
      };
      this.getListRootCategory(dataReq);
    },

    getPeriodByType(type) {
      if (type !== undefined) {
        this.export_report.period_ids = [];
        const periodData = {
          pagination: {
            limit: 0,
            page: 0,
            column: '',
            ascending: false,
            query: '',
          },
          costcenter: {
            root_id: 0,
          },
          type: type,
        };
        this.getPeriodList(periodData);
      } else {
        this.export_report.period_ids = [];
      }
    },


    getListSource(req, callback) {
      const api = jApi.generateApi();
      return api.post('costcenter/list/last-child', req)
        .then((res) => {
          res.data.data.forEach((lab, index) => {
            // eslint-disable-next-line
                res.data.data[index].label = lab.code;
          });
          if (res.data.data.length > 1) {
            callback(null, null);
          } else {
            callback(null, res.data.data);
          }
          return res.data;
        });
    },
    loadOptions({ action, searchQuery, callback }) {
      if (action === ASYNC_SEARCH) {
        const reqData = {
          pagination: {
            limit: 10,
            page: 1,
            column: '',
            ascending: false,
            query: searchQuery,
          },
          costcenter: {
            destination_costcenter_id: 0,
          },
        };
        if (searchQuery.length > 5) {
          this.getListSource(reqData, callback);
        }
      }
    },
  },
};



