<template>
  <div>
    <div
      v-for="(data, index) in data_summary"
      :key="index">
      <h4 v-if="data.active">
        <i class="fa fa-clipboard"/>
        Budget Allocation : <label :class="'c--'+data.status+' bold'">{{ data.status }}</label>
      </h4>
    </div>
    <hr>
    <b-row>
      <b-col md="6">
        <j-summary :data-summary="data_summary"/>
      </b-col>
    </b-row>
    <router-view/>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex';

export default {
  data() {
    return {
      data_status: [
        {
          status: 'Sent',
          amounts: 0,
          summaryDate: 'Aug 12, 2018',
          isStart: true,
          isEnd: false,
          color: 'yellow',
          active: false,
          href: '/dashboard/budget-transfer/list/sent',
        },
        {
          status: 'Approve',
          amounts: 0,
          summaryDate: 'Sep 12, 2018',
          isStart: false,
          isEnd: true,
          color: 'green',
          active: false,
          href: '/dashboard/budget-transfer/list/approved',
        },
        {
          status: 'Rejected',
          amounts: 0,
          summaryDate: 'Sep 12, 2018',
          isStart: true,
          isEnd: true,
          color: 'green',
          active: false,
          href: '/dashboard/budget-transfer/list/rejected',
        },
      ],
    };
  },
  computed: {
    ...mapState('jStoreBudgetAllocation', ['total']),
    data_sum() {
      this.total.forEach((a, index) => {
        this.$set(this.data_status[index], 'status', a.status);
        this.$set(this.data_status[index], 'color', a.status);
        this.$set(this.data_status[index], 'amounts', a.total);
      });
      return this.data_status;
    },
    data_summary() {
      return this.checkActive(this.data_sum);
    },
  },
  created() {
    this.getTotalApprover();
  },
  methods: {
    ...mapActions('jStoreBudgetAllocation', ['getTotalApprover']),
    checkActive(val) {
      if (this.$route.path === '/dashboard/budget-transfer/list/sent') {
        const status = 'process';
        this.setActive(val, status);
      } else if (this.$route.path === '/dashboard/budget-transfer/list/approved') {
        const status = 'approved';
        this.setActive(val, status);
      } else if (this.$route.path === '/dashboard/budget-transfer/list/rejected') {
        const status = 'rejected';
        this.setActive(val, status);
      }
      return val;
    },

    setActive(val, status) {
      val.forEach((e) => {
        const a = e;
        if (a.status === status) {
          this.$set(a, 'active', true);
        } else {
          this.$set(a, 'active', false);
        }
      });
      return val;
    },
  },
};
</script>



