import jConfig from '@/views/dashboard/config/jConfig';
import jEditPeriod from '@/views/dashboard/config/edit-period/jEditPeriod';
import jCreatePeriod from '@/views/dashboard/config/create-period/jCreatePeriod';
import auth from '@/middleware/auth';
// import budgetCustody from '@/middleware/budget-custody';
import adminBudget from '@/middleware/admin-budget';

export default [
  {
    path: '/dashboard/config',
    name: 'jConfig',
    component: jConfig,
    meta: {
      title: 'Config',
      middleware: [auth, adminBudget],
      layout: 'jWrapper',
    },
  },
  {
    path: '/dashboard/config/create',
    name: 'jCreatePeriod',
    component: jCreatePeriod,
    meta: {
      title: 'Create Period',
      middleware: [auth, adminBudget],
      layout: 'jBlankCenter',
    },
  },
  {
    path: '/dashboard/config/edit',
    name: 'jEditPeriod1',
    component: jEditPeriod,
    meta: {
      title: 'Edit Period',
      middleware: [auth, adminBudget],
      layout: 'jBlankCenter',
    },
  },
  {
    path: '/dashboard/config/edit/:id',
    name: 'jEditPeriod',
    component: jEditPeriod,
    meta: {
      title: 'Edit Period',
      middleware: [auth, adminBudget],
      layout: 'jBlankCenter',
    },
  },
];



