/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapActions } from 'vuex';

export const jMixModelEditPeriod = {
  computed: {
    generateData() {
      if (this.period_form !== null) {
        const genData = {
          period: {
            id: this.period_form.id,
            name: this.period_form.name,
            description: this.period_form.description ? this.period_form.description : '',
            start_date: this.period_form.start_date,
            end_date: this.period_form.end_date,
            backdate_settlement: this.period_form.backdate_settlement,
            type: this.period_form.project_based ? 2 : 1,
            blackout_date: {
              start_date: this.period_form.blackout_date.start_date ? this.period_form.blackout_date.start_date : '2019-01-19',
              end_date: this.period_form.blackout_date.end_date ? this.period_form.blackout_date.end_date : '2019-01-29',
            },
          },
        };
        return genData;
      }
      return null;
    },
  },
  methods: {
    ...mapActions('jStoreConfig', ['updatePeriodDetail']),
    sendData() {
      this.updatePeriodDetail(this.generateData);
    },
  },
};



