/* eslint-disable no-shadow,consistent-return,no-param-reassign */
import apiPeriod from '@/services/jApiPeriod';
import apiDivision from '@/services/jApiDivision';
import apiReport from '@/services/jApiReport';

const state = {
  // Set default state(s) here
  list_period: [],
  list_division: [],
  report_res: null,
  list_root_category: [],
  budget_res: null,
};

const getters = {
  // Change value of state(s) with getter
};

const mutations = {
  showListPeriod(state, data) {
    state.list_period = data.data.data;
  },
  showListDivision(state, data) {
    state.list_division = data.data;
  },
  showExportReportRes(state, data) {
    state.report_res = data.data.data;
  },
  showListRootCategory(state, data) {
    state.list_root_category = data.data.data;
  },
  showExportBudgetRes(state, data) {
    state.budget_res = data.data.data;
  },
};

const actions = {
  async getPeriodList({ commit, dispatch }, data) {
    dispatch('jStoreNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      const listPeriod = await apiPeriod.getPeriodList(data);
      commit('showListPeriod', listPeriod);
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
    } catch (err) {
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
      dispatch('jStoreNotificationScreen/toggleProblem', err.response.data.message, { root: true });
    }
  },

  async getDivisionList({ commit, dispatch }, data) {
    try {
      const listDivision = await apiDivision.getDivisionList(data);
      commit('showListDivision', listDivision);
    } catch (error) {
      dispatch('jStoreNotificationScreen/toggleProblem', error.response.data.message, { root: true });
    }
  },

  async getListRootCategory({ commit, dispatch }, data) {
    try {
      const listRootCategory = await apiReport.getListRootCategory(data);
      commit('showListRootCategory', listRootCategory);
    } catch (error) {
      dispatch('jStoreNotificationScreen/toggleProblem', error.response.data.message, { root: true });
    }
  },

  async exportReport({ commit, dispatch }, data) {
    dispatch('jStoreNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      const reportExport = await apiReport.exportReport(data);
      commit('showExportReportRes', reportExport);
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
    } catch (error) {
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
      dispatch('jStoreNotificationScreen/toggleProblem', error.response.data.message, { root: true });
    }
  },

  async exportBudget({ commit, dispatch }, data) {
    dispatch('jStoreNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      const budgetExport = await apiReport.exportBudget(data);
      commit('showExportBudgetRes', budgetExport);
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
    } catch (error) {
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
      dispatch('jStoreNotificationScreen/toggleProblem', error.response.data.message, { root: true });
    }
  },
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};



