/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapState, mapActions } from 'vuex';

export const jMixOptionsFormMappingLayer = {
  data() {
    return {
      operator_list: [
        {
          id: '!=',
          name: '!= (not same)',
        },
        {
          id: '=',
          name: '= (same)',
        },
      ],
      transfer_status: [
        {
          id: 2,
          name: 'Can not transfer',
        },
        {
          id: 3,
          name: 'Transfer without approver',
        },
        {
          id: 4,
          name: 'Transfer with approver',
        },
      ],
    };
  },
  computed: {
    ...mapState('jStoreMappingLayer', ['mapping_layer_state']),
    list_expense_type() {
      if (this.mapping_layer_state.expense_type_list !== null) {
        return this.mapping_layer_state.expense_type_list;
      }
      return [];
    },
    list_rule() {
      if (this.mapping_layer_state.rule_list !== null) {
        return this.mapping_layer_state.rule_list;
      }
      return [];
    },
  },
  methods: {
    ...mapActions('jStoreMappingLayer', ['getExpenseTypeList', 'getRuleList']),
    getListForm() {
      this.getExpenseTypeList();
    },
    getListOption() {
      const req = {
        pagination: {
          limit: 0,
          page: 1,
          column: '',
          ascending: false,
        },
      };
      this.getRuleList(req);
    },
    validationData(data) {
      const error = [];
      data.forEach((a) => {
        const same = data.filter(b => a.expense_type.id === b.expense_type.id);
        const errorText = `- Layer '${same[0].expense_type.name}' is mapped more than once`;
        const exist = error.filter(b => b === errorText);

        if (same.length > 1 && exist.length === 0) {
          error.push(errorText);
        }
      });
      return error;
    },
  },
  created() {
    this.getListForm();
    this.getListOption();
  },
};



