// import stores from '@/stores/modules/jStoreLogin';

export default function budgetCustody({ next, router }) {
  const role = JSON.parse(localStorage.getItem('role'));
  // const packageData = stores.state.package ? stores.state.package : [];
  const packageData = JSON.parse(localStorage.getItem('package'));

  if (!packageData.includes(10)) {
    if (process.env.NODE_ENV === 'production') {
      window.location = '/';
    }
  }

  if (role.includes(18) || role.includes(2) || role.includes(17)) {
    return next();
  } else if (role.includes(1)) {
    window.location = '/';
  }
  // return next();
  return router.push('/logout');
}



