/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapState, mapActions } from 'vuex';

export const jMixModelFilterReport = {
  computed: {
    ...mapState('jStoreReport', ['report_res', 'budget_res']),
    generateData() {
      if (this.export_data !== null) {
        const roles = JSON.parse(localStorage.getItem('role'));
        const genData = {
          period_ids: this.export_data.period_ids,
          division_ids: this.export_data.division_ids,
          root_category_ids: this.export_data.root_category_ids,
          costcenter_ids: this.export_data.costcenter_ids,
          project_ids: [],
          type: this.export_data.type,
          roles,
        };
        return genData;
      }
      return null;
    },
  },
  watch: {
    report_res() {
      this.show_button_report = true;
    },
    budget_res() {
      this.show_button_budget = true;
    },
  },
  methods: {
    ...mapActions('jStoreReport', ['exportReport', 'exportBudget']),

    sendDatas() {
      this.exportReport(this.generateData);
      this.exportBudget(this.generateData);
    },
    downloadReport() {
      const fileUrl = this.report_res.url;
      window.open(fileUrl, '_blank');
    },
    downloadBudgetReport() {
      const fileUrl = this.budget_res.url;
      window.open(fileUrl, '_blank');
    },
  },
};



