<template>
  <div
    id="login-html"
    class="login-html">
    <div class="login-box">
      <b-link
        href="https://jojonomic.com/"
        target="_blank">
        <b-img :src="logo_company"/>
      </b-link>
      <j-form-login/>
    </div>

  </div>
</template>

<script>
import jFormLogin from './form-login/jFormLogin';

export default {
  name: 'JLogin',
  components: {
    jFormLogin,
  },
  computed: {
    logo_company() {
      if (process.env.COMPANY === 'CIMB') {
        // eslint-disable-next-line
        return require('@/assets/logo/logo_cimb_smart_white.png');
      }
      // eslint-disable-next-line
      return require('@/assets/logo/jojonomic_text_logo.png');
    },
  },

};
</script>

<style lang="scss" scoped>

</style>



