/* eslint-disable no-shadow,consistent-return,no-param-reassign */
import apiPeriod from '@/services/jApiPeriod';
import router from '@/router';

const state = {
  // Set default state(s) here
  period_data: {
    detail_period: null,

    list_period: null,

    period_deleted: 0,
  },

};

const getters = {
  // Change value of state(s) with getter
};

const mutations = {
  showDetailPeriod(state, data) {
    state.period_data.detail_period = data.data;
  },

  // getDataPeriod() {
  //   router.push('/dashboard/config/edit');
  // },

  periodList() {
    router.push('/dashboard/config');
  },

  periodDeleted(state) {
    state.period_data.period_deleted += 1;
  },

  initPeriodDelete(state) {
    state.period_data.period_deleted = 0;
  },

  showListPeriod(state, data) {
    state.period_data.list_period = data.data;
  },

};

const actions = {
  async getPeriodList({ commit, dispatch }, data) {
    try {
      const listPeriod = await apiPeriod.getPeriodList(data);
      commit('showListPeriod', listPeriod);
    } catch (err) {
      dispatch('jStoreNotificationScreen/toggleProblem', err.response.data.message, { root: true });
    }
  },

  async getPeriodDetail({ commit, dispatch }, periodeId) {
    dispatch('jStoreNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      const detailPeriod = await apiPeriod.getPeriodDetail(periodeId);
      commit('showDetailPeriod', detailPeriod);
      dispatch('jStoreNotificationScreen/hideLoading', 'Please wait...', { root: true });
    } catch (err) {
      dispatch('jStoreNotificationScreen/toggleProblem', err.response.data.message, { root: true });
    }
  },

  async updatePeriodDetail({ dispatch, commit }, periodData) {
    dispatch('jStoreNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      await apiPeriod.updatePeriodDetail(periodData);
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
      dispatch('jStoreNotificationScreen/toggleSuccess', 'Period Edited', { root: true });
      setTimeout(() => {
        commit('periodList');
      }, 2000);
    } catch (err) {
      if (err.response.data.errors) {
        dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
        dispatch('jStoreNotificationScreen/toggleProblem', err.response.data.errors[0].message, { root: true });
      } else {
        dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
        dispatch('jStoreNotificationScreen/toggleProblem', err.response.data.message, { root: true });
      }
    }
  },

  async createPeriod({ commit, dispatch }, newPeriodData) {
    dispatch('jStoreNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      await apiPeriod.createPeriod(newPeriodData);
      dispatch('jStoreNotificationScreen/hideLoading', 'Please wait...', { root: true });
      dispatch('jStoreNotificationScreen/toggleSuccess', 'Period Created', { root: true });
      commit('periodList');
    } catch (err) {
      if (err.response.data.errors) {
        dispatch('jStoreNotificationScreen/hideLoading', 'Please wait...', { root: true });
        dispatch('jStoreNotificationScreen/toggleProblem', err.response.data.errors[0].message, { root: true });
      } else {
        dispatch('jStoreNotificationScreen/hideLoading', 'Please wait...', { root: true });
        dispatch('jStoreNotificationScreen/toggleProblem', err.response.data.message, { root: true });
      }
    }
  },

  async deletePeriod({ commit, dispatch }, periodeId) {
    dispatch('jStoreNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      await apiPeriod.deletePeriod(periodeId);
      dispatch('jStoreNotificationScreen/hideLoading', 'Please wait...', { root: true });
      dispatch('jStoreNotificationScreen/toggleSuccess', 'Period Deleted', { root: true });
      commit('periodDeleted');
      commit('periodList');
    } catch (err) {
      if (err.response.data.errors) {
        dispatch('jStoreNotificationScreen/hideLoading', 'Please wait...', { root: true });
        dispatch('jStoreNotificationScreen/toggleProblem', err.response.data.errors[0].message, { root: true });
      } else {
        dispatch('jStoreNotificationScreen/hideLoading', 'Please wait...', { root: true });
        dispatch('jStoreNotificationScreen/toggleProblem', err.response.data.message, { root: true });
      }
    }
  },

  async multiDeletePeriod({ commit, dispatch }, periodId) {
    try {
      await apiPeriod.deletePeriod(periodId);
      commit('periodDeleted');
    } catch (err) {
      if (err.response.data.errors) {
        dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
        dispatch('jStoreNotificationScreen/toggleProblem', err.response.data.errors[0].message, { root: true });
      } else {
        dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
        dispatch('jStoreNotificationScreen/toggleProblem', err.response.data.message, { root: true });
      }
    }
  },
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};



