<template>
  <div class="jCostCenter">
    <!-- <j-title-header :options="title"/> -->
    <div class="jcost-center-list">
      <b-row class="top-row">
        <div class="left-div">
          <b-row class="top-text">
            <b-col class="left-top-text">
              <h4>
                <i class="fa fa-sliders cost-center"/>
                COST CENTER
              </h4>
            </b-col>
            <b-col class="right-top-text">
              <p> Setting up your company structure </p>
            </b-col>
          </b-row>
          <hr>
        </div>
        <div class="right-div">
          <div class="notification">
            <i class="fa fa-bell-o" />
          </div>
        </div>
      </b-row>
      <p class="bold m__b--7">
        Build and Set Up Your Company Cost Center
      </p>

      <b-row>
        <b-col
          class="title"
          md="7">
          <p class="bold">
            MANAGE COST BASED ON PURPOSE/PROJECT
          </p>
        </b-col>
        <b-col md="5">
          <b-row class="btn-row">
            <div
              class="search-btn">
              <b-button
                class="btn-round grey"
                @click="$router.replace('/dashboard/cost-center/search')">
                <i class="fa fa-search bold" />
              </b-button>
            </div>
            <j-import-file
              :href-sample="require('@/assets/file/FORMAT_IMPORT_COSTCENTER.zip')"
              class="import-style"
              @import-data="importData"/>
            <div class="cc-btn">
              <b-button
                v-if="is_error"
                class="btn outline red"
                @click="$refs.editCost.show()">
                <i class="fa fa-close"/>
                Open Error Data
              </b-button>
              <b-button
                v-else-if="data_import && !is_error"
                class="btn green outline"
                @click="uploadData">
                Setting by CSV <i class="fa fa-angle-down"/>
              </b-button>
            </div>
            <div class="cc-btn">
              <b-button class="btn blue outline">
                <i class="fa fa-pie-chart"/>
                Save Version
              </b-button>
            </div>
          </b-row>
        </b-col>
      </b-row>
      <br>
    </div>

    <j-tabs :tabs="tabs">
      <div
        id="containertabs12"
        class="tab-content">
        <div
          v-for="i in tabs"
          :key="i.idTab"
          :id="i.idComp"
          :aria-labelledby="i.idTab"
          role="tabpanel"
          class="tab-pane" >
          <component :is="i.comp"/>
        </div>
      </div>
    </j-tabs>

    <!-- <jListCostCenter v-if="custodyCostCenter === 16"/>
    <jListCostCenterCustody v-else/>
    <br>
    <h2>Costcenter Project</h2>
    <jListCostCenterProject /> -->

    <router-view/>
    <j-progress
      ref="ImportFile"
      :data-kirim="data_import"
      :url-api="url"
      :headers="headers"
      @finish="reloadPage"/>

    <div>
      <b-modal
        ref="editCost"
        class="mod-form"
        centered>
        <h3 class="float-to-center c--red">
          Error Data
        </h3>
        <v-client-table
          v-if="error_data[0]"
          :data="error_data"
          :columns="columns"
          :options="options"
          class="jTable">

          <div
            slot="ERROR"
            slot-scope="props">
            <div
              v-for="(err, index) in props.row.ERROR"
              :key="index">
              {{ err }}
            </div>
          </div>

        </v-client-table>
        <download-excel
          :data="error_data"
          class="float-to-right m__t--30"
          name="Costcenter-data-error.xls">
          <b-button class="btn green">
            Export Error
          </b-button>
        </download-excel>
      </b-modal>
    </div>
  </div>
</template>

<script>
// import jCreateCostCenter from './create-cost-center/jCreateCostCenter';
// import apiCategory from '@/services/jApiCategory';
import { mapState } from 'vuex';
// import Vue from 'vue';
import jProgress from '@/components/progress-screen/jProgress';
import jPrivCostCenter from '@/privilage/views/cost-center/jPrivCostCenter';
import jMixCostCenter from '@/mixins/components/get-list-option-form/jMixCostCenter';
import jListCostCenter from './list-cost-center/jListCostCenter';
import jListCostCenterCustody from './list-cost-center/jListCostCenterCustody';
import jListCostCenterProject from './list-cost-center/jListCostCenterProject';
import jListCostCenterContainer from './list-cost-center/jListCostCenterContainer';
import jListCostCenterProjectContainer from './list-cost-center/jListCostCenterProjectContainer';

export default {
  components: {
    // jCreateCostCenter,
    jProgress,
    jListCostCenter,
    jListCostCenterCustody,
    jListCostCenterProject,
    jListCostCenterContainer,
    jListCostCenterProjectContainer,
  },
  mixins: [
    jMixCostCenter,
    jPrivCostCenter,
  ],
  data() {
    return {
      tabs: [
        {
          idTab: 'tab-1', title: 'COST CENTER', idComp: 'comp-1', comp: jListCostCenterContainer, isShow: true,
        },
        {
          idTab: 'tab-2', title: 'PROJECT MONITORING', idComp: 'comp-2', comp: jListCostCenterProjectContainer, isShow: true,
        },
      ],
      // eslint-disable-next-line
      // columns: ['COST_CENTER_ID', 'ROOT', 'NAME', 'DESCRIPTION', 'COST_CENTER_CODE', 'TEAM', 'EXPENSE_LAYER_ID', 'BUDGET_CUSTODY', 'BUDGET_AMOUNT', 'PERIOD_ID', 'PROJECT_BASED', 'UNBUDGET_OPTION', 'RULE', 'TAGGING', 'LOCK_BUDGET', 'ERROR'],
      columns: ['COST_CENTER_ID', 'ROOT', 'NAME', 'ERROR'],
      options: {
        headings: {
          name: 'NAME',
          description: 'DESCRIPTION',
          period: 'PERIOD',
          backdate_settlement: 'BACKDATE',
          blackout_date: 'BLACKOUT DATE PERIOD',
          // eslint-disable-next-line
        },
        filterable: false,
        perPage: 5,
        perPageValues: [5, 10, 20, 50, 100],
      },
      title: {
        icon: 'fas fa-sliders-h',
        main_title: 'COST CENTER',
        opt_title: 'Setting up your company structure',
      },
      data_import: null,
      data_budget: {
      },
      list_error: [],
      error_data: [],
      is_error: false,
      is_finish: false,
    };
  },
  computed: {
    ...mapState('jStoreCostCenter', ['costcenter_updated_res', 'costcenter_created']),
    url() {
      const getEnv = 'costcenter/create';
      return getEnv;
    },
    headers() {
      const token = localStorage.getItem('idTokenalt');
      const headers = {
        Authorization: `Bearer ${token}`,
      };
      return headers;
    },
  },
  watch: {
    costcenter_updated_res() {
      this.$router.go();
    },
    costcenter_created() {
      this.$router.go();
    },
  },
  methods: {
    importData(val) {
      const keys = Object.keys(val[0]);
      if (keys[0] !== 'COST_CENTER_ID' || keys[1] !== 'ROOT' || keys[2] !== 'NAME' || keys[3] !== 'DESCRIPTION'
        || keys[4] !== 'COST_CENTER_CODE' || keys[5] !== 'DIVISIONS' || keys[6] !== 'EXPENSE_LAYER_ID' || keys[7] !== 'BUDGET_CUSTODY'
        || keys[8] !== 'PROJECT_BASED' || keys[9] !== 'BUDGET_AMOUNT' || keys[keys.length - 2] !== 'TAGGING'
        || keys[keys.length - 1] !== 'LOCK_BUDGET') {
        this.$store.dispatch('jStoreNotificationScreen/toggleProblem', 'Please use header format correctly');
      }
      this.is_error = false;
      this.list_error = [];
      this.error_data = val;
      const fileData = val;
      const genData = [];
      fileData.forEach((e, index) => {
        const ccData = this.convertData(e, index, genData);
        if (ccData !== false) {
          genData.push({
            costcenter:
              ccData,
          });
        }
      });
      this.data_import = genData;
    },
    convertData(val, indexData, genData) {
      // eslint-disable-next-line
      let isHaveSameID = [];
      if (genData[0]) {
        // eslint-disable-next-line
        isHaveSameID = genData.filter(idCost => idCost.costcenter.id === val.COST_CENTER_ID);
      }
      if (isHaveSameID[0]) {
        this.list_error[indexData] = [];
        this.error_data[indexData].ERROR = [];
        // teams
        const splitDivisions = this.splitSemicolons(val.DIVISIONS.toLowerCase());

        if (Array.isArray(splitDivisions)) {
          const arrayDivisions = [];
          splitDivisions.forEach((e) => {
            // eslint-disable-next-line
            const filDivision = this.list_division.filter(fildat => fildat.name.toLowerCase() === e.trim());
            // eslint-disable-next-line
            const filDivisionCode = this.list_division.filter(fildat => fildat.code.toLowerCase() === e.trim());
            if (filDivision.length < 1 && filDivisionCode.length < 1) {
              this.is_error = true;
              this.list_error[indexData].push(`- ${e} division is not listed, please try another`);
              this.error_data[indexData].ERROR.push(`- ${e} division is not listed, please try another`);
            } else if (filDivision.length > 0) {
              filDivision[0].relation_id = 0;
              filDivision[0].is_delete = false;
              arrayDivisions.push(filDivision[0]);
            } else if (filDivisionCode.length > 0) {
              filDivisionCode[0].relation_id = 0;
              filDivisionCode[0].is_delete = false;
              arrayDivisions.push(filDivisionCode[0]);
            }
          });
          isHaveSameID[0].costcenter.divisions.push(...arrayDivisions);
          return false;
        }
      } else {
        this.list_error[indexData] = [];
        this.error_data[indexData].ERROR = [];
        const rootCondition = typeof val.ROOT === 'undefined' ? '0' : val.ROOT;
        const rootStringToZero = rootCondition === '' ? '0' : rootCondition;
        const packing = {
          id: val.COST_CENTER_ID,
          root_id: rootStringToZero,
          name: `${val.NAME}`.toLowerCase(),
          description: `${val.DESCRIPTION}`.toLowerCase(),
          code: `${val.COST_CENTER_CODE}`.toLowerCase(),
          type: 2,
          is_delete: false,
          relation_id: 0,
          is_used_transaction: false,
          teams: [],
          divisions: [],
          is_import: true,
          expense_category: null,
          budget_custody: null,
          budget: null,
          policy: {
            is_project_based: null,
            is_unbudgeted_option: null,
            rules: [],
            tags: [],
            is_lock: null,
          },
        };

        // teams
        const splitDivisions = this.splitSemicolons(val.DIVISIONS.toLowerCase());

        if (Array.isArray(splitDivisions)) {
          const arrayDivisions = [];
          splitDivisions.forEach((e) => {
            // eslint-disable-next-line
            const filDivision = this.list_division.filter(fildat => fildat.name.toLowerCase() === e.trim());
            // eslint-disable-next-line
            const filDivisionCode = this.list_division.filter(fildat => fildat.code.toLowerCase() === e.trim());
            if (filDivision.length < 1 && filDivisionCode.length < 1) {
              this.is_error = true;
              this.list_error[indexData].push(`- ${e} division is not listed, please try another`);
              this.error_data[indexData].ERROR.push(`- ${e} division is not listed, please try another`);
            } else if (filDivision.length > 0) {
              filDivision[0].relation_id = 0;
              filDivision[0].is_delete = false;
              arrayDivisions.push(filDivision[0]);
            } else if (filDivisionCode.length > 0) {
              filDivisionCode[0].relation_id = 0;
              filDivisionCode[0].is_delete = false;
              arrayDivisions.push(filDivisionCode[0]);
            }
          });
          this.$set(packing, 'divisions', arrayDivisions);
        }

        // budget & period
        // eslint-disable-next-line
        const periodData = val.PERIOD_ID ? val.PERIOD_ID.toLowerCase().trim() : '';
        // eslint-disable-next-line
        const filterPeriod = this.list_period.filter(fildat => fildat.name.toLowerCase() === periodData);
        // eslint-disable-next-line
        const projectName = typeof val.PROJECT_NAME !== 'undefined' ? val.PROJECT_NAME : '';
        // eslint-disable-next-line
        const filterProject = this.costcenter_data.list_project.filter(fildat => fildat.name === projectName.trim());
        const isProjectOrPeriod = val.PROJECT_BASED.toLowerCase() === 'y' ? filterProject : filterPeriod;
        if (isProjectOrPeriod.length < 1) {
          this.is_error = true;
          if (val.PROJECT_BASED.toLowerCase() === 'y') {
            this.list_error[indexData].push(`- Project of ${val.PROJECT_NAME} is not listed, please try another`);
            this.error_data[indexData].ERROR.push(`- Project of ${val.PROJECT_NAME} is not listed, please try another`);
          } else {
            this.list_error[indexData].push(`- Period of ${val.PERIOD_ID} is not listed, please try another`);
            this.error_data[indexData].ERROR.push(`- Period of ${val.PERIOD_ID} is not listed, please try another`);
          }
        } else {
          const budgetData = [{
            amount: parseFloat(val.BUDGET_AMOUNT),
            is_delete: false,
            id: 0,
            project: val.PROJECT_BASED.toLowerCase().trim() === 'y' ? isProjectOrPeriod[0] : {},
            is_project_based: val.PROJECT_BASED.toLowerCase().trim() === 'y',
            relation_id: 0,
            period: val.PROJECT_BASED.toLowerCase().trim() === 'y' ? {} : {
              id: isProjectOrPeriod[0].id,
            },
          }];
          this.$set(packing, 'budget', budgetData);
        }

        // expense category
        const splitExpenseCat = val.EXPENSE_LAYER_ID.replace(';', '&').split('&');

        splitExpenseCat.forEach((value, index) => {
          this.$set(splitExpenseCat, index, splitExpenseCat[index].replace('(', ''));
          this.$set(splitExpenseCat, index, splitExpenseCat[index].replace(')', ''));
        });

        if (splitExpenseCat.length > 1) {
          this.$set(splitExpenseCat, 1, splitExpenseCat[1].split(';'));
        }

        let expenseCatPar = null;
        const expenseCatChld = [];
        // const requestChild = {
        //   expense_category: {
        //     id: parseFloat(splitExpenseCat[0]),
        //   },
        // };
        // const listChildDet = await apiCategory.getActivityList(requestChild);
        splitExpenseCat.forEach((e, index) => {
          if (index === 0) {
            expenseCatPar = {
              id: parseFloat(e),
              package: 1,
            };
          } else {
            e.forEach((eachChild) => {
              // eslint-disable-next-line
              // const filterChildDet = listChildDet.data.filter(fildat => fildat.id === parseFloat(eachChild));
              expenseCatChld.push({ id: parseFloat(eachChild), name: `Category ID #${parseFloat(eachChild)}`, relation_id: 0, is_delete: false, is_have_child: false });
            });
          }
        });
        expenseCatPar.childs = expenseCatChld;

        this.$set(packing, 'expense_category', expenseCatPar);

        // budget custody
        // eslint-disable-next-line
        const filterBC = this.list_budget_custody.filter(filDat => filDat.name.toLowerCase().trim() === val.BUDGET_CUSTODY.toLowerCase().trim());
        if (filterBC.length < 1) {
          this.is_error = true;
          this.list_error[indexData].push(`- Budget custody of ${val.BUDGET_CUSTODY} is not listed, please try another`);
          this.error_data[indexData].ERROR.push(`- Budget custody of ${val.BUDGET_CUSTODY} is not listed, please try another`);
        } else {
          const BudgetCustody = [
            {
              id: filterBC[0].id,
              name: filterBC[0].name,
              is_delete: false,
              relation_id: 0,
            },
          ];
          this.$set(packing, 'budget_custody', BudgetCustody);
        }

        // is project based
        let projectBased = null;
        if (val.PROJECT_BASED.toLowerCase().trim() === 'y') {
          projectBased = true;
        } else {
          projectBased = false;
        }
        this.$set(packing.policy, 'is_project_based', projectBased);

        // unbudget option
        let unbudgetOption = null;
        if (val.UNBUDGET_OPTION.toLowerCase().trim() === 'y') {
          unbudgetOption = true;
        } else {
          unbudgetOption = false;
        }
        this.$set(packing.policy, 'is_unbudgeted_option', unbudgetOption);
        const valRule = typeof val.RULE !== 'undefined' ? val.RULE : '';
        // rule
        if (packing.policy.is_unbudgeted_option === true && valRule.length > 0) {
          // eslint-disable-next-line
          const filRules = this.list_rule.filter(a => a.name.toLowerCase().trim() === val.RULE.toLowerCase().trim());
          if (filRules.length < 1) {
            this.is_error = true;
            this.list_error[indexData].push(`- Rule of ${val.RULE} is not listed, please try another`);
            this.error_data[indexData].ERROR.push(`- Rule of ${val.RULE} is not listed, please try another`);
          } else {
            const rules = [
              {
                id: filRules[0].id,
                relation_id: 0,
                is_delete: false,
              },
            ];
            const arrRule = val.UNBUDGET_OPTION.toLowerCase().trim() === 'y' ? rules : [];
            this.$set(packing.policy, 'rules', arrRule);
          }
        }

        // is lock
        let isLock = null;
        if (val.LOCK_BUDGET.toLowerCase().trim() === 'y') {
          isLock = true;
        } else {
          isLock = false;
        }
        this.$set(packing.policy, 'is_lock', isLock);

        // tags
        let splitTags;
        if (typeof val.TAGGING !== 'undefined') {
          if (typeof val.TAGGING === 'string' && val.TAGGING.length > 0) {
            splitTags = this.splitSemicolons(val.TAGGING.toLowerCase().trim());
          } else {
            splitTags = [];
          }
        } else {
          splitTags = [];
        }

        if (Array.isArray(splitTags)) {
          const arrayTags = [];

          splitTags.forEach((e) => {
          // eslint-disable-next-line
          const filTag = this.list_tag.filter(fildat => fildat.name.toLowerCase().trim() === e.toLowerCase().trim());
            if (filTag.length < 1) {
              arrayTags.push({
                id: 0,
                name: e,
                relation_id: 0,
              });
            } else {
              arrayTags.push({
                id: filTag[0].id,
                name: filTag[0].name,
                relation_id: 0,
                is_delete: false,
              });
            }
          });
          this.$set(packing.policy, 'tags', arrayTags);
        }

        // validate Budget

        // create Each ID
        this.data_budget[packing.id] = {
          total: parseFloat(val.BUDGET_AMOUNT),
          used: 0,
        };
        if (packing.root_id !== '0' && packing.root_id !== undefined) {
          // eslint-disable-next-line
          const totalBudget = this.data_budget[packing.root_id].used + parseFloat(val.BUDGET_AMOUNT);
          if (totalBudget <= this.data_budget[packing.root_id].total) {
            this.data_budget[packing.root_id].used = totalBudget;
          } else {
            this.is_error = true;
            this.list_error[indexData].push('- this budget is greater than parent budget, please choose budget amount correctly');
            this.error_data[indexData].ERROR.push('- this budget is greater than parent budget, please choose budget amount correctly');
          }
        }

        return packing;
      }

      return false;
      // eslint-disable-next-line
      // if (!val.COST_CENTER_ID || !val.ROOT || !val.NAME || !val.DESCRIPTION || !val.COST_CENTER_CODE || !val.TEAM || !val.EXPENSE_LAYER_ID || !val.BUDGET_CUSTODY || !val.BUDGET_AMOUNT || !val.PERIOD_ID || !val.PROJECT_BASED || !val.UNBUDGET_OPTION || !val.RULE || !val.TAGGING || !val.LOCK_BUDGET) {
      // eslint-disable-next-line
      //   return this.$store.dispatch('jStoreNotificationScreen/toggleProblem', 'Please use format header correctly');
      // }
    },
    splitSemicolons(val) {
      let splitSemicolon = null;
      splitSemicolon = val.split(';');
      const removeSpace = splitSemicolon.map(el => el.trim());
      return removeSpace;
    },
    uploadData() {
      this.$refs.ImportFile.toggleIsVisible();
      // const refreshToken = {
      //   refresh_token: localStorage.getItem('refreshToken'),
      // };
      // this.$store.dispatch('jStoreLogin/refreshTokenApi', refreshToken);
      //
      // this.setIntervalToken();
    },
    // setIntervalToken() {
    //   setInterval(() => {
    //     if (!Vue.ls.get('idToken') && this.is_finish === false) {
    //       const refreshToken = {
    //         refresh_token: localStorage.getItem('refreshToken'),
    //       };
    //       this.$store.dispatch('jStoreLogin/refreshTokenApi', refreshToken);
    //     }
    //   }, 1000);
    // },

    reloadPage() {
      this.is_finish = true;
      this.$router.go();
    },
  },
};
</script>

<style lang="scss">

</style>



