<template>
  <div class="jContainer">

    <!-- header title -->
    <j-headful :title="$route.meta.title"/>

    <!-- header border -->
    <j-header/>

    <!-- router -->
    <router-view/>

    <!-- notification screen -->
    <j-loading-screen
      ref="loadingScreen"
      :message="loading_message"/>
    <j-info-screen
      ref="infoScreen"
      :message="info_message"/>
    <j-problem-screen
      ref="problemScreen"
      :message="problem_message"/>
    <j-success-screen
      ref="successScreen"
      :message="success_message"/>

  </div>
</template>

<script>
/* eslint-disable vue/name-property-casing */
import jMixNotificationScreen from '@/mixins/components/notification-screen/jMixNotificationScreen';
import Vue from 'vue';
import { mapState, mapActions } from 'vuex';

export default {
  name: 'jBlankCenter',
  mixins: [jMixNotificationScreen],
  data() {
    return {
      is_pop_up: true,
    };
  },
  computed: {
    ...mapState('jStoreLogin', ['is_refresh_token', 'login_failed']),
  },
  watch: {
    is_refresh_token() {
      this.is_pop_up = true;
    },

    login_failed() {
      this.is_pop_up = true;
    },
  },
  created() {
    setInterval(() => {
      if (!Vue.ls.get('Token')) {
        this.$router.replace('/logout');
        window.location = '/';
      }
    }, 1000);
    // cek refresh token
    // setInterval(() => {
    //   if (!Vue.ls.get('Token') && this.is_pop_up) {
    //     this.is_pop_up = false;
    //     const text = 'Do you want to refresh the session?';
    //
    //     this.$swal({
    //       title: 'Session Expired',
    //       text,
    //       type: 'question',
    //       showCancelButton: false,
    //       cancelButtonColor: '#d33',
    //       confirmButtonText: 'Login',
    //       html:
    //           '<input id="email-refresh-token" type="email" placeholder=
    // "Email Address" class="jFormGroup form-control m__b--5"> ' +
    //           '<input id="password-refresh-token" type="password"
    // placeholder="Password" class="jFormGroup form-control"> ',
    //     }).then((result) => {
    //       if (result.value) {
    //         const akunRefreshToken = {
    //           email: document.getElementById('email-refresh-token').value,
    //           password: document.getElementById('password-refresh-token').value,
    //         };
    //
    //         this.loginRefreshTokenApi(akunRefreshToken);
    //       } else {
    //         this.$router.replace('/');
    //       }
    //     });
    //   }
    // }, 3000);
  },
  beforeDestroy() {
    this.is_pop_up = false;
  },
  beforeUpdate() {
    this.$store.commit('jStoreLogin/setCurrencyFormat');
  },
  methods: {
    ...mapActions('jStoreLogin', ['loginRefreshTokenApi']),
  },
};
</script>

<style lang="scss">
  .jContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 40px 20px;
  }
</style>



