<template>
  <div>
    <j-title-header :options="title"/>
    <div class="w__percent--80">
      <b-card class="m__b--15">
        <div class="float-to-center">
          <h4>EXPORT REPORT</h4>
        </div>
        <j-report-form
          ref="report"
          @data="sendData"/>
        <div class="m__t--50 m__b--30">
          <b-button
            class="btn green"
            @click="getReport">
            Search
          </b-button> <br> <br>
          <b-button
            v-if="show_button_report"
            class="btn blue m__r--15"
            @click="downloadReport">EXPORT COST CENTER</b-button>
          <b-button
            v-if="show_button_budget"
            class="btn blue"
            @click="downloadBudgetReport">EXPORT BUDGET</b-button>
        </div>
      </b-card>
    </div>
  </div>
</template>

<script>
import jReportForm from '@/components/form/report/jReportForm';
import { jMixModelFilterReport } from '@/mixins/models/report/jMixModelFilterReport';

export default {
  components: {
    jReportForm,
  },
  mixins: [
    jMixModelFilterReport,
  ],
  data() {
    return {
      title: {
        icon: 'fa-user',
        main_title: 'REPORT',
      },
      export_data: null,
      show_button_report: false,
      show_button_budget: false,
    };
  },
  methods: {
    getReport() {
      this.$refs.report.save();
      if (this.export_data !== null) {
        this.sendDatas();
      }
    },
    sendData(data) {
      this.export_data = data;
    },
  },
};
</script>




