import jReport from '@/views/dashboard/report/jReport';
import auth from '@/middleware/auth';
import budgetCustody from '@/middleware/budget-custody';

export default [
  {
    path: '/dashboard/report',
    name: 'jReport',
    component: jReport,
    meta: {
      title: 'Export Report',
      middleware: [auth, budgetCustody],
      layout: 'jWrapper',
    },
  },
];



