<template>
  <j-budget-dynamic
    :data="budgetData"
    :status="budgetColor"
    :height="20"
    class="jBudgetCost">
    <div
      v-if="left_bottom"
      slot="left-bottom"
      class="left">
      <slot name="left-bottom"/>
    </div>
    <div slot="right-center">
      <slot name="right-center"/>
    </div>
    <div
      v-if="right_bottom"
      slot="right-bottom"
      class="right">
      <slot name="right-bottom"/>
    </div>
  </j-budget-dynamic>
</template>

<script>
export default {
  props: {
    amount: {
      type: Number,
      default: 0,
    },
    used: {
      type: Number,
      default: 0,
    },
    locked: {
      type: Number,
      default: 0,
    },
    accrue: {
      type: Number,
      default: 0,
    },
    substract: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {
      data: [],
      color: [],
    };
  },
  computed: {
    budgetData() {
      this.$set(this.data, '0', this.accrue);
      if (this.accrue + this.locked + this.used > this.amount) {
        let a;
        let b;
        if (this.used + this.accrue < this.amount) {
          a = this.used + this.accrue;
          b = this.amount - (this.used + this.accrue);
        } else {
          a = this.amount - this.accrue;
          b = 0;
        }
        this.$set(this.data, '1', a);
        this.$set(this.data, '2', b);
        if (this.substract !== 0) {
          this.$set(this.data, '3', (this.locked + b) + this.substract);
          this.$set(this.data, '4', this.amount);
        } else {
          this.$set(this.data, '3', 0);
          this.$set(this.data, '4', this.amount);
        }
      } else {
        this.$set(this.data, '1', this.accrue + this.used);
        this.$set(this.data, '2', this.accrue + this.locked + this.used);
        if (this.substract !== 0) {
          this.$set(this.data, '3', (this.accrue + this.locked + this.used) + this.substract);
          this.$set(this.data, '4', this.amount);
        } else {
          this.$set(this.data, '3', 0);
          this.$set(this.data, '4', this.amount);
        }
      }
      return this.data;
    },
    budgetColor() {
      const a = (40 / 100) * this.amount;
      const b = (60 / 100) * this.amount;
      this.$set(this.color, '0', 'grey');
      this.$set(this.color, '2', 'light-grey');
      if (this.used <= a) {
        this.$set(this.color, '1', 'green');
      } else if (this.used > a && this.used <= b) {
        this.$set(this.color, '1', 'orange');
      } else if (this.used > b) {
        this.$set(this.color, '1', 'red');
      }
      this.$set(this.color, '3', 'purple');
      this.$set(this.color, '4', 'purple');
      return this.color;
    },
    left_bottom() {
      return this.$slots['left-bottom'];
    },
    right_bottom() {
      return this.$slots['right-bottom'];
    },
  },
};
</script>



