/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { required, requiredIf } from 'vuelidate/lib/validators';

export const jMixValidationFormMappingLayer = {
  validations() {
    return {
      mapping_layer_data:
      {
        $each: {
          expense_type: {
            required,
          },
          case: {
            $each: {
              operator: {
                required,
              },
              status_transfer: {
                required,
              },
              flow: {
                requiredIf: requiredIf((e) => {
                  if (e.status_transfer !== undefined && e.status_transfer !== null) {
                    return e.status_transfer.id === 4;
                  }
                  return false;
                }),
              },
            },
          },
        },
      },
    };
  },
};



