<template>
  <div class="jcost-center-list">
    <div v-if="project_data.length > 0">
      <div
        v-for="(project, index) in project_data"
        :key="index">
        <jProjectDetail :project-data="project"/>
      </div>

      <div>
        <a
          v-if="project_data[project_data.length - 1].is_next === true"
          style="cursor:pointer;"
          @click="showMoreProject">
          Show more . . .
        </a>
        <a
          v-else
          style="cursor:pointer;">
          Refresh . . .
        </a>
      </div>
    </div>
    <j-loading-nested-table v-else/>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex';
import jBudgetCost from '@/components/budget-component/jBudgetComponent';
import jUnbudgetedCost from '@/components/budget-component/jUnbudgetedComponent';
import jLoadingNestedTable from '@/components/skeleton-loading/jLoadingNestedTable';
import jProjectDetail from './jListCostCenterProjectDetail';
// import jTransferBudget from '@/components/cost-center-form/jTransferBudget';

export default {
  name: 'JListCostCenterProject',
  components: {
    jBudgetCost,
    jLoadingNestedTable,
    jUnbudgetedCost,
    jProjectDetail,
    // jTransferBudget,
  },
  data() {
    return {
      project_data: [],
      send_data: {
        top_id: 0,
        bottom_id: 0,
        top_db_id: 0,
        bottom_db_id: 0,
        last_update: 0,
        count: 1,
      },
    };
  },
  computed: {
    ...mapState('jStoreCostCenter', ['costcenter_list', 'delete_response', 'detail_cost', 'costcenter_data']),
  },
  watch: {
    // eslint-disable-next-line
    'costcenter_data.list_costcenter_project': function(newVal) {
      this.project_data.push(...newVal);
      if (this.project_data.length > 0) {
        this.send_data.top_id = this.project_data[0].id;
        this.send_data.top_db_id = this.project_data[0].id;
        this.send_data.bottom_id = this.project_data[this.project_data.length - 1].id;
        this.send_data.bottom_db_id = this.project_data[this.project_data.length - 1].id;
      }
    },
  },
  created() {
    this.getCostCenterListProject(this.send_data);
  },
  methods: {
    ...mapActions('jStoreCostCenter', ['getCostCenterList', 'deleteCostCenter', 'getCostCenterListProject']),

    showMoreProject() {
      this.getCostCenterListProject(this.send_data);
    },

  },
};
</script>



