/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */

export const jMixOptionsFormTransferBudget = {
  created() {
    this.reusableFunction();
    if (this.preview === true) {
      this.modePreview();
    } else if (this.preview === false) {
      this.modeCreate();
    }
  },
  computed: {
    active_source() {
      if (this.preview) {
        if (this.transfer_budget.source_costcenter.length > 0) {
          return this.getActiveSource();
        }
      }
      return this.transfer_budget.source_costcenter.budget;
    },
    active_destination() {
      if (this.preview) {
        if (this.transfer_budget.destination_costcenter.length > 0) {
          this.getActiveDestination();
        }
      }
      return this.transfer_budget.destination_costcenter.budget;
    },
  },
  watch: {
    list_sources() {
      if (this.preview) {
        // this.getSourceCostCenterPreview();
      }
    },
    // eslint-disable-next-line func-names
    'transfer_budget.selected_source': function (val) {
      if (!this.preview) {
        this.getListSourcesCreate(val);
      }
    },
  },
};



