/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapActions, mapState } from 'vuex';

export const jMixModelDeletePeriods = {
  computed: {
    delete_data() {
      if (this.selectedRows.length > 0) {
        return this.selectedRows.map(data => ({
          period: {
            id: data.id,
          },
        }));
      }
      return null;
    },
  },

  methods: {
    deletePeriods() {
      if (this.delete_data !== null) {
        const text = `You want to delete ${this.periods_name}`;

        this.$swal({
          title: 'Are you sure?',
          text,
          type: 'question',
          showCancelButton: true,
          cancelButtonColor: '#d33',
          confirmButtonText: 'Yes',
        }).then((result) => {
          if (result.value) {
            this.$refs.deletePeriod.toggleIsVisible();
          }
        });
      }
    },
  },

};

export const jMixModelDeletePeriod = {
  computed: {
    ...mapState('jStoreConfig', ['period_data']),

    delete_data() {
      if (this.period_data.detail_period !== null) {
        return {
          period: {
            id: Number(this.$route.params.id),
          },
        };
      }
      return null;
    },
  },

  methods: {
    ...mapActions('jStoreConfig', ['deletePeriod']),

    deleteData(val) {
      if (!this.is_used_transaction_edit) {
        this.$swal({
          title: 'Are you sure?',
          text: 'to delete this period',
          type: 'warning',
          showCancelButton: true,
          cancelButtonColor: '#d33',
          confirmButtonText: 'Yes',
        }).then((result) => {
          if (result.value) {
            this.deletePeriod(this.delete_data);
          }
        });
      }
    },
  },

};



