/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapActions } from 'vuex';

export const jMixModelDetailTransferBudget = {
  methods: {
    ...mapActions('jStoreBudgetAllocation', ['updateActionTransfer', 'updateActionTransferReject']),
    approveAllocation() {
      const approverId = {
        status: 3,
        allocation_ids: [this.detail_transfer.id],
      };
      const text = 'to approve this budget allocation';

      this.$swal({
        title: 'Are you sure?',
        text,
        type: 'question',
        showCancelButton: true,
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes',
      }).then((result) => {
        if (result.value) {
          this.updateActionTransfer(approverId);
        }
      });
    },
    rejectAllocation() {
      const rejectionId = {
        status: 4,
        allocation_ids: [this.detail_transfer.id],
      };
      const text = 'to reject this budget allocation';

      this.$swal({
        title: 'Are you sure?',
        text,
        type: 'warning',
        showCancelButton: true,
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes',
      }).then((result) => {
        if (result.value) {
          this.updateActionTransferReject(rejectionId);
        }
      });
    },
  },
};



