/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import apiCostCenter from '@/services/jApiCostCenter';
import jLoadingTable from '@/components/skeleton-loading/jLoadingTable';
import { mapState, mapActions } from 'vuex';

export const jMixPaginationCostCenterCustody = {
  components: {
    jLoadingTable,
  },
  data() {
    return {
      filter_costcenter: '',
      columns: ['name', 'code', 'budget', 'remaining', 'divisions', 'SL', 'root_name'],
      load_first: false,
      length_table: '',
      env: 'costcenter/search/custody',
      options: {
        headings: {
          name: 'NAME',
          code: 'CODE',
          budget: 'BUDGET',
          remaining: 'REMAINING',
          divisions: 'DIVISIONS',
          SL: 'SL',
          root_name: 'PARENT NAME',
        },
        texts: {
          limit: 'Rows per page :',
        },
        debounce: 1000,
        filterable: true,
        perPage: 5,
        perPageValues: [5, 10, 20, 50, 100],
        requestAdapter(data) {
          return {
            pagination: {
              limit: data.limit,
              page: data.page,
              column: data.orderBy ? data.orderBy : '',
              ascending: !!data.ascending,
              query: data.query ? data.query : '',
            },
          };
        },
        requestFunction(data) {
          const getCostCenterList = apiCostCenter.searchCostCenterCustody(data);
          return getCostCenterList.catch((e) => {
            this.$store.dispatch('jStoreNotificationScreen/toggleProblem', e.response.data.errors[0].message).bind(this);
          });
        },
        responseAdapter({ data }) {
          return {
            data: data.data ? data.data : [],
            count: data.length,
          };
        },
        templates: {
          budget(h, row) {
            const filterProjectBased = row.budget.filter(fil => fil.is_project_based !== true);
            if (filterProjectBased.length > 0) {
              const filterBudg = filterProjectBased.filter(fil => fil.is_active === true);
              if (filterBudg.length > 0) {
                return filterBudg[0].amount;
              }
            }
            const budgetKosong = [{
              amount: 0,
              remaining: 0,
            }];
            return budgetKosong[0].amount;
          },
          remaining(h, row) {
            const filterProjectBased = row.budget.filter(fil => fil.is_project_based !== true);
            if (filterProjectBased.length > 0) {
              const filterBudg = filterProjectBased.filter(fil => fil.is_active === true);
              if (filterBudg.length > 0) {
                return filterBudg[0].remaining;
              }
            }
            const budgetKosong = [{
              amount: 0,
              remaining: 0,
            }];
            return budgetKosong[0].remaining;
          },
          SL(h, row) {
            const listName = [];
            function isHaveChild(data) {
              let slsl;
              if (data.is_have_child === true) {
                if (Array.isArray(data.child)) {
                  slsl = isHaveChild(data.child[0]);
                } else {
                  slsl = isHaveChild(data.child);
                }
              } else if (data.is_have_child === false) {
                slsl = data.name;
              } else {
                slsl = '';
              }
              listName.push(data.name);
              return slsl;
            }
            isHaveChild(row.expense_category);
            return listName[1];
          },
          divisions(h, row) {
            const div = [];
            row.divisions.forEach((a) => {
              div.push(a.name);
            });
            return div.join(', ');
          },
        },
      },
    };
  },
  computed: {
    ...mapState('jStoreCostCenter', ['delete_response']),
  },
  watch: {
    delete_response() {
      this.refreshTable();
    },
    filter_costcenter(val) {
      if (val === '') {
        this.$refs.costCenterTable.setFilter('');
      }
    },
  },
  methods: {
    ...mapActions('jStoreNotificationScreen', ['hideLoading', 'toggleProblem', 'showLoading']),
    rowClick(data) {
      this.$router.replace(`/dashboard/cost-center/search/edit/${data.row.id}`);
    },
    refreshTable() {
      this.$refs.costCenterTable.getData();
    },
    loading() {
      this.showLoading();
    },
    loaded() {
      if (!this.load_first) {
        this.load_first = !this.load_first;
      }
      this.hideLoading();
    },
    customFilter(val) {
      if (val !== '') {
        this.$refs.costCenterTable.setFilter(val);
      }
    },
  },
};



