/* eslint-disable no-shadow,consistent-return,no-param-reassign */
import apiMappingLayer from '@/services/jApiMappingLayer';

const state = {
  // Set default state(s) here
  mapping_layer_state: {
    expense_type_list: null,
    rule_list: null,
  },
  mapping_layer_detail: null,
  create_res: false,
  update_res: false,
};

const getters = {
  // Change value of state(s) with getter
};

const mutations = {
  showExpenseTypeList(state, data) {
    state.mapping_layer_state.expense_type_list = data.data;
  },
  showRuleList(state, data) {
    state.mapping_layer_state.rule_list = data.data;
  },
  showListMapping(state, data) {
    state.mapping_layer_detail = data.data;
  },
  updateRes(state) {
    state.update_res = !state.update_res;
  },
  createRes(state) {
    state.create_res = !state.update_res;
  },
};

const actions = {
  async getExpenseTypeList({ commit, dispatch }) {
    try {
      const res = await apiMappingLayer.getExpenseTypeList();
      commit('showExpenseTypeList', res);
    } catch (err) {
      dispatch('jStoreNotificationScreen/toggleProblem', err.response.data.errors[0].message, { root: true });
    }
  },

  async getRuleList({ commit, dispatch }, req) {
    dispatch('jStoreNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      const res = await apiMappingLayer.getRuleList(req);
      commit('showRuleList', res);
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
    } catch (err) {
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
      dispatch('jStoreNotificationScreen/toggleProblem', err.response.data.errors[0].message, { root: true });
    }
  },

  async createMapping({ commit, dispatch }, req) {
    dispatch('jStoreNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      await apiMappingLayer.createMapping(req);
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
      dispatch('jStoreNotificationScreen/toggleSuccess', 'Mapping Layer Created', { root: true });
      setTimeout(() => {
        commit('createRes');
      }, 2000);
    } catch (err) {
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
      dispatch('jStoreNotificationScreen/toggleProblem', err.response.data.errors[0].message, { root: true });
    }
  },

  async updateMapping({ commit, dispatch }, req) {
    dispatch('jStoreNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      await apiMappingLayer.updateMapping(req);
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
      dispatch('jStoreNotificationScreen/toggleSuccess', 'Mapping Layer Updated', { root: true });
      setTimeout(() => {
        commit('updateRes');
      }, 2000);
    } catch (err) {
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
      dispatch('jStoreNotificationScreen/toggleProblem', err.response.data.errors[0].message, { root: true });
    }
  },

  async getList({ commit, dispatch }, req) {
    dispatch('jStoreNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      const res = await apiMappingLayer.getList(req);
      commit('showListMapping', res);
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
    } catch (err) {
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
      dispatch('jStoreNotificationScreen/toggleProblem', err.response.data.errors[0].message, { root: true });
    }
  },
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};



