<template>
  <div>
    <b-form-group :class="{'error-input' : $v.export_report.project_type.$error}">
      <label>Project Type*</label>
      <j-single-select
        v-model="$v.export_report.project_type.$model"
        :options="project_types"
        placeholder="Select Project Name"
        label="name"
        @input="getPeriodByType"/>
    </b-form-group>
    <b-form-group :class="{'error-input' : $v.export_report.period_ids.$error}">
      <label>Period Name*</label>
      <j-multi-select
        v-model="$v.export_report.period_ids.$model"
        :options="list_period"
        placeholder="Select Period Name"
        label="name"/></b-form-group>
    <b-form-group :class="{'error-input' : $v.export_report.division_ids.$error}">
      <label>Division Name</label>
      <j-multi-select
        v-model="$v.export_report.division_ids.$model"
        :options="list_division"
        placeholder="Select Division Name"
        label="name"/>
    </b-form-group>
    <b-form-group :class="{'error-input' : $v.export_report.root_category_ids.$error}">
      <label>SL</label>
      <j-multi-select
        v-model="$v.export_report.root_category_ids.$model"
        :options="list_root_category"
        placeholder="Select SL"
        label="name"/>
    </b-form-group>
    <b-form-group :class="{'error-input' : $v.export_report.costcenter_ids.$error}">
      <label>Cost Center Code</label>
      <j-multi-select
        v-model="$v.export_report.costcenter_ids.$model"
        :is-async="true"
        :load-options="loadOptions"
        placeholder="Type Cost Center Code"
        label="code" />
    </b-form-group>
  </div>
</template>

<script>
import { jMixOptionsFormReport } from '@/mixins/list-options/report/jMixOptionsFormReport';
import { jMixModeFilterReport } from '@/mixins/mode-form/report/jMixModeFilterReport';
import { jMixValidationFormReport } from '@/mixins/validations/report/jMixValidationFormReport';

export default {
  mixins: [
    jMixOptionsFormReport,
    jMixValidationFormReport,
    jMixModeFilterReport,
  ],
  data() {
    return {
      mode: 'filter',
      export_report: {
        project_type: null,
        period_ids: [],
        division_ids: [],
        root_category_ids: [],
        costcenter_ids: [],
        type: 'xls',
      },
    };
  },
  methods: {
    save() {
      this.$v.export_report.$touch();
      if (!this.$v.export_report.$error) {
        this.$emit('data', this.export_report);
      } else {
        this.$emit('data', null);
      }
    },
  },
};
</script>



