/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapActions, mapState } from 'vuex';

export const jMixModelEditMappingLayer = {
  computed: {
    ...mapState('jStoreMappingLayer', ['update_res']),
    generateData() {
      if (this.mapping_layer_data.data_mapping !== null) {
        const genData = {
          layers: [],
        };
        this.mapping_layer_data.forEach((e, index) => {
          if (e.deleted_layer === undefined) {
            const data = {
              is_exist: e.is_exist ? e.is_exist : false,
              id: e.expense_type.id,
              name: e.expense_type.name,
              order: index,
              cases: [],
            };
            e.case.forEach((f) => {
              const dataCase = {
                id: f.id ? f.id : 0,
                relation_id: f.relation_id ? f.relation_id : 0,
                operation: f.operator.id,
                value: f.value ? f.value : 0,
                value_type: f.status_transfer.id,
                flows: [],
                rule: f.rule ? f.rule : 0,
                item_id: f.item_id ? f.item_id : 0,
                is_over_budget: f.is_over_budget ? f.is_over_budget : false,
                is_delete: f.is_delete ? f.is_delete : false,
              };
              let dataFlow;
              if (f.default_data !== undefined) {
                dataFlow = f.default_data.map(g => ({
                  id: g.id,
                  relation_id: g.relation_id,
                  name: g.name,
                  is_delete: g.is_delete,
                }),
                );
              } else {
                dataFlow = f.flow.map(g => ({
                  id: g.id,
                  relation_id: g.relation_id,
                  name: g.name,
                  is_delete: g.is_delete,
                }),
                );
              }
              this.$set(dataCase, 'flows', dataFlow);
              data.cases.push(dataCase);
            });

            if (e.deleted_case !== undefined) {
              e.deleted_case.forEach((f) => {
                const dataCase = {
                  id: f.id ? f.id : 0,
                  relation_id: f.relation_id ? f.relation_id : 0,
                  operation: f.operator.id,
                  value: f.value ? f.value : 0,
                  value_type: f.status_transfer.id,
                  flows: [],
                  rule: f.rule ? f.rule : 0,
                  item_id: f.item_id ? f.item_id : 0,
                  is_over_budget: f.is_over_budget ? f.is_over_budget : false,
                  is_delete: f.is_delete ? f.is_delete : false,
                };
                let dataFlow;
                if (f.default_data !== undefined) {
                  dataFlow = f.default_data.map(g => ({
                    id: g.id,
                    relation_id: g.relation_id,
                    name: g.name,
                    is_delete: g.is_delete,
                  }),
                  );
                } else {
                  dataFlow = f.flow.map(g => ({
                    id: g.id,
                    relation_id: g.relation_id,
                    name: g.name,
                    is_delete: g.is_delete,
                  }),
                  );
                }
                this.$set(dataCase, 'flows', dataFlow);
                data.cases.push(dataCase);
              });
            }
            genData.layers.push(data);
          } else {
            e.deleted_layer.forEach((f) => {
              const data = {
                is_exist: f.is_exist,
                id: f.expense_type.id,
                name: f.expense_type.name,
                order: -1,
                cases: [],
              };
              f.case.forEach((g) => {
                const dataCase = {
                  id: g.id ? g.id : 0,
                  relation_id: g.relation_id ? g.relation_id : 0,
                  operation: g.operator.id,
                  value: g.value ? g.value : 0,
                  value_type: g.status_transfer.id,
                  flows: [],
                  rule: g.rule ? g.rule : 0,
                  item_id: g.item_id ? g.item_id : 0,
                  is_over_budget: g.is_over_budget ? g.is_over_budget : false,
                  is_delete: true,
                };
                let dataFlow;
                // console.log(g, 'apa ini');
                if (g.default_data !== undefined) {
                  dataFlow = g.default_data.map(h => ({
                    id: h.id,
                    relation_id: h.relation_id,
                    name: h.name,
                    is_delete: h.is_delete,
                  }),
                  );
                } else {
                  dataFlow = g.flow.map(h => ({
                    id: h.id,
                    relation_id: h.relation_id,
                    name: h.name,
                    is_delete: h.is_delete,
                  }),
                  );
                }
                this.$set(dataCase, 'flows', dataFlow);
                if (dataCase.id !== 0) {
                  data.cases.push(dataCase);
                }
              });

              if (f.deleted_case !== undefined && f.deleted_case.length > 0) {
                f.deleted_case.forEach((g) => {
                  const dataCase = {
                    id: g.id ? g.id : 0,
                    relation_id: g.relation_id ? g.relation_id : 0,
                    operation: g.operator.id,
                    value: g.value ? g.value : 0,
                    value_type: g.status_transfer.id,
                    flows: [],
                    rule: g.rule ? g.rule : 0,
                    item_id: g.item_id ? g.item_id : 0,
                    is_over_budget: g.is_over_budget ? g.is_over_budget : false,
                    is_delete: true,
                  };
                  let dataFlow;
                  if (g.default_data !== undefined) {
                    dataFlow = g.default_data.map(h => ({
                      id: h.id,
                      relation_id: h.relation_id,
                      name: h.name,
                      is_delete: h.is_delete,
                    }),
                    );
                  } else {
                    dataFlow = g.flow.map(h => ({
                      id: h.id,
                      relation_id: h.relation_id,
                      name: h.name,
                      is_delete: h.is_delete,
                    }),
                    );
                  }
                  this.$set(dataCase, 'flows', dataFlow);
                  data.cases.push(dataCase);
                });
              }
              genData.layers.unshift(data);
            });
          }
        });
        return genData;
      }
      return null;
    },
  },
  watch: {
    update_res() {
      this.getListMapping();
    },
  },
  methods: {
    ...mapActions('jStoreMappingLayer', ['updateMapping', 'getList']),
    getListMapping() {
      const req = {
        pagination: {
          limit: 0,
          page: 1,
          column: 'order',
          ascending: true,
          query_type: '',
          query: '',
        },
      };
      this.getList(req);
    },

    sendData() {
      this.updateMapping(this.generateData);
    },
  },
};



