/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapActions, mapState } from 'vuex';
// eslint-disable-next-line

export const jMixReusableTransferBudget = {
  computed: {
    ...mapState('jStoreCostCenter', ['detail_cost', 'list_approvers']),
    amount_destination() {
      if (this.transfer_budget !== null) {
        if (typeof (this.transfer_budget.amount) !== 'number') {
          return this.active_destination[0].amount - this.active_destination[0].locked
          - this.active_destination[0].accrue;
        }
        const amnt = (this.active_destination[0].amount +
          this.transfer_budget.amount) -
          this.active_destination[0].locked - this.active_destination[0].accrue;
        return amnt;
      }
      return 0;
    },

    amount_source() {
      if (this.transfer_budget.amount !== null) {
        if (typeof (this.transfer_budget.amount) !== 'number') {
          return this.active_source[0].amount - this.active_source[0].locked
          - this.active_source[0].accrue;
        }
        return this.active_source[0].amount - this.transfer_budget.amount
        - this.active_source[0].locked - this.active_source[0].accrue;
      }
      return this.active_source[0].amount - this.active_source[0].locked
      - this.active_source[0].accrue;
    },
    remaining_source() {
      return this.amount_source - this.active_source[0].used;
    },
    remaining_destination() {
      return this.amount_destination - this.active_destination[0].used;
    },

    remaining_source_approved() {
      if (this.transfer_budget.amount !== null) {
        return this.transfer_budget.source_costcenter.budget[0].remaining;
      }
      return 0;
    },

    remaining_destination_approved() {
      if (this.transfer_budget.amount !== null) {
        return this.transfer_budget.destination_costcenter.budget[0].remaining;
      }
      return 0;
    },
  },
  watch: {
    detail_cost(val) {
      // filter budget
      const filterwithProject = val.budget.filter(data => data.is_project_based !== true);
      let budget = filterwithProject.filter(data => data.is_active === true);
      if (budget.length < 1) {
        budget = [
          {
            accrue: 0,
            amount: 0,
            locked: 0,
            used: 0,
            is_active: false,
          },
        ];
      }

      const packing = {
        id: val.id,
        code: val.code,
        budget,
        is_delete: val.is_delete ? val.is_delete : false,
        is_used_transaction: val.is_used_transaction ? val.is_used_transaction : false,
        name: val.name ? val.name : '',
        root_id: val.root_id ? val.root_id : 0,
      };

      this.transfer_budget.destination_costcenter = packing;
    },
  },
  methods: {
    ...mapActions('jStoreCostCenter', ['getApproversList', 'getListSources']),
    reusableFunction() {
      this.getApproversList();
      this.getCostCenterSource();
    },

    getCostCenterSource() {
      const params = this.$route.params.id ? this.$route.params.id : 0;
      const destinationId = {
        pagination: {
          limit: 0,
          page: 0,
          column: '',
          ascending: false,
          query: '',
        },
        costcenter: {
          destination_costcenter_id: Number(params),
        },
      };
      this.getListSources(destinationId);
    },
  },
};



