/* eslint-disable no-shadow,consistent-return,no-param-reassign */
import apiBudgetTransfer from '@/services/jApiBudgetCostCenter';
import router from '@/router';

const state = {
  total: [],
  detail_transfer: null,
  transfer_log: [],
};

const getters = {
  // Change value of state(s) with getter
};

const mutations = {
  showTotalApprover(state, data) {
    state.total = data.data;
  },
  showDetail(state, data) {
    state.detail_transfer = data.data;
  },
  showBudgetAllocationLog(state, data) {
    state.transfer_log = data.data;
  },
};

const actions = {
  async getTotalApprover({ commit, dispatch }) {
    dispatch('jStoreNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      const totalApprover = await apiBudgetTransfer.getTotalApprover();
      commit('showTotalApprover', totalApprover);
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
    } catch (err) {
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
      dispatch('jStoreNotificationScreen/toggleProblem', err.response.data.errors[0].message, { root: true });
    }
  },

  async getDetailTransfer({ commit, dispatch }, transferId) {
    dispatch('jStoreNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      const detailTransfer = await apiBudgetTransfer.getDetailTransfer(transferId);
      commit('showDetail', detailTransfer);
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
    } catch (err) {
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
      dispatch('jStoreNotificationScreen/toggleProblem', err.message, { root: true });
    }
  },

  async getBudgetAllocationLog({ commit, dispatch }, transferId) {
    dispatch('jStoreNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      const budgetAllocationLog = await apiBudgetTransfer.getBudgetAllocationLog(transferId);
      commit('showBudgetAllocationLog', budgetAllocationLog);
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
    } catch (err) {
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
      dispatch('jStoreNotificationScreen/toggleProblem', err.message, { root: true });
    }
  },

  async approveAction({ dispatch }, allocation) {
    dispatch('jStoreNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      await apiBudgetTransfer.approveAction(allocation);
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
      router.push('/dashboard/budget-transfer/list/approved');
      const actionApprove = { allocation_ids: [state.detail_transfer.id] };
      dispatch('updateActionTransfer', actionApprove);
      dispatch('jStoreNotificationScreen/toggleSuccess', 'Budget Allocation Approved', { root: true });
    } catch (err) {
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
      dispatch('jStoreNotificationScreen/toggleProblem', err.message, { root: true });
    }
  },

  async rejectionAction({ dispatch, state }, allocation) {
    dispatch('jStoreNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      await apiBudgetTransfer.rejectionAction(allocation);
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
      router.push('/dashboard/budget-transfer/list/rejected');
      const actionReject = { allocation_ids: [state.detail_transfer.id] };
      dispatch('updateActionTransfer', actionReject);
      dispatch('jStoreNotificationScreen/toggleSuccess', 'Budget Allocation Rejected', { root: true });
    } catch (err) {
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
      dispatch('jStoreNotificationScreen/toggleProblem', err.message, { root: true });
    }
  },

  async updateActionTransfer({ dispatch }, action) {
    dispatch('jStoreNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      const update = await apiBudgetTransfer.updateActionTransfer(action);
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
      dispatch('jStoreNotificationScreen/toggleSuccess', update.message, { root: true });
      router.push('/dashboard/budget-transfer/list/approved');
    } catch (err) {
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
      dispatch('jStoreNotificationScreen/toggleProblem', err.message, { root: true });
    }
  },

  async updateActionTransferReject({ dispatch }, action) {
    dispatch('jStoreNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      const update = await apiBudgetTransfer.updateActionTransfer(action);
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
      dispatch('jStoreNotificationScreen/toggleSuccess', update.message, { root: true });
      router.push('/dashboard/budget-transfer/list/rejected');
    } catch (err) {
      dispatch('jStoreNotificationScreen/hideLoading', '', { root: true });
      dispatch('jStoreNotificationScreen/toggleProblem', err.message, { root: true });
    }
  },
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};



