/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { required, numeric, maxLength, requiredIf } from 'vuelidate/lib/validators';

export const jMixValidationFormPeriod = {
  validations() {
    return {
      period_form: {
        name: {
          required,
          maxLength: maxLength(50),
        },
        description: {
          maxLength: maxLength(300),
        },
        start_date: {
          required,
        },
        end_date: {
          required,
        },
        backdate_settlement: {
          requiredIf: requiredIf(() => !this.period_form.project_based),
          numeric,
          maxLength: maxLength(3),
        },
        blackout_date: {
          start_date: {
            required: requiredIf(() => !this.period_form.project_based),
          },
          end_date: {
            required: requiredIf(() => !this.period_form.project_based),
          },
        },
      },
    };
  },

  computed: {
    error_minLength() {
      if (!this.$v.period_form.backdate_settlement.maxLength) {
        return 'maximum 3 characters';
      }
      return 'field is required';
    },
  },

};



