<template>
  <div>
    <draggable
      :list="mapping_layer_data"
      class="list-group"
      ghost-class="ghost"
    >
      <b-card
        v-for="(data, index) in $v.mapping_layer_data.$each.$iter"
        :key="index"
        class="list-group-item m__b--10 p--1"
      >
        <div
          v-if="mapping_layer_data.length > 1">
          <i
            style="cursor: pointer;"
            class="fa fa-times float-right"
            @click="deleteData(index)"/>
        </div>
        <br>
        <b-row>
          <b-col
            cols="3">
            <b-form-group :class="{'error-input' : data.expense_type.$error}">
              <label>Category Layer*</label>
              <j-single-select
                v-model="data.expense_type.$model"
                :disabled="mapping_layer_data[index].is_exist ?
                mapping_layer_data[index].is_exist : false"
                :options="list_expense_type"
                value-format="object"
                placeholder="Select Expense Type"
                label="name"/>
            </b-form-group>
          </b-col>
          <b-col cols="9">
            <b-row
              v-for="(dataCase, indexCase) in data.case.$each.$iter"
              :key="indexCase">
              <b-col>
                <b-form-group :class="{'error-input' : dataCase.operator.$error}">
                  <label>Operator*</label>
                  <j-single-select
                    v-model="dataCase.operator.$model"
                    :options="operator_list"
                    value-format="object"
                    placeholder="Select Operator"
                    label="name"/>
                </b-form-group>
              </b-col>
              <b-col>
                <b-form-group :class="{'error-input' : dataCase.status_transfer.$error}">
                  <label>Status Transfer* </label>
                  <j-single-select
                    v-model="dataCase.status_transfer.$model"
                    :options="transfer_status"
                    value-format="object"
                    placeholder="Select Status Transfer"
                    label="name"
                    @input="getStatus($event, index, indexCase)"/>
                </b-form-group>
              </b-col>
              <b-col
                v-show="mapping_layer_data[index].case[indexCase].status_transfer &&
                mapping_layer_data[index].case[indexCase].status_transfer.id === 4">
                <b-form-group :class="{'error-input' : dataCase.flow.$error}">
                  <label>Flows* </label>
                  <j-multi-select
                    v-model="dataCase.flow.$model"
                    :options="list_rule"
                    :default-data="default_rule[index] !== undefined ?
                    default_rule[index][indexCase] : []"
                    value-format="object"
                    placeholder="Select Flows"
                    label="name"
                    @getData="getDataRule($event, index, indexCase)"/>
                </b-form-group>
              </b-col>
              <div
                v-if="mapping_layer_data[index].case.length > 1"
                class="m__t--35">
                <b-button
                  class="float-right btn red outline btn-round-21 m__r--10"
                  @click="deleteCase(index, indexCase)">
                  <i
                    class="fa fa-times"/>
                </b-button>
              </div>
            </b-row>
            <label
              v-show="mapping_layer_data[index].case.length < operator_list.length"
              class="bold c--blue float-right"
              style="cursor: pointer;"
              @click="addCase(index)">
              + add case
            </label>
          </b-col>
        </b-row>
      </b-card>
    </draggable>
    <label
      class="bold c--blue"
      style="cursor: pointer;"
      @click="addMappingLayer">
      + add mapping layer
    </label>
    <br>
  </div>
</template>

<script>
import { mapActions } from 'vuex';
import { jMixOptionsFormMappingLayer } from '@/mixins/list-options/mapping-layer/jMixOptionsFormMappingLayer';
import { jMixModeEditMappingLayer } from '@/mixins/mode-form/mapping-layer/jMixModeEditMappingLayer';
import { jMixValidationFormMappingLayer } from '@/mixins/validations/mapping-layer/jMixValidationFormMappingLayer';
import draggable from 'vuedraggable';

export default {
  components: {
    draggable,
  },
  mixins: [
    jMixOptionsFormMappingLayer,
    jMixModeEditMappingLayer,
    jMixValidationFormMappingLayer,
  ],
  props: {
    mode: {
      type: String,
      default: 'create',
    },
  },
  data() {
    return {
      mapping_layer_data: [
        {
          expense_type: null,
          case: [
            {
              status_transfer: null,
              operator: null,
              flow: [],
            },
          ],
        },
      ],
      default_rule: [],
      rule_data: [],
      deleted_layer: [],
      halo: true,
    };
  },
  methods: {
    ...mapActions('jStoreNotificationScreen', ['toggleProblem', 'showLoading', 'hideLoading']),
    save() {
      this.$v.mapping_layer_data.$touch();
      if (!this.$v.mapping_layer_data.$error) {
        const error = this.validationData(this.mapping_layer_data);
        if (this.mode === 'create') {
          if (error.length > 0) {
            this.$store.dispatch('jStoreNotificationScreen/toggleProblem', error);
            this.$emit('data', null);
          } else {
            this.$emit('data', this.mapping_layer_data);
          }
        } else if (this.mode === 'edit') {
          if (error.length > 0) {
            this.$store.dispatch('jStoreNotificationScreen/toggleProblem', error);
            this.$emit('data', null);
          } else {
            const dataMapping = [...this.mapping_layer_data];
            if (this.deleted_layer.length > 0) {
              dataMapping.push({
                deleted_layer: this.deleted_layer,
              });
            }
            this.$emit('data', dataMapping);
            this.deleted_layer = [];
          }
        }
      } else {
        this.$emit('data', null);
      }
    },
    getDataRule(val, index, indexCase) {
      if (this.mode === 'create') {
        this.$set(this.mapping_layer_data[index].case[indexCase], 'flow', val);
      } else {
        this.$set(this.mapping_layer_data[index].case[indexCase], 'default_data', val);
      }
    },
    getStatus(val, index, indexCase) {
      if (val !== undefined && val.id !== 4) {
        if (this.mode === 'create') {
          this.$set(this.mapping_layer_data[index].case[indexCase], 'flow', []);
        } else {
          this.$set(this.mapping_layer_data[index].case[indexCase], 'flow', []);
          this.$set(this.mapping_layer_data[index].case[indexCase], 'default_data', []);
        }
      }
    },
    addMappingLayer() {
      this.mapping_layer_data.push({
        expense_type: null,
        case: [
          {
            status_transfer: null,
            operator: null,
            flow: [],
          },
        ],
      });
      this.$emit('length', this.mapping_layer_data.length);
    },

    addCase(index) {
      this.mapping_layer_data[index].case.push({
        status_transfer: null,
        operator: null,
        flow: [],
      });
    },

    deleteCase(index, indexCase) {
      if (this.mode === 'create') {
        this.mapping_layer_data[index].case.splice(indexCase, 1);
      } else if (this.mode === 'edit') {
        if (this.mapping_layer_data[index].is_delete !== undefined) {
          this.$set(this.mapping_layer_data[index].case[indexCase], 'is_delete', true);

          if (this.mapping_layer_data[index].deleted_case === undefined) {
            this.$set(this.mapping_layer_data[index], 'deleted_case', []);
          }
          // eslint-disable-next-line
          this.mapping_layer_data[index].deleted_case.push(this.mapping_layer_data[index].case[indexCase]);
          this.mapping_layer_data[index].case.splice(indexCase, 1);
          this.default_rule[index].splice(indexCase, 1);
        } else {
          this.mapping_layer_data[index].case.splice(indexCase, 1);
        }
      }
    },

    deleteData(index) {
      if (this.mode === 'create') {
        this.mapping_layer_data.splice(index, 1);
      } else if (this.mode === 'edit') {
        if (this.mapping_layer_data[index].is_delete !== undefined) {
          this.$set(this.mapping_layer_data[index], 'is_delete', true);
          this.deleted_layer.push(this.mapping_layer_data[index]);
          this.mapping_layer_data.splice(index, 1);
          this.default_rule.splice(index, 1);
        } else {
          this.mapping_layer_data.splice(index, 1);
        }
      }
      this.$emit('length', this.mapping_layer_data.length);
    },
  },
};
</script>
<style scoped>
.buttons {
  margin-top: 35px;
}
.ghost {
  opacity: 0.5;
  background: #c8ebfb;
}
</style>



