// eslint-disable-next-line import/no-mutable-exports
let paramsRoute;
const jMixReusableFunction = {
  created() {
    paramsRoute = Number(this.$route.params.id);
    if (this.$route.params.idTransfer) {
      paramsRoute = Number(this.$route.params.idTransfer);
    }
  },
  methods: {
    loading() {
      this.showLoading();
    },
    loaded() {
      if (!this.load_first) {
        this.load_first = !this.load_first;
      }
      this.hideLoading();
    },
  },
};
export { jMixReusableFunction, paramsRoute };



