/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapActions, mapState } from 'vuex';

export const jMixModelCreateTransferBudget = {
  computed: {
    ...mapState('jStoreCostCenter', ['create_transbudget_res']),
    collect_data_transfer() {
      const getDat = this.generateDataTransfer();
      return getDat;
    },
  },
  watch: {
    create_transbudget_res() {
      this.transfer_budget.amount = 0;
      this.transfer_budget.note = null;
      this.transfer_budget.approver = [];
      this.transfer_budget.selected_source = null;
      this.label_source_name = null;
      setTimeout(() => {
        this.$v.$reset();
      }, 100);
    },
  },
  methods: {
    ...mapActions('jStoreCostCenter', ['createBudgetTransfer']),

    generateDataTransfer() {
      const budgetAllocation = {
        budget_allocation: {
          id: 0,
          amount: this.transfer_budget.amount,
          source_costcenter: {
            id: this.transfer_budget.source_costcenter.id,
            name: this.transfer_budget.source_costcenter.name,
          },
          destination_costcenter: {
            id: this.transfer_budget.destination_costcenter.id,
            name: this.transfer_budget.destination_costcenter.name,
          },
          source_budget_id: this.transfer_budget.source_costcenter.budget[0].id,
          destination_budget_id: this.transfer_budget.destination_costcenter.budget[0].id,
          note: this.transfer_budget.note,
          status: 2,
          approver: {
            organigrams: [],
          },
        },
      };

        // generate organigrams
      const organigrams = this.transfer_budget.approver.map((data) => {
        const genDat = {
          id: data,
        };
        return genDat;
      });

      budgetAllocation.budget_allocation.approver.organigrams = organigrams;
      return budgetAllocation;
    },
    save() {
      this.$v.transfer_budget.$touch();
      if (!this.$v.transfer_budget.$error) {
        const text = `to transfer budget from "${this.transfer_budget.source_costcenter.name}" to 
        "${this.transfer_budget.destination_costcenter.name}"`;

        this.$swal({
          title: 'Are you sure?',
          text,
          type: 'question',
          showCancelButton: true,
          cancelButtonColor: '#d33',
          confirmButtonText: 'Yes',
        }).then((result) => {
          if (result.value) {
            this.createBudgetTransfer(this.collect_data_transfer);
          }
        });
      }
    },
  },
};



