import jApi from './jApi';

const req = {
  pagination: {
    limit: 0,
    page: 0,
    column: '',
    ascending: false,
    query: '',
    query_type: '',
  },
};

export default {
  getDivisionListByCostcenter(rootIdCostCenter) {
    const api = jApi.generateApi();
    return api.post('costcenter/division', rootIdCostCenter)
      .then(res => res.data);
  },

  getDivisionList() {
    const api = jApi.generateApi();
    return api.post('jojoflow/setup/division/list', req)
      .then(res => res.data);
  },
};



