const jPrivCostCenter = {
  computed: {
    user_role() {
      const role = localStorage.getItem('role');
      if (role.includes(2)) {
        return 'SysAdmin';
      } else if (role.includes(17)) {
        return 'admin_budget';
      } else if (role.includes(18)) {
        return 'budget_custody';
      }
      return null;
    },
  },
};

export default jPrivCostCenter;



