/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapActions, mapState } from 'vuex';

export const jMixModelCreateMappingLayer = {
  computed: {
    ...mapState('jStoreMappingLayer', ['created_res']),
    generateData() {
      if (this.mapping_layer_data !== null) {
        const genData = {
          layers: [],
        };
        this.mapping_layer_data.forEach((e, index) => {
          const data = {
            is_exist: false,
            id: e.expense_type.id,
            name: e.expense_type.name,
            order: index,
            cases: [],
          };
          e.case.forEach((f) => {
            const dataCase = {
              id: 0,
              relation_id: 0,
              operation: f.operator.id,
              value: 0,
              value_type: f.status_transfer.id,
              flows: [],
              rule: 0,
              item_id: 0,
              is_over_budget: false,
              is_delete: false,
            };
            const dataFlow = f.flow.map(g => ({
              id: g.id,
              relation_id: g.relation_id,
              name: g.name,
              is_delete: g.is_delete,
            }),
            );

            this.$set(dataCase, 'flows', dataFlow);

            data.cases.push(dataCase);
          });
          genData.layers.push(data);
        });
        return genData;
      }
      return null;
    },
  },
  watch: {
    created_res() {
      this.getListMapping();
    },
  },
  methods: {
    ...mapActions('jStoreMappingLayer', ['createMapping', 'getList']),
    getListMapping() {
      const req = {
        pagination: {
          limit: 0,
          page: 1,
          column: 'order',
          ascending: true,
          query_type: '',
          query: '',
        },
      };
      this.getList(req);
    },

    sendData() {
      this.createMapping(this.generateData);
    },
  },
};



