<template>
  <div class="custom-search-table">
    <v-server-table
      v-show="load_first"
      ref="periodServer"
      :columns="columns"
      :options="options"
      class="jTable"
      @loading="loading"
      @loaded="loaded">

      <div
        slot="beforeFilter"
        class="custom-search">
        <b-row>
          <b-col cols="3">
            <j-select
              v-model="select_type"
              :disabled="false"
              :options="options_search"/>
          </b-col>
          <b-col cols="5">
            <j-input
              v-model="filter_period"
              @keyup.13.native="customFilter(filter_period)"/>
          </b-col>
          <b-col
            cols="4"
            class="p__l--25">
            <b-button
              class="btn blue outline"
              @click="customFilter(filter_period)">
            <i class="fa fa-search bold" /> Search </b-button>
          </b-col>
        </b-row>
      </div>

      <div
        slot="checkbox"
        slot-scope="props"
        class="jTableReport">
        <b-form-group>
          <b-form-checkbox-group v-model="selected_rows">
            <b-form-checkbox :value="props.row"/>
          </b-form-checkbox-group>
        </b-form-group>
      </div>

      <div
        slot="name"
        slot-scope="props"
        @click="$router.replace(`/dashboard/config/edit/${props.row.id}`)">
        {{ props.row.name }}
      </div>

      <div
        slot="description"
        slot-scope="props"
        @click="$router.replace(`/dashboard/config/edit/${props.row.id}`)">
        {{ props.row.description }}
      </div>

      <div
        slot="period"
        slot-scope="props"
        @click="$router.replace(`/dashboard/config/edit/${props.row.id}`)">
        {{ props.row.start_date | moment("DD MMM, YYYY") }} -
        {{ props.row.end_date | moment("DD MMM, YYYY") }}
      </div>

      <div
        slot="backdate_settlement"
        slot-scope="props"
        @click="$router.replace(`/dashboard/config/edit/${props.row.id}`)">
        {{ props.row.backdate_settlement }} days
      </div>

      <div
        slot="blackout_date"
        slot-scope="props"
        @click="$router.replace(`/dashboard/config/edit/${props.row.id}`)">
        {{ props.row.blackout_date.start_date | moment("DD MMM, YYYY") }} -
        {{ props.row.blackout_date.end_date | moment("DD MMM, YYYY") }}
      </div>

    </v-server-table>

    <div>
      <jLoadingTable v-if="!load_first"/>
    </div>

    <jDeletePeriod
      ref="floating"
      :selected-rows="selected_rows"
      @finish="periodsDeleted" />
  </div>
</template>

<script>
// import { mapActions, mapState, mapMutations } from 'vuex';
import jLoadingTable from '@/components/skeleton-loading/jLoadingTable';
import { jMixPaginationPeriod } from '@/mixins/paginations/config/jMixPaginationPeriod';
import jDeletePeriod from '@/views/dashboard/config/delete-period/jDeletePeriod';

export default {
  components: {
    jDeletePeriod,
    jLoadingTable,
  },
  mixins: [
    jMixPaginationPeriod,
  ],
};
</script>



