/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapActions } from 'vuex';
import apiBudgetTransaction from '@/services/jApiBudgetCostCenter';
import { paramsRoute } from '@/mixins/paginations/transfer-budget/jMixReusableFunction';

export const jMixPaginationTransaction = {
  data() {
    return {
      mutation_columns: ['date', 'transaction_type', 'user', 'amount'],
      load_first: false,
      length_table: '',
      env: 'costcenter/mutation/list',
      mutation_options: {
        headings: {
          date: 'DATE',
          transaction_type: 'TRANSACTION TYPE',
          user: 'USER',
          amount: 'AMOUNT',
        },
        texts: {
          filterPlaceholder: 'Search Budget Transaction',
        },
        debounce: 2000,
        filterable: false,
        perPage: 5,
        perPageValues: [5, 10, 20, 50, 100],
        requestAdapter(data) {
          return {
            pagination: {
              limit: data.limit,
              page: data.page,
              column: data.orderBy ? data.orderBy : '',
              ascending: !!data.ascending,
              query: data.query ? data.query : '',
            },
            budget_id: 0,
            costcenter_id: paramsRoute,
            type_by: 'transaction',
          };
        },
        requestFunction(data) {
          const getTransactionList = apiBudgetTransaction.getBudgetTransactionList(data);
          return getTransactionList.catch((e) => {
            this.$store.dispatch('jStoreNotificationScreen/toggleProblem', e.response.data.errors[0].message).bind(this);
          });
        },
        responseAdapter({ data }) {
          return {
            data: data.data ? data.data : [],
            count: data.length ? data.length : 0,
          };
        },
      },
    };
  },
  methods: {
    ...mapActions('jStoreNotificationScreen', ['hideLoading', 'toggleProblem', 'showLoading']),
  },
};



