import jCostCenter from '@/views/dashboard/cost-center/jCostCenter';
import jCreateCostCenter from '@/views/dashboard/cost-center/create-cost-center/jCreateCostCenter';
import jEditCostCenter from '@/views/dashboard/cost-center/edit-cost-center/jEditCostCenter';
import jTransferBudgetList from '@/views/dashboard/approval/list-transfer-budget/jTransferBudgetList';
import jTransferBudgetSent from '@/views/dashboard/approval/list-transfer-budget/jSentList';
import jTransferBudgetApproved from '@/views/dashboard/approval/list-transfer-budget/jApprovedList';
import jTransferBudgetRejected from '@/views/dashboard/approval/list-transfer-budget/jRejectedList';
import jDetailTransferBuget from '@/views/dashboard/approval/detail-transfer-budget/jDetailTransfer';
import jSearchCostCenter from '@/views/dashboard/cost-center/search-cost-center/jSearchCostCenterContainer';
import auth from '@/middleware/auth';
import budgetCustody from '@/middleware/budget-custody';
import adminBudget from '@/middleware/admin-budget';

export default [
  {
    path: '/dashboard/cost-center',
    name: 'jCostCenter',
    component: jCostCenter,
    meta: {
      title: 'Cost Center',
      middleware: [auth, budgetCustody],
      layout: 'jWrapper',
    },
    children: [
      {
        path: '/dashboard/cost-center/create/:root',
        name: 'jCreateCostCenter',
        component: jCreateCostCenter,
        meta: {
          title: 'Create Cost Center',
          middleware: [auth, budgetCustody],
        },
      },
      {
        path: '/dashboard/cost-center/edit/:id',
        name: 'jEditCostCenter',
        component: jEditCostCenter,
        meta: {
          title: 'Edit Cost Center',
          middleware: [auth, budgetCustody],
        },
      },
    ],
  },
  {
    path: '/dashboard/cost-center/search',
    name: 'jSearchCostCenter',
    component: jSearchCostCenter,
    meta: {
      title: 'Search Cost Center',
      middleware: [auth, budgetCustody],
      layout: 'jWrapper',
    },
    children: [
      {
        path: '/dashboard/cost-center/search/edit/:id',
        name: 'jSearchEditCostCenter',
        component: jEditCostCenter,
        meta: {
          title: 'Edit Cost Center',
          middleware: [auth, budgetCustody],
        },
      },
      {
        path: '/dashboard/cost-center/search/create/:root',
        name: 'jCreateCostCenterSearch',
        component: jCreateCostCenter,
        meta: {
          title: 'Create Cost Center',
          middleware: [auth, adminBudget],
        },
      },
    ],
  },
  {
    path: '/dashboard/budget-transfer/list',
    name: 'jTransferBudgetList',
    component: jTransferBudgetList,
    redirect: '/dashboard/budget-transfer/list/sent',
    meta: {
      title: 'Transfer Budget List',
      middleware: [auth, budgetCustody],
      layout: 'jWrapper',
    },
    children: [
      {
        path: '/dashboard/budget-transfer/list/sent',
        name: 'jTransferBudgetSent',
        component: jTransferBudgetSent,
        meta: {
          title: 'Sent Transfer Budget List',
          middleware: [auth, budgetCustody],
        },
      },
      {
        path: '/dashboard/budget-transfer/list/approved',
        name: 'jTransferBudgetApproved',
        component: jTransferBudgetApproved,
        meta: {
          title: 'Approved Transfer Budget List',
          middleware: [auth, budgetCustody],
        },
      },
      {
        path: '/dashboard/budget-transfer/list/rejected',
        name: 'jTransferBudgetRejected',
        component: jTransferBudgetRejected,
        meta: {
          title: 'Rejected Transfer Budget List',
          middleware: [auth, budgetCustody],
        },
      },
    ],
  },
  {
    path: '/dashboard/budget-transfer/detail/:idAproverStatus/:idTransfer',
    name: 'jDetailTransferBuget',
    component: jDetailTransferBuget,
    meta: {
      title: 'Cost Center',
      middleware: [auth, budgetCustody],
      layout: 'jWrapper',
    },
  },
];



