<template>
  <div>
    <j-title-header :options="title"/>
    <div class="create-period float-to-right">
      <b-button
        class="btn blue"
        @click="$router.replace('/dashboard/config/create')">
        CREATE PERIOD
      </b-button>
    </div>
    <j-period-list/>
  </div>
</template>

<script>
import jPeriodList from './table-period/jPeriodList';

export default {
  components: {
    jPeriodList,
  },
  data() {
    return {
      title: {
        icon: 'fa-user',
        main_title: 'CONFIG',
        opt_title: 'Administrator',
      },
    };
  },
};
</script>



