import jApi from './jApi';

export default {
  getExpenseTypeList() {
    const api = jApi.generateApi();
    return api.get('expense-setup/expense-layer/setup/list')
      .then(res => res.data);
  },

  getRuleList(req) {
    const api = jApi.generateApi();
    return api.post('jojoflow/flow/list', req)
      .then(res => res.data);
  },

  createMapping(req) {
    const api = jApi.generateApi();
    return api.post('case/layer/create', req)
      .then(res => res.data);
  },

  updateMapping(req) {
    const api = jApi.generateApi();
    return api.post('case/layer/update', req)
      .then(res => res.data);
  },

  getList(req) {
    const api = jApi.generateApi();
    return api.post('case/layer/list', req)
      .then(res => res.data);
  },
};



