<template>
  <div class="custom-search-table jcost-center-list">
    <b-row>

      <b-col>
        <h4>
          <i class="fa fa-sliders cost-center"/>
          COST CENTER
        </h4>
        <hr>
      </b-col>
    </b-row>
    <p class="bold m__b--7">
      Search Cost Center
    </p>
    <v-server-table
      v-show="load_first"
      ref="costCenterTable"
      :url="env"
      :columns="columns"
      :options="options"
      class="jTable"
      @loading="loading"
      @loaded="loaded"
      @row-click="rowClick">
      <div
        slot="beforeFilter"
        class="custom-search">
        <b-row>
          <b-col cols="7">
            <j-input
              v-model="filter_costcenter"
              @keyup.13.native="customFilter(filter_costcenter)"/>
          </b-col>
          <b-col
            class="p__l--10"
            cols="5">
            <b-button
              class="btn blue outline"
              @click="customFilter(filter_costcenter)">
            <i class="fa fa-search bold" /> Search </b-button>
          </b-col>
        </b-row>
      </div>
      <div
        slot="name"
        slot-scope="props">
        {{ props.row.name }}
      </div>
      <div
        slot="code"
        slot-scope="props">
        {{ props.row.code }}
      </div>
    </v-server-table>
    <jLoadingTable v-if="!load_first"/>
    <router-view/>
  </div>
</template>
<script>
import { jMixPaginationCostCenterAdmin } from '@/mixins/paginations/cost-center/jMixPaginationCostCenterAdmin';

export default {
  mixins: [
    jMixPaginationCostCenterAdmin,
  ],
};
</script>



