/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapActions, mapState } from 'vuex';


export const jMixOptionsFormLayerCategory = {
  data() {
    return {
      category_hover_tooltip: [
        {
          is_show: false,
          label: null,
        },
      ],
      count: 0,
    };
  },
  computed: {
    ...mapState('jStoreCostCenter', ['costcenter_data']),

    is_used_transaction_disabled() {
      if (this.mode === 'create') {
        return false;
      } else if (this.mode === 'edit') {
        return this.is_used_transaction_edit;
      }
      return false;
    },

  },

  watch : {
    'costcenter_data.list_mc'(newVal) {
      if (this.mode === 'create') {
        this.modeCreateListMC(newVal);
        this.$set(this.layer_options, 0, newVal);
        if (this.layer_categories[0] !== null) {
          const id = this.layer_options[0].filter(a =>
            a.id === this.layer_categories[0]);
          if (id.length === 0) {
            this.updateLayerCategory(newVal);
          }
        }
      } else if (this.mode === 'edit') {
        this.modeEditListMC(newVal);
        this.$set(this.layer_options, 0, newVal);
        if (this.layer_categories[0] !== null) {
          const id = this.layer_options[0].filter(a =>
            a.id === this.layer_categories[0]);
          if (id.length === 0) {
            this.updateLayerCategory(newVal);
          }
        }
      }
    },

    'costcenter_data.list_activity'(newVal) {
      this.loading_skeleton = false;
      if (this.mode === 'create') {
        this.modeCreateListActivity(newVal);
      } else if (this.mode === 'edit') {
        this.modeEditListActivity(newVal);
      }
    },

    layer_options(newVal) {
      this.is_has_child.index = newVal.length - 1;
      this.is_has_child.is_has = false;

      const sortHaveChild = newVal[newVal.length - 1].filter(child => child.is_have_child === true);
      if (sortHaveChild.length < 1 && newVal[0].length > 0) {
        this.is_has_child.index = newVal.length - 1;
        this.is_has_child.is_has = true;
      }
    },
  },

  methods: {
    ...mapActions('jStoreCostCenter', ['getActivityList']),

    getNextActivityList(index) {
      const nextChild = {
        expense_category: {
          id: this.layer_categories[index],
        },
      };
      // eslint-disable-next-line
      const sortSelectLayer = this.layer_options[index].filter(data => data.id === this.layer_categories[index]);
      if (sortSelectLayer[0]) {
        this.is_has_child.index = index;
        this.is_has_child.is_has = false;
        if (sortSelectLayer[0].is_have_child) {
          this.loading_skeleton = true;
          this.getActivityList(nextChild);
        }
      }
    },

    selectLayer(index) {
      this.isLabelLong(index);

      if (this.layer_categories[index] === undefined) {
        this.layer_categories.splice(index + 1);
        this.layer_options.splice(index + 1);
      } else {
        this.layer_categories.splice(index + 1);
        this.layer_options.splice(index + 1);

        this.getNextActivityList(index);

        if (this.mode === 'create') {
          this.generateDataCreate();
        } else if (this.mode === 'edit') {
          this.generateDataEdit();
        }
      }
    },

    isLabelLong(index) {
      const tooltips = {
        is_show: false,
        label: null,
      };

      if (this.layer_categories[index] === undefined) {
        this.category_hover_tooltip[index] = tooltips;
      } else if (Array.isArray(this.layer_categories[index])) {
        this.category_hover_tooltip[index] = tooltips;
      } else {
        // eslint-disable-next-line
        const filterbyId = this.layer_options[index].filter(fil => fil.id === this.layer_categories[index]);
        const isTooLong = filterbyId[0].name.length > 30;

        if (isTooLong) {
          const newTooltips = {
            is_show: true,
            label: filterbyId[0].name,
          };
          this.category_hover_tooltip[index] = newTooltips;
        } else {
          this.category_hover_tooltip[index] = tooltips;
        }
      }
    },

    updateLayerCategory(data) {
      this.layer_options = [];
      this.layer_options.push(data);
      this.layer_categories[0] = null;
    },

  },

};



