<template>
  <div>
    <br><br>
    <jListCostCenterProject
      v-if="user_role === 'admin_budget' || user_role === 'SysAdmin'"/>
    <jListCostCenterCustodyProject v-else-if="user_role === 'budget_custody'"/>
  </div>
</template>

<script>
import jPrivCostCenter from '@/privilage/views/cost-center/jPrivCostCenter';
import jListCostCenterProject from './jListCostCenterProject';
import jListCostCenterCustodyProject from './jListCostCenterCustodyProject';

export default {
  name: 'JListCostCenterContainer',
  components: {
    jListCostCenterProject,
    jListCostCenterCustodyProject,
  },
  mixins: [
    jPrivCostCenter,
  ],
};
</script>



