<template>
  <div>
    <jMappingLayerForm
      ref="form"
      mode="create"
      @data="getData"/>

    <div>
      <b-button
        class="btn blue float-right"
        @click="saveMapping">
        Submit
      </b-button>
    </div>

  </div>
</template>

<script>
import jMappingLayerForm from '@/components/form/mapping-layer/jMappingLayerForm';
import { jMixModelCreateMappingLayer } from '@/mixins/models/mapping-layer/jMixModelCreateMappingLayer';

export default {
  components: {
    jMappingLayerForm,
  },
  mixins: [
    jMixModelCreateMappingLayer,
  ],
  data() {
    return {
      mapping_layer_data: null,
      length_data: 0,
    };
  },
  methods: {

    saveMapping() {
      this.$refs.form.save();
      if (this.mapping_layer_data !== null) {
        this.sendData();
      }
    },

    getData(data) {
      this.mapping_layer_data = data;
    },
  },
};
</script>



