<template>
  <div class="form-cost-center">
    <br>
    <j-toggle
      v-model="type_form"
      :disabled="mode === 'edit'"
      class="toggle-cost-center"
      off="Type 1"
      on="Type 2"/>
    <b-form-group :class="{'error-input' : $v.cost_center.name.$error}">
      <label> Name* </label>
      <j-input
        v-model="$v.cost_center.name.$model"
        :max-length="50"
        placeholder="Name"/>
    </b-form-group>

    <b-form-group :class="{'error-input' : $v.cost_center.description.$error}">
      <label> Description </label>
      <j-textarea
        v-model="$v.cost_center.description.$model"
        :max-length="300"
        placeholder="Description"/>
    </b-form-group>

    <b-row>
      <b-col>
        <b-form-group :class="{'error-input' : $v.cost_center.code.$error || error_code}">
          <label> Cost Center Code* </label>
          <j-input
            v-model="$v.cost_center.code.$model"
            :max-length="42"
            :disabled="isUsedTransaction()"
            placeholder="Code"/>
        </b-form-group>
      </b-col>

      <b-col>
        <b-form-group :class="{'error-input' : $v.cost_center.divisions.$error}">
          <label> Select Divisions* </label>
          <j-multi-select
            v-model="$v.cost_center.divisions.$model"
            :options="list_division_by_costcenter"
            :disabled="isUsedTransaction()"
            value-format="object"
            label="name"
            placeholder="Select Divisions"
          />
        </b-form-group>
      </b-col>
    </b-row>

    <hr>

    <label class="bold c--blue">Category Layer*</label>

    <hr>

    <jLayerCategoryForm
      ref="layerCategory"
      :mode="mode"
      @lastChild="lastChild"/>

    <b-form-group
      :class="{'error-input' : $v.cost_center.budget_custody.$error}">
      <label>Budget Custody* </label>
      <j-single-select
        v-model="$v.cost_center.budget_custody.$model"
        :options="list_budget_custody"
        :disabled="isUsedTransaction()"
        placeholder="Select Budget Custody"
        label="name"/>
    </b-form-group>

    <b-form-group
      :class="{'error-input' : $v.cost_center.alternate_budget_custody.$error}">
      <label>Alternate Budget Custody</label>
      <j-multi-select
        v-model="$v.cost_center.alternate_budget_custody.$model"
        :options="list_budget_custody"
        :default-data="default_alternate_custody"
        placeholder="Select Budget Custody"
        value-format="object"
        label="name"
        @getData="getDataAlternateCustody"/>
    </b-form-group>

    <div v-if="type_form">
      <div
        v-for="(budget, index) in $v.cost_center.budget.$each.$iter"
        :key="index"
        class="create-budget">
        <b-row>
          <b-col
            cols="3"
            class="budget-name">
            <b-row>
              <b-col
                cols="6"
                class="p__r--12">
                Budget {{ Number(index) + 1 }}
              </b-col>
              <b-col
                cols="6"
                class="p--1">
                <b-form-group
                  :class="{'error-input' : budget.is_project_based.$error}">
                  <j-checkbox
                    v-model="budget.is_project_based.$model"
                    :checked="budget.is_project_based.$model"
                    :disabled="isDisabledBudget(Number(index)) || isDisabledPeriod(Number(index))"
                    :checked-value="true"
                    :unchecked-value="false"
                    class="check-box">
                    Project Based
                  </j-checkbox>
                </b-form-group>
              </b-col>
            </b-row>
          </b-col>
          <b-col>
            <b-form-group :class="{'error-input' : budget.amount.$error}">
              <label> Budget* </label>
              <j-currency
                v-if="refresh_list_budget"
                v-model.number="budget.amount.$model"
                :disabled="isDisabledPeriodAmount(Number(index))"
                :input_mode="true"
                type="number"
                placeholder="Budget Amount"/>
            </b-form-group>
          </b-col>

          <b-col>
            <b-form-group :class="{'error-input' : budget.period.$error }">
              <div v-if="budget.is_project_based.$model === true">
                <label> Project* </label>
                <j-single-select
                  v-if="refresh_list_budget"
                  v-model="budget.project.$model"
                  :options="costcenter_data.list_project"
                  :disabled="isDisabledPeriod(Number(index))"
                  value-format="object"
                  placeholder="Select Project Name"
                  label="name" />
              </div>
              <div v-else>
                <label> Period* </label>
                <div>
                  <j-single-select
                    v-if="refresh_list_budget"
                    v-model="budget.period.$model"
                    :options="isPeriodEdit(Number(index))"
                    :disabled="isDisabledPeriod(Number(index))"
                    placeholder="Select Period"
                    label="name" />
                </div>
              </div>
            </b-form-group>
          </b-col>

          <b-col cols="1">
            <div
              v-if="(cost_center.budget.length > 1 && !isDisabledBudget(Number(index)))
              && cost_center.is_has_child === false"
              class="delete-budget"
              @click="deleteBudget(index)">
              <i class="fa fa-times-circle" />
            </div>
          </b-col>
        </b-row>
      </div>
    </div>
    <div v-else>
      <b-form-group>
        <j-checkbox
          v-model="project_based"
          :disabled="mode === 'edit'"
          :checked="project_based"
          :checked-value="true"
          :unchecked-value="false"
          class="check-box">
          Project Based
        </j-checkbox>
      </b-form-group>
      <div
        v-for="(budget, index) in $v.cost_center.budget.$each.$iter"
        :key="index"
        class="create-budget">
        <b-row>
          <b-col
            cols="3"
            class="budget-name">
            <b-row>
              <b-col
                cols="6"
                class="p__r--12">
                Budget {{ Number(index) + 1 }}
              </b-col>
            </b-row>
          </b-col>
          <b-col>
            <b-form-group :class="{'error-input' : budget.amount.$error}">
              <label> Budget* </label>
              <j-currency
                v-if="refresh_list_budget"
                v-model.number="budget.amount.$model"
                :input_mode="true"
                :disabled="isDisabledPeriodAmount(Number(index))"
                type="number"
                placeholder="Budget Amount"/>
            </b-form-group>
          </b-col>

          <b-col>
            <b-form-group :class="{'error-input' : budget.period.$error }">
              <div v-if="project_based === true">
                <label> Project* </label>
                <j-single-select
                  v-if="refresh_list_budget"
                  v-model="budget.project.$model"
                  :options="costcenter_data.list_project"
                  :disabled="isDisabledPeriod(Number(index))"
                  value-format="object"
                  placeholder="Select Project Name"
                  label="name" />
              </div>
              <div v-else>
                <label> Period* </label>
                <div>
                  <j-single-select
                    v-if="refresh_list_budget"
                    v-model="budget.period.$model"
                    :options="isPeriodEdit(Number(index))"
                    :disabled="isDisabledPeriod(Number(index))"
                    placeholder="Select Period"
                    label="name" />
                </div>
              </div>
            </b-form-group>
          </b-col>

          <b-col cols="1">
            <div
              v-if="(cost_center.budget.length > 1 && !isDisabledBudget(Number(index)))
              && cost_center.is_has_child === false"
              class="delete-budget"
              @click="deleteBudget(index)">
              <i class="fa fa-times-circle"/>
            </div>
          </b-col>
        </b-row>
      </div>
    </div>

    <!-- <label
      v-if="cost_center.budget.length < list_period.length"
      class="italic pointer--cursor add-more-budget"
      @click="addMore">
      + add more budget
    </label> -->

    <label
      class="italic pointer--cursor add-more-budget"
      @click="addMore">
      + add more budget
    </label>

    <br>
    <br>

    <label>Cost Center Policy</label>
    <i
      v-b-toggle="'policy-detail'"
      class="fa fa-angle-down"/>
    <j-collapse
      id="policy-detail"
      :visible="false">

      <b-row>
        <b-col>
          <b-form-group
            :class="{'error-input' : $v.cost_center.policy.is_unbudgeted_option.$error}">
            <j-checkbox
              v-model="$v.cost_center.policy.is_unbudgeted_option.$model"
              :checked="cost_center.policy.is_unbudgeted_option"
              :disabled="false"
              :checked-value="true"
              :unchecked-value="false">
              Unbudgeted Option
            </j-checkbox>
          </b-form-group>
        </b-col>
        <b-col>
          <div class="float-to-right p__t--10">
            <label>Choose Rule</label>
          </div>
        </b-col>
        <b-col>
          <b-form-group :class="{'error-input' : $v.cost_center.policy.rules.$error}">
            <j-single-select
              :disabled="!cost_center.policy.is_unbudgeted_option"
              v-model="$v.cost_center.policy.rules.$model"
              :options="list_rule"
              placeholder="Select Rule"
              label="name"/>
          </b-form-group>
        </b-col>
        <b-col/>
      </b-row>
      <b-row>
        <b-col>
          <b-form-group
            v-if="mode === 'create'"
            :class="{'error-input' : $v.cost_center.policy.tags.$error}">
            <label> TAGGING </label>
            <j-multi-select
              v-model="$v.cost_center.policy.tags.$model"
              :is-async="true"
              :load-options="loadOptions"
              :default-data="[]"
              value-format="object"
              placeholder="Select Tagging"
              label="name"
              @getData="getDataTags"/>
          </b-form-group>
          <b-form-group
            v-if="mode === 'edit'"
            :class="{'error-input' : $v.cost_center.policy.tags.$error}">
            <label> TAGGING </label>
            <j-multi-select
              v-if="detail_cost && tag_fields"
              v-model="$v.cost_center.policy.tags.$model"
              :is-async="true"
              :load-options="loadOptions"
              :default-options="detail_cost !== null ? detail_cost.policy.tags : false"
              :default-data="detail_cost !== null ? detail_cost.policy.tags : []"
              value-format="object"
              placeholder="Select Tagging"
              label="name"
              @getData="getDataTags"/>
          </b-form-group>
        </b-col>
        <b-col>
          <b-row>
            <b-col md="11">
              <b-form-group>
                <label> CREATE NEW TAGGING </label>
                <j-input
                  v-model="tag_name"
                  :max-length="50"
                  placeholder="Tag Name"/>
              </b-form-group>
            </b-col>
            <b-col
              md="1"
              class="p__l--1 btn-add">
              <b-button
                class="btn-round-25 green outline"
                @click="createTag">
                +
              </b-button>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
      <b-row
        v-if="mode === 'edit'"
        class="create-costcenter">
        <b-col>
          <label class="italic c--light-grey">choose budget preferal</label>
          <div>
            <b-row>
              <b-col
                :class="{'half-opacity': !cost_center.policy.is_lock}"
                cols="3"
                class="pointer--cursor"
                @click="cost_center.policy.is_lock = true">
                <i class="fa fa-unlock"/>Lock
              </b-col>
              <b-col
                :class="{'half-opacity': cost_center.policy.is_lock}"
                class="pointer--cursor"
                @click="cost_center.policy.is_lock = false">
                <i class="fa fa-unlock-alt"/>Unlock
              </b-col>
            </b-row>
          </div>
        </b-col>
      </b-row>
    </j-collapse>
    <!-- <b-button @click="validateForm">Clek</b-button> -->
  </div>
</template>

<script>
import jMixCostCenter from '@/mixins/components/get-list-option-form/jMixCostCenter';
import jMixValCostCenter from '@/mixins/components/validations-form/jMixValCostCenter';
import jLayerCategoryForm from '@/components/form/costcenter/jLayerCategoryForm';
import jApi from '@/services/jApi';
// eslint-disable-next-line
import { ASYNC_SEARCH } from '@riophae/vue-treeselect';

export default {
  components: {
    jLayerCategoryForm,
  },
  mixins: [
    jMixCostCenter,
    jMixValCostCenter,
  ],
  data() {
    return {
      refresh_list_budget: true,
      project_based: false,
      type_form: false,
      cost_center: {
        name: '',
        description: '',
        root_id: '',
        code: '',
        is_delete: false,
        is_has_child: false,
        is_used_transaction: false,
        teams: null,
        divisions: null,
        expense_category: {
          package: 1,
          id: 0,
          childs: [],
        },
        budget_custody: null,
        alternate_budget_custody: [],
        budget: [
          {
            amount: null,
            used: 0,
            remaining: '',
            locked: 0,
            accrue: 0,
            period: null,
            project: null,
            is_delete: false,
            is_project_based: false,
          },
        ],
        policy: {
          is_project_based: false,
          is_unbudgeted_option: false,
          rules: null,
          tags: null,
          is_lock: false,
          tags_edit: null,
        },
      },
      error_code: false,
      current_activity: 0,
      activity_options: [],
      tag_name: '',
      is_sl: false,
      mode: 'create',
      is_valid_layer: false,
      tag_fields: false,
      default_alternate_custody: [],
      alternate_custody_val: [],
    };
  },
  computed: {
    // ...mapState('jStoreLogin', ['profile_data']),

    cost_center_filter() {
      return this.generateData();
    },

    // is_budget_custody() {
    //   const userCompanyID = profile_data ? profile_data.user_company_id ? 0;
    //   const userBudgetCustody =
    // detail_cost.budget_custody.filter((filBC) => filBC.type === 1);
    //   const userAlterBudgetCustody =
    // detail_cost.budget_custody.filter((filBC) => filBC.type === 2);
    //   if ([userBudgetCustody[0].])
    //   return includes()
    // },
  },
  watch: {
    // eslint-disable-next-line
    'cost_center.policy.is_unbudgeted_option': function (newData) {
      if (newData === false) {
        this.cost_center.policy.rules = null;
      }
    },
    // eslint-disable-next-line
    'cost_center.divisions': function (newData) {
      const filterDat = newData.map((div) => {
        const obj = {
          id: div.id,
        };
        return obj;
      });

      const filterBatch = [];
      newData.forEach((div) => {
        filterBatch.push(...div.organigram_ids);
      });


      const listBatchOrganigrams = { organigram_ids: filterBatch };

      const fildatLayer = this.list_layer.filter(data => data.order === 1);

      const sendData = {
        division: filterDat,
        layer_id: fildatLayer[0].id,
      };
      this.getMCList(sendData);
      this.getListProject(listBatchOrganigrams);
    },

    sort_by_period_active() {
      this.refresh_list_budget = false;
      setTimeout(() => {
        this.refresh_list_budget = true;
      }, 0);
    },

    detail_cost(newval) {
      this.tag_fields = true;
      if (this.mode === 'edit') {
        const dataEdit = this.genEditData(newval);
        this.cost_center = dataEdit;

        let checkRoot;

        if (this.$route.params.root) {
          checkRoot = Number(this.$route.params.root);
        } else if (this.$route.params.id) {
          checkRoot = Number(newval.root_id);
        } else {
          checkRoot = 0;
        }

        const rootId = {
          pagination: {
            limit: 0,
            page: 0,
            column: '',
            ascending: false,
            query: '',
          },
          costcenter: {
            root_id: checkRoot,
          },
          type: 1,
        };
        this.getPeriodList(rootId);
        // const divisions = newval.divisions.map(div => div.id);
        this.cost_center.divisions.push(...newval.divisions);
      }
    },

    // list_mc() {
    //   this.cost_center.expense_category.mastercard = null;
    // },
    create_tag_res(val) {
      if (val.error === false) {
        this.tag_name = '';
      }
    },
    // eslint-disable-next-line func-names
    'cost_center.code': function (newVal, oldVal) {
      if (!newVal.match(/^[A-Za-z0-9-_]+$/g) && newVal.length !== 0) {
        setTimeout(() => {
          this.cost_center.code = oldVal;
        }, 0);
      }
    },
  },
  methods: {
    loadOptions({ action, searchQuery, callback }) {
      if (action === ASYNC_SEARCH) {
        const reqData = {
          pagination: {
            limit: 0,
            page: 0,
            column: 'name',
            ascending: true,
            query: searchQuery,
          },
        };
        this.getListTags(reqData, callback);
      }
    },

    getListTags(req, callback) {
      const newTag = {
        id: `0${req.pagination.query}`,
        label: `${req.pagination.query} (new tag)`,
        name: req.pagination.query,
      };
      const api = jApi.generateApi();
      return api.post('costcenter/config/tag/list', req)
        .then((res) => {
          if (typeof res.data.data !== 'undefined') {
            res.data.data.forEach((lab, index) => {
              // eslint-disable-next-line
              res.data.data[index].label = lab.name;
            });
            // eslint-disable-next-line
            const isQueryExist = res.data.data.filter(isExist => isExist.name === req.pagination.query);
            if (isQueryExist.length < 1) {
              res.data.data.push(newTag);
            }
            callback(null, res.data.data);
          } else {
            callback(null, [newTag]);
          }
          return res.data;
        });
    },

    isPeriodEdit(index) {
      // return this.list_period;
      if (this.mode === 'edit') {
        if (this.detail_cost !== null) {
          if (index <= this.detail_cost.budget.length - 1) {
            if (this.detail_cost.budget[index].is_expired) {
              return this.list_period;
            } else if (!this.detail_cost.budget[index].is_active) {
              return this.sort_by_period_active;
            }
          } else {
            return this.sort_by_period_active;
          }
        }
      }
      return this.sort_by_period_active;
    },

    isDisabledPeriod(index) {
      if (this.mode === 'edit') {
        if (this.detail_cost !== null) {
          if (index <= this.detail_cost.budget.length - 1) {
            // eslint-disable-next-line
            if ((this.detail_cost.is_used_transaction && this.detail_cost.budget[index].is_active === true) || this.detail_cost.budget[index].is_expired === true) {
              return true;
            }
          } else {
            return false;
          }
        }
      }
      return false;
    },

    isDisabledPeriodAmount(index) {
      if (this.mode === 'edit') {
        if (this.detail_cost !== null) {
          if (index < this.detail_cost.budget.length) {
            return typeof this.detail_cost.budget[index].is_expired !== 'undefined' ?
              this.detail_cost.budget[index].is_expired : false;
          }
        }
      }
      return false;
    },

    isDisabledBudget(index) {
      if (this.mode === 'edit') {
        // eslint-disable-next-line
        if (this.detail_cost !== null) {
          // eslint-disable-next-line
          if (index <= this.detail_cost.budget.length - 1 && this.detail_cost.budget[index].is_active && this.detail_cost.is_used_transaction) {
            return true;
          }
        }
      }
      return false;
    },

    isUsedTransaction() {
      // console.log(typeof this.detail_cost.is_used_transaction, 'hahahah');
      if (this.mode === 'edit') {
        if (this.detail_cost !== null) {
          if (this.detail_cost.is_used_transaction === true) {
            return true;
          }
          return false;
        }
      }
      return false;
    },

    selectPeriod(val, index) {
      this.index_budget = index;
      // if (val[0]) {
      //   const filterData = this.list_period.filter(listper => listper.id !== val[0].id);
      //   this.setNewListPeriod(filterData);
      // }
    },

    lastChild(data) {
      if (data === null || typeof data[0] === 'undefined') {
        this.is_valid_layer = false;
      } else {
        this.is_valid_layer = true;
        this.cost_center.expense_category.package = data[0].package;
        this.cost_center.expense_category.id = data[0].root_id;
        // this.cost_center.expense_category.id = data[0].root_id;
        this.cost_center.expense_category.childs = data;
        if (this.mode === 'edit') {
          this.cost_center.expense_category.childs = [];
          data.forEach((lastChil, index) => {
            this.cost_center.expense_category.childs[index] = {};
            this.cost_center.expense_category.childs[index].id = lastChil.id;
            this.cost_center.expense_category.childs[index].relation_id = lastChil.relation_id;
            this.cost_center.expense_category.childs[index].is_delete = lastChil.is_delete;
            this.cost_center.expense_category.childs[index].name = lastChil.name;
            this.cost_center.expense_category.childs[index].is_have_child = lastChil.is_have_child;
          });
        }
      }
    },

    genEditData(data) {
      const budgetDet = data.budget.slice(0);

      let rules;
      if (data.policy.rules.length > 0) {
        rules = data.policy.rules[0].id;
      } else {
        rules = null;
      }
      // eslint-disable-next-line
      const budgetCustody = data.budget_custody.filter((filbudcus) => filbudcus.type === 1);
      // eslint-disable-next-line
      const AlterbudgetCustody = data.budget_custody.filter((filbudcus) => filbudcus.type === 2);


      const costCenter = {
        name: data.name,
        description: data.description,
        code: data.code,
        type: data.type,
        root_id: data.root_id,
        is_import: false,
        is_delete: data.is_delete,
        is_used_transaction: data.is_used_transaction,
        is_has_child: data.is_has_child ? data.is_has_child : false,
        teams: [],
        divisions: [],
        expense_category: {
          package: 1,
          id: 0,
          childs: [],
        },
        budget_custody: budgetCustody[0].id,
        alternate_budget_custody: [...AlterbudgetCustody],
        budget: budgetDet,
        policy: {
          is_project_based: data.policy.is_project_based,
          is_unbudgeted_option: data.policy.is_unbudgeted_option,
          rules,
          tags: data.policy.tags,
          is_lock: data.policy.is_lock,
        },
      };

      // // team
      // const teams = data.teams.map(team => team.id);
      // costCenter.teams = teams;

      // divisions
      // const divisions = data.divisions.map(div => div.id);
      // costCenter.divisions = divisions;

      // tags
      // const tags = data.policy.tags.map(tag => tag.id);
      // costCenter.policy.tags = tags;

      // Period
      budgetDet.forEach((per, index) => {
        const detailBud = {
          accrue: per.accrue,
          amount: per.amount,
          id: per.id,
          is_active: per.is_active,
          is_delete: per.is_delete,
          locked: per.locked,
          period: per.is_project_based === false ? per.period.id : '',
          project: per.is_project_based ? per.project : null,
          is_project_based: per.is_project_based ? per.is_project_based : false,
          remaining: per.remaining,
          used: per.used,
        };
        costCenter.budget[index] = detailBud;
      });

      this.type_form = costCenter.type !== 1;
      if (!this.type_form) {
        this.project_based = costCenter.budget[0].is_project_based;
      }
      this.default_alternate_custody = [...costCenter.alternate_budget_custody];
      return costCenter;
    },

    getDataAlternateCustody(val) {
      this.alternate_custody_val = val;
    },

    generateData() {
      const costCenter = {
        id: this.cost_center.id ? this.cost_center.id : 0,
        name: this.cost_center.name,
        description: this.cost_center.description,
        code: this.cost_center.code,
        type: !this.type_form ? 1 : 2,
        is_import: false,
        root_id: Number(this.$route.params.root),
        is_delete: this.cost_center.is_delete,
        is_used_transaction: this.cost_center.is_used_transaction,
        teams: null,
        divisions: null,
        expense_category: this.cost_center.expense_category,
        budget_custody: [],
        budget: !this.type_form ? [] : this.cost_center.budget,
        policy: {
          is_project_based: !this.type_form ? this.project_based : false,
          is_unbudgeted_option: this.cost_center.policy.is_unbudgeted_option,
          rules: [],
          tags: this.cost_center.policy.tags_edit,
          is_lock: false,
        },
      };

      // root id costcenter id and is_lock
      if (this.mode === 'edit') {
        costCenter.root_id = this.detail_cost.root_id;
        costCenter.id = this.detail_cost.id;
        costCenter.policy.is_lock = this.cost_center.policy.is_lock;
      }

      if (!this.type_form) {
        this.cost_center.budget.forEach((e) => {
          e.is_project_based = this.project_based;
        });
        costCenter.budget = this.cost_center.budget;
      }

      // get teams
      // const teams = [];
      // if (this.mode === 'create') {
      //   this.cost_center.teams.forEach((team) => {
      //     const filterData = this.list_team.filter(fildat => fildat.id === team);
      //     filterData[0].relation_id = 0;
      //     filterData[0].name = filterData[0].title;
      //     filterData[0].is_delete = false;
      //     teams.push(filterData[0]);
      //   });
      // } else if (this.mode === 'edit') {
      //   const getData = this.cost_center.teams.slice(0);
      //   this.detail_cost.teams.forEach((k) => {
      //     let dataExist = false;
      //     for (let i = 0; i < this.cost_center.teams.length; i += 1) {
      //       if (k.id === this.cost_center.teams[i]) {
      //         dataExist = true;
      //         getData[i] = {
      //           id: k.id, is_delete: k.is_delete, relation_id: k.relation_id, name: k.name,
      //         };
      //       }
      //     }
      //     if (!dataExist) {
      //       const a = {
      //         id: k.id, is_delete: true, relation_id: k.relation_id, name: k.name,
      //       };
      //       getData.push(a);
      //     }
      //   });
      //   teams.push(...getData);
      // }
      // const objTeams = teams.map((createObj) => {
      //   if (typeof createObj !== 'object') {
      //     const detailTeamId = this.list_team.filter(teamdet => teamdet.id === createObj);
      //     return {
      //       id: createObj,
      //       relation_id: 0,
      //       is_delete: false,
      //       name: detailTeamId[0].title,
      //     };
      //   }
      //   return createObj;
      // });
      // costCenter.teams = objTeams;

      const divisions = [];
      if (this.mode === 'create') {
        this.cost_center.divisions.forEach((div) => {
          const filterData = this.list_division.filter(fildat => fildat.id === div.id);
          filterData[0].relation_id = 0;
          filterData[0].name = filterData[0].name;
          filterData[0].is_delete = false;
          divisions.push(filterData[0]);
        });
      } else if (this.mode === 'edit') {
        const getData = this.cost_center.divisions.slice(0);
        this.detail_cost.divisions.forEach((k) => {
          let dataExist = false;
          for (let i = 0; i < this.cost_center.divisions.length; i += 1) {
            if (k.id === this.cost_center.divisions[i].id) {
              dataExist = true;
              getData[i] = {
                id: k.id, is_delete: k.is_delete, relation_id: k.relation_id, name: k.name,
              };
            }
          }
          if (!dataExist) {
            const a = {
              id: k.id, is_delete: true, relation_id: k.relation_id, name: k.name,
            };
            getData.push(a);
          }
        });
        divisions.push(...getData);
      }
      const objDiv = divisions.map((createObj) => {
        if (typeof createObj !== 'object') {
          const detailTeamId = this.list_division.filter(teamdiv => teamdiv.id === createObj.id);
          return {
            id: createObj.id,
            relation_id: 0,
            is_delete: false,
            name: detailTeamId[0].name,
          };
        }
        return createObj;
      });
      costCenter.divisions = objDiv;

      // budget custody
      const budgetCustodySelected = [];
      // eslint-disable-next-line
      const BudgetCustody = this.list_budget_custody.filter(filDat => filDat.id === this.cost_center.budget_custody);
      if (this.mode === 'create') {
        const filterBC = [
          {
            id: BudgetCustody[0].id,
            name: BudgetCustody[0].name,
            is_delete: false,
            relation_id: 0,
            type: 1,
          },
        ];
        budgetCustodySelected.push(...filterBC);
        this.cost_center.alternate_budget_custody.forEach((e) => {
          const alternateCustody = {
            id: e.id,
            name: e.name,
            is_delete: false,
            relation_id: 0,
            type: 2,
          };
          budgetCustodySelected.push(alternateCustody);
        });
      } else if (this.mode === 'edit') {
        if (this.detail_cost.budget_custody[0].id === this.cost_center.budget_custody) {
          const val = [...this.detail_cost.budget_custody];
          val[0].type = 1;
          budgetCustodySelected.push(...val);
        } else {
          const newChoose = {
            id: this.cost_center.budget_custody,
            relation_id: 0,
            is_delete: false,
            type: 1,
          };
          const oldChoose = {
            id: this.detail_cost.budget_custody[0].id,
            relation_id: this.detail_cost.budget_custody[0].relation_id,
            is_delete: true,
            type: 1,
          };
          budgetCustodySelected[0] = {};
          budgetCustodySelected[1] = {};
          budgetCustodySelected[0] = newChoose;
          budgetCustodySelected[1] = oldChoose;
        }

        this.alternate_custody_val.forEach((e) => {
          const val = {
            id: e.id,
            name: e.name,
            is_delete: e.is_delete,
            relation_id: e.relation_id,
            type: e.others.type ? e.others.type : 2,
          };
          budgetCustodySelected.push(val);
        });
      }
      costCenter.budget_custody = budgetCustodySelected;

      // get budget
      const periodData = this.cost_center.budget.map((period) => {
        if (this.mode === 'create') {
          const genPeriod = {
            amount: period.amount,
            is_delete: false,
            relation_id: 0,
            project: period.is_project_based ? period.project : {},
            is_project_based: period.is_project_based ? period.is_project_based : false,
            period: period.is_project_based ? {} : { id: period.period },
          };
          return genPeriod;
        }
        const genPeriod = {
          id: period.id,
          is_delete: false,
          amount: period.amount,
          project: period.is_project_based ? period.project : {},
          is_project_based: period.is_project_based,
          period: period.is_project_based ? {} : { id: period.period },
        };
        return genPeriod;
      });
      costCenter.budget = periodData;

      const storeBudget = [];
      if (this.mode === 'edit') {
        const getData = this.cost_center.budget.slice(0);
        if (this.detail_cost.budget.length > 0) {
          this.detail_cost.budget.forEach((k) => {
            let dataExist = false;
            for (let i = 0; i < this.cost_center.budget.length; i += 1) {
              if (k.id === this.cost_center.budget[i].id) {
                dataExist = true;
                getData[i] = {
                  // eslint-disable-next-line
                  id: k.id,
                  is_delete: k.is_delete,
                  // eslint-disable-next-line
                  project: this.cost_center.budget[i].is_project_based ? this.cost_center.budget[i].project : '',
                  amount: this.cost_center.budget[i].amount,
                  // eslint-disable-next-line
                  period: this.cost_center.budget[i].is_project_based ? {} : { id: this.cost_center.budget[i].period },
                  // eslint-disable-next-line
                  is_project_based: this.cost_center.budget[i].is_project_based ? this.cost_center.budget[i].is_project_based : false,
                };
              }
            }
            if (!dataExist) {
              const a = {
                // eslint-disable-next-line
                id: k.id,
                is_delete: true,
                project: k.is_project_based ? k.project : '',
                is_project_based: k.is_project_based ? k.is_project_based : false,
                amount: k.amount,
                period: k.is_project_based ? {} : { id: k.period.id },
              };
              getData.push(a);
            }
          });
        }
        storeBudget.push(...getData);
      }

      if (this.mode === 'edit') {
        storeBudget.forEach((zeroId, index) => {
          if (zeroId.id === 0) {
            const newdataBud = {
              id: 0,
              is_delete: false,
              project: zeroId.is_project_based ? zeroId.project : '',
              amount: zeroId.amount,
              is_project_based: zeroId.is_project_based ? zeroId.is_project_based : false,
              period: zeroId.is_project_based ? {} : { id: zeroId.period },
            };
            storeBudget[index] = newdataBud;
          }
        });
        costCenter.budget = storeBudget;
      }


      // get rule
      // const ruleSelected = [];
      if (this.mode === 'create') {
        const rules = [
          {
            id: this.cost_center.policy.rules,
            relation_id: 0,
            is_delete: false,
          },
        ];
        if (typeof this.cost_center.policy.rules === 'number') {
          const rule = this.cost_center.policy.is_unbudgeted_option ? rules : [];
          costCenter.policy.rules = rule;
        } else {
          costCenter.policy.rules = [];
        }
      }

      if (this.mode === 'edit') {
        // eslint-disable-next-line
        if (this.cost_center.policy.is_unbudgeted_option && !this.detail_cost.policy.is_unbudgeted_option) {
          const newChoose = {
            id: this.cost_center.policy.rules,
            relation_id: 0,
            is_delete: false,
          };
          costCenter.policy.rules[0] = newChoose;
          // eslint-disable-next-line
        } else if (!this.cost_center.policy.is_unbudgeted_option && this.detail_cost.policy.is_unbudgeted_option) {
          if (typeof this.detail_cost.policy.rules[0] !== 'undefined') {
            const unbudgetFalse = {
              id: this.detail_cost.policy.rules[0].id,
              relation_id: this.detail_cost.policy.rules[0].relation_id,
              is_delete: true,
            };
            costCenter.policy.rules[0] = unbudgetFalse;
          }
          // eslint-disable-next-line
        } else if (this.cost_center.policy.is_unbudgeted_option && this.detail_cost.policy.is_unbudgeted_option) {
          if (typeof this.detail_cost.policy.rules[0] !== 'undefined') {
            if (this.detail_cost.policy.rules[0].id === this.cost_center.policy.rules) {
              costCenter.policy.rules = this.detail_cost.policy.rules;
            } else if (typeof this.cost_center.policy.rules === 'number') {
              const newChoose = {
                id: this.cost_center.policy.rules,
                relation_id: 0,
                is_delete: false,
              };
              costCenter.policy.rules[0] = newChoose;
              costCenter.policy.rules[1] = {
                id: this.detail_cost.policy.rules[0].id,
                relation_id: this.detail_cost.policy.rules[0].relation_id,
                is_delete: true,
              };
            } else if (typeof this.cost_center.policy.rules !== 'number') {
              const newChoose = [{
                id: typeof this.detail_cost.policy.rules[0] !== 'undefined' ? this.detail_cost.policy.rules[0].id : null,
                relation_id: typeof this.detail_cost.policy.rules[0] !== 'undefined' ? this.detail_cost.policy.rules[0].relation_id : null,
                is_delete: true,
              }];
              costCenter.policy.rules = typeof this.detail_cost.policy.rules[0] !== 'undefined' ? newChoose : [];
            }
          } else if (typeof this.cost_center.policy.rules === 'number') {
            const newChoose = {
              id: this.cost_center.policy.rules ? this.cost_center.policy.rules : null,
              relation_id: 0,
              is_delete: false,
            };
            costCenter.policy.rules[0] = newChoose;
          } else if (this.cost_center.policy.rules === null) {
            const newChoose = [{
              id: typeof this.detail_cost.policy.rules[0] !== 'undefined' ? this.detail_cost.policy.rules[0].id : null,
              relation_id: typeof this.detail_cost.policy.rules[0] !== 'undefined' ? this.detail_cost.policy.rules[0].relation_id : null,
              is_delete: true,
            }];
            costCenter.policy.rules = typeof this.detail_cost.policy.rules[0] !== 'undefined' ? newChoose : [];
          }
        }
      }

      // get tag

      // const storeTags = [];
      // if (this.mode === 'edit') {
      //   const getData = this.cost_center.policy.tags.slice(0);
      //   if (this.detail_cost.policy.tags.length > 0) {
      //     this.detail_cost.policy.tags.forEach((k) => {
      //       let dataExist = false;
      //       for (let i = 0; i < this.cost_center.policy.tags.length; i += 1) {
      //         if (k.id === this.cost_center.policy.tags[i]) {
      //           dataExist = true;
      //           getData[i] = {
      //             id: k.id, is_delete: k.is_delete, relation_id: k.relation_id, name: k.name,
      //           };
      //         }
      //       }
      //       if (!dataExist) {
      //         const a = {
      //           id: k.id, is_delete: true, relation_id: k.relation_id, name: k.name,
      //         };
      //         getData.push(a);
      //       }
      //     });
      //   }
      //   storeTags.push(...getData);
      // } else if (this.mode === 'create') {
      //   if (this.cost_center.policy.tags !== null) {
      //     const tagData = this.cost_center.policy.tags.map((tagDat) => {
      //       const detailTagsId = this.list_tag.filter(tagsdet => tagsdet.id === tagDat);
      //       const tags = {
      //         id: tagDat,
      //         name: detailTagsId[0].name,
      //         is_delete: false,
      //         relation_id: 0,
      //       };
      //       return tags;
      //     });
      //     storeTags.push(...tagData);
      //   } else {
      //     costCenter.policy.tags = [];
      //   }
      // }
      //
      // const objTags = storeTags.map((createObj) => {
      //   if (typeof createObj !== 'object') {
      //     const detailTagsId = this.list_tag.filter(tagsdet => tagsdet.id === createObj);
      //     return {
      //       id: createObj,
      //       relation_id: 0,
      //       is_delete: false,
      //       name: detailTagsId[0].name,
      //     };
      //   }
      //   return createObj;
      // });
      // costCenter.policy.tags = objTags;
      return costCenter;
    },

    getDataTags(data) {
      const editTags = data.map((tags) => {
        const dataTags = tags;
        if (typeof dataTags.id === 'string') {
          dataTags.id = 0;
        }
        return dataTags;
      });
      this.cost_center.policy.tags_edit = editTags;
    },
    addMore() {
      const newBudget = {
        id: 0,
        amount: null,
        used: 0,
        remaining: '',
        locked: 0,
        accrue: 0,
        period: null,
        project: null,
        is_delete: false,
        is_project_based: !this.type_form ? this.project_based : false,
      };
      this.cost_center.budget.push(newBudget);
    },

    deleteBudget(index) {
      this.cost_center.budget.splice(index, 1);
    },

    validateForm() {
      this.$v.cost_center.$touch();
      this.$refs.layerCategory.validateForm();
      if (!this.$v.cost_center.$error && this.is_valid_layer) {
        const isSame = this.validatePeriod();
        if (isSame) {
          this.$store.dispatch('jStoreNotificationScreen/toggleProblem', 'Period is used in the same budget, please select another period');
        } else {
          this.$emit('getValue', this.cost_center_filter);
        }
      } else {
        this.$emit('getValue', null);
      }
    },

    validatePeriod() {
      let isSame = false;
      this.cost_center_filter.budget.forEach((data, index1) => {
        if (data.period.id !== undefined) {
          this.cost_center_filter.budget.forEach((next, index2) => {
            if (next.period.id === data.period.id && index1 !== index2) {
              isSame = true;
            }
          });
        }
      });
      return isSame;
    },

    createTag() {
      if (this.tag_name !== '') {
        this.$swal({
          title: 'Are you sure?',
          text: `to create new tag ${this.tag_name}`,
          type: 'question',
          showCancelButton: true,
          cancelButtonColor: '#d33',
          confirmButtonText: 'Yes',
        }).then((result) => {
          if (result.value) {
            const tagName = {
              tag: {
                id: 0,
                name: this.tag_name,
              },
            };
            this.createTagName(tagName);
          }
        });
      }
    },
  },
};
</script>



