<template>
  <div>
    <b-form-group :class="{'error-input' : $v.period_form.name.$error}">
      <label>Name*</label>
      <j-input
        v-model="$v.period_form.name.$model"
        :max-length="50"
        placeholder="Name"/>
    </b-form-group>

    <b-form-group :class="{'error-input' : $v.period_form.description.$error}">
      <label>Description</label>
      <j-textarea
        v-model="$v.period_form.description.$model"
        :max-length="300"
        placeholder="Description"/>
    </b-form-group>
    <b-form-group>
      <j-checkbox
        v-model="period_form.project_based"
        :checked="period_form.project_based"
        :disabled="is_used_transaction_disabled">
        Project Based
      </j-checkbox>
    </b-form-group>

    <label class="f--bold">Period</label>

    <hr>

    <b-row>
      <b-col
        md="4"
        sm="6"
        cols="12">
        <b-form-group :class="{'error-input' : $v.period_form.start_date.$error}">
          <label>Start date*</label>
          <j-datepicker
            v-model="$v.period_form.start_date.$model"
            :disabled="is_used_transaction_disabled"/>
        </b-form-group>
      </b-col>
      <b-col
        md="4"
        sm="6"
        cols="12">
        <b-form-group :class="{'error-input' : $v.period_form.end_date.$error}">
          <label>End date*</label>
          <j-datepicker
            v-model="$v.period_form.end_date.$model"
            :disabled="is_used_transaction_disabled"
            placeholder="ex: Jan 01, 2020"/>
        </b-form-group>
      </b-col>
      <b-col
        md="4"
        sm="12"
        cols="12">
        <b-form-group :class="{'error-input' : $v.period_form.backdate_settlement.$error}">
          <label>Backdate*</label>
          <j-input
            v-model.number="$v.period_form.backdate_settlement.$model"
            :error-message="error_minLength"
            :max-length="3"
            :disabled="period_form.project_based || is_used_transaction_disabled"
            type="number"
            placeholder="30"/>
        </b-form-group>
      </b-col>
    </b-row>

    <hr>

    <label class="f--bold">Blackout Date</label>

    <hr>

    <b-row>
      <b-col
        cols="12"
        sm="6">
        <b-form-group :class="{'error-input' : $v.period_form.blackout_date.start_date.$error}">
          <label>Start date</label>
          <j-datepicker
            v-model="$v.period_form.blackout_date.start_date.$model"
            :disabled="period_form.project_based"
            placeholder="ex: Dec 28, 2019"/>
        </b-form-group>
      </b-col>
      <b-col
        cols="12"
        sm="6">
        <b-form-group :class="{'error-input' : $v.period_form.blackout_date.end_date.$error}">
          <label>End date</label>
          <j-datepicker
            v-model="$v.period_form.blackout_date.end_date.$model"
            :disabled="period_form.project_based"
            placeholder="ex: Jan 30, 2020"/>
        </b-form-group>
      </b-col>
    </b-row>

    <hr>
  </div>
</template>

<script>
import { jMixValidationFormPeriod } from '@/mixins/validations/config/jMixValidationFormPeriod';
import { jMixOptionsFormPeriod } from '@/mixins/list-options/config/jMixOptionsFormPeriod';
import { jMixModeCreatePeriod } from '@/mixins/mode-form/config/jMixModeCreatePeriod';
import { jMixModeEditPeriod } from '@/mixins/mode-form/config/jMixModeEditPeriod';


export default {
  mixins: [
    jMixValidationFormPeriod,
    jMixOptionsFormPeriod,
    jMixModeCreatePeriod,
    jMixModeEditPeriod,
  ],
  props: {
    mode: {
      type: String,
      default: 'create',
    },
  },
  data() {
    return {
      period_form: {
        id: 0,
        name: '',
        description: '',
        project_based: false,
        start_date: '',
        end_date: '',
        backdate_settlement: '',
        blackout_date: {
          start_date: null,
          end_date: null,
        },
      },
    };
  },
  computed: {
    sort_form_data() {
      const data = this.period_form;
      data.start_date = this.$moment(this.period_form.start_date).format('YYYY-MM-DD');
      data.end_date = this.$moment(this.period_form.end_date).format('YYYY-MM-DD');
      if (!this.period_form.project_based) {
        data.blackout_date.start_date = this.$moment(this.period_form.blackout_date.start_date).format('YYYY-MM-DD');
        data.blackout_date.end_date = this.$moment(this.period_form.blackout_date.end_date).format('YYYY-MM-DD');
      }
      return data;
    },
  },
  methods: {
    save() {
      this.$v.period_form.$touch();
      if (this.period_form.project_based) {
        this.$emit('data', this.sort_form_data);
      } else {
        this.validateUnprojectPeriod();
      }
    },

    validateUnprojectPeriod() {
      if (this.period_form.blackout_date.start_date > this.period_form.blackout_date.end_date) {
        this.$store.dispatch('jStoreNotificationScreen/toggleProblem', 'Start date of blackout date must be less than end date of blackout date');
      } else if (!this.$v.period_form.$error) {
        this.$emit('data', this.sort_form_data);
      } else {
        this.$emit('data', null);
      }
    },
  },
};
</script>



