import { mapState, mapActions, mapMutations, mapGetters } from 'vuex';

const jMixCostCenter = {
  computed: {
    ...mapState('jStoreCostCenter', ['costcenter_data', 'list_budget_custody', 'list_period', 'list_rule', 'list_tag', 'list_division', 'list_mc', 'list_sl', 'list_activity', 'detail_cost', 'create_tag_res', 'list_layer', 'list_division_by_costcenter']),
    ...mapGetters('jStoreCostCenter', ['sort_by_period_active']),
  },
  methods: {
    ...mapActions('jStoreCostCenter', ['getBudgetCustodyList', 'getPeriodList', 'getRuleList', 'getTagList', 'getDivisionList', 'getMCList', 'getSLList', 'getActivityList', 'getDetailCostCenter', 'createTagName', 'getLayerList', 'getDivisionListByCostcenter', 'getListProject']),
    ...mapMutations('jStoreCostCenter', ['setNewListPeriod']),
  },
  created() {
    let checkRoot;

    if (this.$route.params.root) {
      checkRoot = Number(this.$route.params.root);
    } else if (this.$route.params.id) {
      checkRoot = Number(this.$route.params.id);
    } else {
      checkRoot = 0;
    }

    const rootId = {
      pagination: {
        limit: 0,
        page: 0,
        column: '',
        ascending: false,
        query: '',
      },
      costcenter: {
        root_id: checkRoot,
      },
      type: 1,
    };

    const paginationReq = {
      pagination: {
        limit: 0,
        page: 0,
        column: '',
        ascending: false,
        query: '',
      },
    };

    this.getBudgetCustodyList();
    // this.getPeriodList(rootId);
    this.getRuleList();
    this.getTagList(paginationReq);
    this.getDivisionList();
    this.getDivisionListByCostcenter(rootId);
    this.getLayerList();
    // this.getListProject(paginationReq);
    // this.getMCList();
    // if (this.$route.params.root) {
    //   this.cost_center.root_id = Number(this.$route.params.root);
    //   const getRoot = {
    //     costcenter: {
    //       root_id: Number(this.$route.params.root),
    //     },
    //   };
    //   this.getPeriodList(getRoot);
    // }
    if (this.$route.params.id) {
      this.mode = 'edit';
      this.is_valid_layer = true;
      const costCenterId = {
        costcenter: {
          id: Number(this.$route.params.id),
        },
      };
      this.getDetailCostCenter(costCenterId);
    }
  },
};

export default jMixCostCenter;



