import jApi from './jApi';

export default {
  getBudgetTransferList(dataCostCenter) {
    const apis = jApi.generateApi();
    return apis.post('costcenter/budget/allocation/list', dataCostCenter)
      .then(res => res);
  },

  getBudgetTransactionList(dataCostCenter) {
    const apis = jApi.generateApi();
    return apis.post('costcenter/mutation/list', dataCostCenter)
      .then(res => res);
  },

  createBudgetTransfer(dataTransfer) {
    const apis = jApi.generateApi();
    return apis.post('costcenter/budget/allocation/create', dataTransfer)
      .then(res => res.data);
  },

  getTotalApprover() {
    const apis = jApi.generateApi();
    return apis.post('costcenter/approver/total')
      .then(res => res.data);
  },

  getDetailTransfer(req) {
    const apis = jApi.generateApi();
    return apis.post('costcenter/approver/detail', req)
      .then(res => res.data);
  },

  getBudgetAllocationLog(req) {
    const apis = jApi.generateApi();
    return apis.post('jojoflow/request/log', req)
      .then(res => res.data);
  },

  approveAction(req) {
    const apis = jApi.generateApi();
    return apis.post('jojoflow/approval/action/approve', req)
      .then(res => res.data);
  },

  rejectionAction(req) {
    const apis = jApi.generateApi();
    return apis.post('jojoflow/approval/action/reject', req)
      .then(res => res.data);
  },

  updateActionTransfer(req) {
    const apis = jApi.generateApi();
    return apis.post('costcenter/approver/action', req)
      .then(res => res.data);
  },
};



