import { required, maxLength, requiredIf } from 'vuelidate/lib/validators';

const jMixValCostCenter = {
  validations: {
    cost_center: {
      name: {
        required,
        maxLength: maxLength(50),
      },
      description: {
        maxLength: maxLength(300),
      },
      code: {
        required,
        maxLength: maxLength(42),
      },
      is_delete: {
        required,
      },
      is_used_transaction: {
        required,
      },
      teams: { },
      divisions: {
        required,
      },
      expense_category: {
        mastercard: {
          // required,
        },
        subledger: {
          // required,
        },
      },
      budget_custody: {
        required,
      },
      alternate_budget_custody: {

      },
      budget: {
        required,
        $each: {
          amount: {
            required,
          },
          period: {
            // eslint-disable-next-line
            required: requiredIf(function () {
              return this.is_project_based === false;
            }),
          },
          project: {
            // eslint-disable-next-line
            required: requiredIf(function () {
              return this.is_project_based === true;
            }),
          },
          is_project_based: {
            required: false,
          },
        },
      },
      policy: {
        is_project_based: {
          required,
        },
        is_unbudgeted_option: {
          required,
        },
        rules: {

        },
        tags: {

        },
      },
    },
  },
};

export default jMixValCostCenter;



