/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapActions, mapState } from 'vuex';
import apiBudgetTransfer from '@/services/jApiBudgetCostCenter';
import { paramsRoute } from '@/mixins/paginations/transfer-budget/jMixReusableFunction';

export const jMixPaginationTransfer = {
  data() {
    return {
      filter_transfer: '',
      env_transfertable: 'costcenter/budget/allocation/list',
      columns: ['Date', 'From', 'Amount', 'Notes', 'status'],
      options: {
        sortable: ['Date', 'From', 'Amount', 'Notes'],
        filterable: true,
        texts: {
          filterPlaceholder: 'Search Budget Transfer',
        },
        debounce: 2000,
        perPage: 5,
        perPageValues: [5, 10, 20, 50, 100],
        requestAdapter(data) {
          return {
            pagination: {
              limit: data.limit,
              page: data.page,
              column: data.orderBy ? data.orderBy : '',
              ascending: !!data.ascending,
              query: data.query ? data.query : '',
            },
            costcenter: {
              id: paramsRoute,
            },
            type: 1,
          };
        },
        requestFunction(data) {
          const getTransferList = apiBudgetTransfer.getBudgetTransferList(data);
          return getTransferList.catch((e) => {
            this.$store.dispatch('jStoreNotificationScreen/toggleProblem', e.response.data.errors[0].message).bind(this);
          });
        },
        responseAdapter({ data }) {
          return {
            data: data.data ? data.data : [],
            count: data.length ? data.length : 0,
          };
        },
      },
    };
  },
  watch: {
    create_transbudget_res() {
      if (this.$route.params.id) {
        this.$refs.transferTable.getData();
      }
    },
    filter_transfer(val) {
      if (val === '') {
        this.$refs.transferTable.setFilter('');
      }
    },
  },
  computed: {
    ...mapState('jStoreCostCenter', ['create_transbudget_res']),
  },
  methods: {
    ...mapActions('jStoreNotificationScreen', ['hideLoading', 'toggleProblem', 'showLoading']),
    customFilter(val) {
      if (val !== '') {
        this.$refs.transferTable.setFilter(val);
      }
    },
  },
};



