<template>
  <div class="jcost-center-list">
    <b-row>
      <b-col
        cols="3"
        class="m__l--30 jInput">
        Filter of List
        <j-select
          v-model="filterby"
          :options="options"/>
      </b-col>
    </b-row>
    <br>
    <j-nested
      v-if="data_costcenter && refresh"
      :data="data_costcenter"
      class="item"
      @click="getChild">
      <template
        slot-scope="props">
        <div
          v-if="!props.row.show_more && props.row.name"
          class="content">
          <b-row class="no-margin">
            <b-col
              md="1"
              class="icon no-padding">
              <i class="fa fa-money"/>
            </b-col>
            <b-col
              md="11">
              <b-row>
                <b-col
                  md="8"
                  class="description p__l--1 p__r--15">
                  <p
                    :class="props.row.budget[0].period.name ? '' : 'm__b--10'"
                    class="name bold">{{ props.row.name }} </p>
                  <p
                    v-if="props.row.budget.length > 0"
                    class="detail">
                    {{ props.row.budget[0].period.name }}
                  </p>
                </b-col>
                <b-col
                  md="4"
                  class="p__l--5">
                  <b-row>
                    <b-col
                      class="add-div no-padding"
                      md="7">
                      <label
                        class="add-child bold"
                        @click="$router.replace(`/dashboard/cost-center/create/${props.row.id}`)">
                        + Add child cost
                      </label>
                    </b-col>
                    <b-col
                      class="button-choice"
                      md="5">
                      <b-row class="list-button">
                        <b-button
                          v-if="props.row.policy.is_lock"
                          class="btn-round blue">
                          <i class="fa fa-lock"/>
                        </b-button>
                        <b-button
                          v-else
                          class="btn-round purple">
                          <i class="fa fa-unlock-alt"/>
                        </b-button>
                        <b-button
                          class="btn-round orange"
                          @click="editCostCenter(props.row)">
                          <i class="fa fa-pencil"/>
                        </b-button>
                        <b-button
                          class="btn-round red"
                          @click="delButton(props.row)">
                          <i class="fa fa-times"/>
                        </b-button>
                      </b-row>
                    </b-col>
                  </b-row>
                </b-col>
              </b-row>
              <b-row>
                <b-col
                  md="8"
                  class="p__l--1 p__r--15">
                  <j-budget-cost
                    v-if="props.row.budget.length > 0"
                    :amount="props.row.budget[0].amount"
                    :used="props.row.budget[0].used"
                    :locked="props.row.budget[0].locked"
                    :accrue="props.row.budget[0].accrue">
                    <div
                      slot="left-bottom"
                      class="budget-comp">
                      <span>
                        <j-currency
                          :value="props.row.budget[0].amount">
                          <template slot="text-left">Budget</template>
                        </j-currency>
                      </span>
                      <span v-if="props.row.budget[0].remaining < 0">
                        <j-currency
                          :value="Math.abs(props.row.budget[0].remaining)
                          + props.row.budget[0].amount">
                          <template slot="text-left">Total Spending</template>
                        </j-currency>
                      </span>
                    </div>
                    <div
                      v-if="props.row.budget[0].remaining > 0"
                      slot="right-center"
                      class="float-right">
                      <span>
                        <j-currency
                          :value="props.row.budget[0].remaining">
                          <template slot="text-left">Remaining</template>
                        </j-currency>
                      </span>
                    </div>
                    <div
                      v-else
                      slot="right-center"
                      class="float-right">
                      <span>
                        <j-currency
                          :value="0">
                          <template slot="text-left">Remaining</template>
                        </j-currency>
                      </span>
                    </div>
                  </j-budget-cost>
                </b-col>
                <b-col
                  v-if="props.row.budget[0].remaining < 0"
                  md="4"
                  class="no-padding">
                  <j-unbudgeted-cost
                    :remaining="props.row.budget[0].remaining"
                    :used="props.row.budget[0].used"
                    :accrue="props.row.budget[0].accrue"
                    :amount="props.row.budget[0].amount"
                    :locked="props.row.budget[0].locked"/>
                </b-col>
              </b-row>
            </b-col>
          </b-row>
        </div>
        <div v-else-if="props.row.show_more">
          <div
            v-if="props.row.show_more.parent.children[props.row.show_more.length-1]
            !== undefined">
            <a
              v-if="props.row.show_more.parent.children[props.row.show_more.length-1].is_next
              === true"
              style="cursor:pointer;"
              @click="showMore(props.row.show_more.parent)">
              Show more . . .
            </a>
            <a
              v-else
              style="cursor:pointer;"
              @click="showMore(props.row.show_more.parent)">
              Refresh . . .
            </a>
          </div>
        </div>
      </template>
    </j-nested>
    <j-loading-nested-table v-else/>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex';
import jBudgetCost from '@/components/budget-component/jBudgetComponent';
import jUnbudgetedCost from '@/components/budget-component/jUnbudgetedComponent';
import jLoadingNestedTable from '@/components/skeleton-loading/jLoadingNestedTable';
import { setTimeout } from 'timers';

export default {
  name: 'JListCostCenter',
  components: {
    jBudgetCost,
    jLoadingNestedTable,
    jUnbudgetedCost,
  },
  data() {
    return {
      refresh: true,
      filterby: 3,
      options: [
        { text: 'Non-Project', value: 1 },
        { text: 'Project', value: 2 },
        { text: 'All', value: 3 },
      ],
      send_data: {
        costcenter_id: 0,
        top_id: 0,
        bottom_id: 0,
        top_db_id: 0,
        bottom_db_id: 0,
        last_update: 0,
        count: 3,
        filter: {
          type: 3,
        },
      },
      data_costcenter: null,
    };
  },
  computed: {
    ...mapState('jStoreCostCenter', ['costcenter_list', 'delete_response', 'detail_cost']),
  },
  watch: {
    costcenter_list(val) {
      this.data_costcenter = val;
    },
    filterby(val) {
      this.refresh = false;
      const sendData = {
        costcenter_id: 0,
        top_id: 0,
        bottom_id: 0,
        top_db_id: 0,
        bottom_db_id: 0,
        last_update: 0,
        count: 3,
        filter: {
          type: val,
        },
      };
      this.getCostCenterList(sendData);
      setTimeout(() => {
        this.refresh = true;
      }, 1000);
    },
  },
  created() {
    this.getCostCenterList(this.send_data);

    this.$root.$on('click', (val) => {
      this.getChild(val);
    });
  },
  methods: {
    ...mapActions('jStoreCostCenter', ['getCostCenterList', 'deleteCostCenter']),

    async getChild(val) {
      const a = val;
      const sendData = {
        data_req: {
          costcenter_id: a.id,
          top_id: 0,
          bottom_id: 0,
          top_db_id: 0,
          bottom_db_id: 0,
          last_update: 0,
          count: this.send_data.count,
          filter: {
            type: this.filterby,
          },
        },
        parent: a,
      };

      if (a.children[0].child) {
        try {
          await this.getCostCenterList(sendData);
          this.$root.$emit('toggleOpen', val);
        } catch (error) {
          throw error;
        }
      } else {
        this.$root.$emit('toggleOpen', val);
      }
    },

    async showMore(val) {
      const prnt = val;
      const chld = val.children;
      const sendData = {
        data_req: {
          costcenter_id: prnt.id,
          top_id: 0,
          bottom_id: chld[chld.length - 2].id,
          top_db_id: chld[0].id,
          bottom_db_id: chld[chld.length - 2].id,
          last_update: 0,
          count: this.send_data.count,
          filter: {
            type: this.filterby,
          },
        },
        parent: prnt,
        children: chld,
      };
      try {
        await this.getCostCenterList(sendData);
      } catch (error) {
        throw error;
      }
    },

    delButton(val) {
      this.$swal({
        title: 'Are you sure?',
        text: `to delete cost center ${val.name}`,
        type: 'warning',
        showCancelButton: true,
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes',
      }).then((result) => {
        if (result.value) {
          this.delCostCenter(val);
        }
      });
    },

    async delCostCenter(val) {
      const array = val;
      const dataCostcenter = {
        data_req: {
          costcenters: [
            {
              id: array.id,
            },
          ],
        },
        val: array,
      };
      try {
        await this.deleteCostCenter(dataCostcenter);
      } catch (error) {
        throw error;
      }
    },

    editCostCenter(val) {
      this.$router.replace(`/dashboard/cost-center/edit/${val.id}`);
    },

  },
};
</script>



