/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapState } from 'vuex';

export const jMixModeEditCategoryLayer = {
  data() {
    return {
      initial_mode_edit: true,
      length_category: 0,
    };
  },

  methods: {
    initialValue() {
      // handle init edit data effect to edit activity
      if (this.list_id_category.length > this.length_category) {
        this.length_category += 1;
        setTimeout(() => {
          // eslint-disable-next-line
          const sortHaveChild = this.layer_options[this.layer_options.length - 1].filter(child => child.is_have_child === true);
          if (sortHaveChild.length < 1) {
            // eslint-disable-next-line
            this.layer_categories.splice([this.layer_options.length - 1], 0, this.list_id_category[this.layer_options.length - 1]);
          } else {
            // eslint-disable-next-line
            if (Array.isArray(this.list_id_category[this.layer_options.length - 1])) {
              // eslint-disable-next-line
              this.layer_categories.splice([this.layer_options.length - 1], 0, this.list_id_category[this.layer_options.length - 1][0].id);
            } else {
              // eslint-disable-next-line
              this.layer_categories.splice([this.layer_options.length - 1], 0, this.list_id_category[this.layer_options.length - 1]);
            }
          }
        }, 100);
      }
    },

    modeEditListMC(newVal) {
      if (this.layer_options.length < 1) {
        this.layer_categories.push(null);
        this.layer_options.push(newVal);

        this.initialValue();
      } else {
        this.layer_options[0] = newVal;
      }

      this.modeEditAddHoverInfo();
    },

    modeEditListActivity(newVal) {
      this.layer_options.push(newVal);
      const isHaveChildren = newVal.filter(child => child.is_have_child === true);

      this.initialValue();

      if (isHaveChildren.length > 0) {
        this.layer_categories.push(null);
      } else {
        this.layer_categories.push([]);
      }

      this.modeEditAddHoverInfo();
    },

    isHasChild(data) {
      if (data.is_have_child) {
        this.list_id_category.push(data.id);
        this.isHasChild(data.child);
      } else if (!data.is_have_child) {
        // handle get detail cost double [bugs] -fad
        this.initial_mode_edit = false;
        if (Array.isArray(data)) {
          this.list_id_category.push(data);
          this.last_old_layer = data;
        } else {
          this.list_id_category.push([data]);
          this.last_old_layer = [data];
        }
      }
    },

    modeEditAddHoverInfo() {
      const tooltips = {
        is_show: false,
        label: null,
      };
      this.category_hover_tooltip[this.layer_options.length - 1] = tooltips;
    },

  },

  watch: {
    'costcenter_data.detail_cost'(newVal) {
      // const data = { ...newVal };
      if (this.initial_mode_edit) {
        this.isHasChild(newVal.expense_category);
      }
    },
  },

  computed: {
    ...mapState('jStoreCostCenter', ['costcenter_data']),

    is_used_transaction_edit() {
      if (this.costcenter_data.detail_cost !== null) {
        return this.costcenter_data.detail_cost.is_used_transaction;
      }
      return false;
    },
  },

};



