/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapActions, mapState } from 'vuex';

export const jMixModePreviewTransferBudget = {
  data() {
    return {
      list_source_preview: null,
    };
  },
  computed: {
    ...mapState('jStoreCostCenter', ['list_sources']),
    ...mapState('jStoreBudgetAllocation', ['detail_transfer']),
    transferStatus() {
      if (this.transfer_budget.status === 2) {
        return 'process';
      } else if (this.transfer_budget.status === 3) {
        return 'approved';
      }
      return 'rejected';
    },
  },
  watch: {
    detail_transfer(val) {
      this.list_source_preview = [val.source_costcenter];
      if (this.preview) {
        const detailTransfer = this.genDetailTransfer(val);
        this.transfer_budget = detailTransfer;
      }
    },
  },
  methods: {
    ...mapActions('jStoreBudgetAllocation', ['getDetailTransfer']),
    modePreview() {
      const transferId = {
        budget_allocation: {
          id: Number(this.$route.params.idTransfer),
          status: Number(this.$route.params.idAproverStatus),
        },
      };
      this.getDetailTransfer(transferId);
    },

    getActiveSource() {
      let isAvailable;
      if (this.transfer_budget.source_costcenter.length > 0) {
        // eslint-disable-next-line no-prototype-builtins
        if (this.transfer_budget.source_costcenter.hasOwnProperty('budget')) {
        // eslint-disable-next-line
          const filterwithProject = this.transfer_budget.source_costcenter.budget.filter(data => data.is_project_based !== true);
          // eslint-disable-next-line
          isAvailable = filterwithProject.filter(data => data.is_active === true);
          if (isAvailable.length < 1) {
            isAvailable = [{
              accrue: 0,
              amount: 0,
              locked: 0,
              used: 0,
              is_active: false,
            }];
          }
        } else {
          isAvailable = [{
            accrue: 0,
            amount: 0,
            locked: 0,
            used: 0,
            is_active: false,
          }];
        }
        return isAvailable;
      }
      isAvailable = [{
        accrue: 0,
        amount: 0,
        locked: 0,
        used: 0,
        is_active: false,
      }];
      return isAvailable;
    },

    getActiveDestination() {
      let isAvailable;
      if (this.transfer_budget.destination_costcenter.length > 0) {
        // eslint-disable-next-line no-prototype-builtins
        if (this.transfer_budget.destination_costcenter.hasOwnProperty('budget')) {
          // eslint-disable-next-line
          const filterwithProject = this.transfer_budget.source_costcenter.budget.filter(data => data.is_project_based !== true);
          // eslint-disable-next-line
          isAvailable = filterwithProject.filter(data => data.is_active === true);
          if (isAvailable.length < 1) {
            isAvailable = [{
              accrue: 0,
              amount: 0,
              locked: 0,
              used: 0,
              is_active: false,
            }];
          }
          return isAvailable;
        }
        isAvailable = [{
          accrue: 0,
          amount: 0,
          locked: 0,
          used: 0,
          is_active: false,
        }];
        return isAvailable;
      }
      isAvailable = [{
        accrue: 0,
        amount: 0,
        locked: 0,
        used: 0,
        is_active: false,
      }];
      return isAvailable;
    },

    getSourceCostCenterPreview() {
      // eslint-disable-next-line
      const filterDat = this.list_sources.filter(data => data.id === this.transfer_budget.selected_source);
      if (filterDat.length > 0) {
        this.transfer_budget.source_costcenter = filterDat[0];
      }
    },

    genDetailTransfer(data) {
      const budgetTransfer = {
        amount: data.amount,
        selected_source: null,
        destination_costcenter: {
          id: data.destination_costcenter.id,
          name: data.destination_costcenter.name,
          budget: [data.destination_costcenter.budget],
        },
        source_costcenter: {
          name: data.source_costcenter.name,
          budget: [data.source_costcenter.budget],
        },
        note: data.note,
        status: data.approver_status,
      };
      this.$set(budgetTransfer, 'selected_source', data.source_costcenter.id);
      return budgetTransfer;
    },
  },
};



