<template>
  <div>
    <v-server-table
      v-show="load_first"
      ref="mutationTable"
      :columns="mutation_columns"
      :options="mutation_options"
      class="jTable"
      @loading="loading"
      @loaded="loaded">
      <div
        slot="date"
        slot-scope="props">
        {{ props.row.created_date | moment("DD MMM, YYYY") }}
      </div>
      <div
        slot="transaction_type"
        slot-scope="props">
        {{ props.row.transaction.type }}
      </div>
      <div
        slot="user"
        slot-scope="props">
        {{ props.row.user.email }}
      </div>
      <div
        slot="amount"
        slot-scope="props">
        {{ props.row.amount }}
      </div>
    </v-server-table>
  </div>
</template>

<script>
import { jMixPaginationTransaction } from '@/mixins/paginations/transfer-budget/jMixPaginationTransaction';
import { jMixReusableFunction } from '@/mixins/paginations/transfer-budget/jMixReusableFunction';

export default {
  mixins: [
    jMixPaginationTransaction,
    jMixReusableFunction,
  ],
};
</script>




